import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Toast } from 'antd-mobile';
import { Redirect } from 'react-router-dom'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse,
    ForgetBox,
    MsgWarp,
    MobileTip,
} from './style'

import { DvaHeader } from '../../common/Template'

class MobileRevise extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        this.props.handleInital()
    }

    render() { 
        const { codeTime, isGetCode, history, code, ChangeCode, isActive, msg, handleNext, handleGetCode } = this.props

        const userInfo = JSON.parse(localStorage.getItem('user_info'))
        let user = userInfo.Mobile
        let token = localStorage.getItem('user_token')

        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                <DvaHeader Left={true}  Title='手机号修改'></DvaHeader>
                <Container>
                    <MobileTip>原用户名可接收短信的用户可自主验证修改用户名，修改后原有的邀请关系不受影响；原用户名无法接收短信的用户，需联系在线客服验证后修改用户名。</MobileTip>
                    <LoginWarpper>
                        <LoginBox>
                            <InputBox className= 'dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe6d6;</i>
                                    <input 
                                        type="text"
                                        value = {userInfo.Mobile}
                                        ref={(input)=>{this.inputUser=input}}
                                        disabled
                                    />
                                </NavInput>
                            </InputBox>
    
                            <InputBox className= 'dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe635;</i>
                                    <input 
                                        type="text"
                                        value = {code}
                                        ref={(input)=>{this.inputCode=input}}
                                        onChange={ ()=>{ChangeCode( user ,this.inputCode.value )} }
                                        placeholder = '请输入验证码'
                                    />
                                    {
                                        isGetCode ? (<span className='inputTpye' onClick={ ()=>{ handleGetCode(user) } } >
                                            获取验证码
                                        </span>) : (<span className='inputTpye avtiive' >
                                            {codeTime} 秒
                                        </span>)
                                    }
                                </NavInput>
                            </InputBox>
    
                            {/* <MsgWarp>{ msg }</MsgWarp> */}
                            <StartUse className={isActive?'active':''}  onClick={ ()=>{handleNext( user, code, isActive, history)} } >下一步</StartUse>
                            <ForgetBox>

                            </ForgetBox>
                        </LoginBox>
                    </LoginWarpper>
                </Container>
            </Fragment>)
        }
    }
}

const mapStateToProps = (state)=>{
    return {
        code: state.getIn(['mobile','code']),
        isCode: state.getIn(['mobile','isCode']),
        msg: state.getIn(['mobile','msg']),
        isActive: state.getIn(['mobile','isActive']),
        codeTime: state.getIn(['mobile','codeTime']), 
        isGetCode: state.getIn(['mobile','isGetCode']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleInital() {
            if(JSON.parse(localStorage.getItem('user_info')).EnabledMark <= 0 || JSON.parse(localStorage.getItem('user_info')).DeleteMark == 1) {
                setTimeout(()=>{
                    console.log('http://' + window.location.host + '/#/login')
                    window.location.href = 'http://' + window.location.host + '/#/login'
                    localStorage.removeItem('user_token')
                }, 2000)
            }
            dispatch(actionCreatore.initalAction(false))
        },
        handleGetCode(user) {
            dispatch(actionCreatore.getCodeAction(user))
        },
        handleNext(user, code, isActive, history) {
            if(isActive){
                dispatch(actionCreatore.nextAction(user, code, history))
                Toast.loading('正在验证...', 0, () => {
                   
                }, true);
            }
        }, 
        ChangeCode(user, code) {
            dispatch(actionCreatore.changeCodeAction(user, code))
        },
    }
}
 
export default connect(mapStateToProps, mapDispatchToProps)(MobileRevise);