import styled from 'styled-components'
import { ToRem } from '../../Js/remToPx'

export const HeaderWarp = styled.div`
    color: #fff;
    background-color: #EC503F;
    font-size: ${ToRem(14)};
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    height: ${ToRem(44)};
    padding: ${ToRem(7)} ${ToRem(3)};
    display: flex;
    overflow: hidden;
    justify-content: space-between;
    z-index: 998;
    transition-property: all;
    text-align: center;
`

export const HeaderBack = styled.div`
    display: flex;
    align-items: center;
    font-size: ${ToRem(16)};
`

export const BackBox = styled.div`
    position: relative;
    width: auto;
    margin: 0 ${ToRem(2)};
    word-break: keep-all;
    white-space: pre;
    cursor: pointer;
    i {
        display: block;
        font-size: ${ToRem(18)};
        height: ${ToRem(18)};
        line-height: ${ToRem(18)};
        width: ${ToRem(18)};
    }
`

export const HeaderTitle = styled.div`
    position: absolute;
    left: ${ToRem(70)};
    right: ${ToRem(70)};
    min-width: 0;
    user-select: auto;
`

export const TitleBox = styled.div`
    font-weight: 700;
    font-size: ${ToRem(16)};
    line-height: ${ToRem(30)};
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
`

export const HeaderRight = styled.div`
    display: flex;
    align-items: center;
    font-size: ${ToRem(16)};
    margin-right: ${ToRem(20)};
`