import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

export const ContenWarp = styled.div`
    font-size: ${ToRem(14)};
    min-height: calc(100vh);
    margin-top: ${ToRem(44)};
    background: linear-gradient(to right, #FF4359 , #FD7335);
    .lg-title {
        width: 75%;
        margin: 0 auto;
        margin-top: 25px;
        img {
            width: 100%;
            display: block;
        }
    }
    .recommend{

        margin: 1rem;
        
    border: solid 1px #c8494d;
    border-radius: 8px;
    padding: 10px;
    color: #fff;
    font-size: 0.4rem;
    font-weight: bold;
    background-color: #c8494d;
    }
    .box-warp {
        width: 95%;
        margin: 0 auto;
        margin-top: 20px;
        background: #fff;
        padding: 20px;
        border-radius: 8px;
        display: flex;
        .cell {
            display: flex;
            width: 33.33%;
            text-align: center;
            flex-flow: column;
            justify-content: space-between;
            align-items: center;
            height: 60px;
            &.border-left {
                border-left: 1px solid #ddd;
            }
            .cell-top {

                .sum {
                    font-weight: 700;
                    font-size: 23px;
                }
                .btn {
                    background: linear-gradient(to right, #FF4359 , #FD7335);
                    display: block;
                    padding: 5px 13px;
                    border-radius: 15px;
                    color: #fff;
                }
            }
            .cell-bottom {
                &.invite {
                    font-weight: 700;
                }
            }
        }
    }
    .spec {
        width: 95%;
        margin: 0 auto;
        color: #fff;
        margin-top: 20px;
        p {
            margin-bottom: 10px;
        }
    }
    .lg-qr {
        text-align: center;
        padding-top: 20px;
    }
    .bottom-warp {
        position: fixed;
        width: 100%;
        padding: 0 ${ToRem(10)};
        padding-bottom: ${ToRem(5)};
        height: ${ToRem(40)};
        bottom: 0;
        left: 0;
        background: #fff;
        z-index: 100;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .bottom-btn {
            color: ${MV.dva_color};
            font-weight: 700;
            font-size: ${ToRem(18)};
            text-align: center;
            height: ${ToRem(35)};
            line-height: ${ToRem(35)};
            width: 100%;
            margin: 0 auto;
            border-radius: ${ToRem(8)};
            &.userType {
                width: 45%; 
            }
            &.extend {
                background: rgba(0,0,0,.4);
            }
        }
    }
`

export const Tip = styled.div`
    color: #f76a24;
    line-height: ${ToRem(16)};
    font-size: ${ToRem(14)};
    text-align: left;
    margin-bottom: ${ToRem(25)} ;
    padding: ${ToRem(10)} ${ToRem(5)};
    background-color: #FEFCEC;
    margin-bottom: ${ToRem(0)};
    span
    {
        font-size: ${ToRem(14)};
        color: #4d94ff;
    }
`