import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    initalIndex: 0,
    isLoading: false,
    animite: false,
    applyReas: '',
    isPlatPro: false,
    isYouPro: false,
    ischeck: false,
    applyData: '',
    proRes: '',
    onList: [],
    offList: [],
    listData: [],
    tabs: [],
    listTotal: 0,
    listPage: 1,
    listPno: 1
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_TABINDEX:
            return state.merge({
                initalIndex: action.index,
                isLoading: action.isloading
            })
        case actionTypes.SET_ANIMITE:
            return state.merge({
                initalIndex: action.index,
                animite: action.animite
            })
        case actionTypes.SET_LIST_TOTAL:
            return state.set('listTotal', action.value)
        case actionTypes.SET_LIST_PNO:
            return state.set('listPno', action.value)
        case actionTypes.SET_LIST_PAGE:
            return state.set('listPage', action.value)
        case actionTypes.SET_INITAL_ON_LIST:
            return state.set('onList', action.list)
        case actionTypes.SET_INITAL_OFF_LIST:
            return state.set('offList', action.list)
        case actionTypes.SET_LIST_DATA:
            return state.set('listData', action.list)
        case actionTypes.SET_TABS:
            return state.set('tabs', action.list)
        default:
            return state   
    }
}   