import React, { Fragment, useState, useEffect } from 'react';
import { ContenWarp } from './style'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Toast } from 'antd-mobile';
import { Link, useHistory, Redirect } from 'react-router-dom'
// import USER_TYPE from '../../common/Js/glabal'
import { getUserInfo } from '../../common/Js/glabalAxios'
import { Axios } from '../../common/Axios';
import { Empty } from 'antd';

const UserMoney = (props) => {
    let history = useHistory();

    useEffect(()=> {
        // if(USER_TYPE.userType == USER_TYPE.type_unextend) {
        //     Toast.info('只有线上推广用户才能查看此页面', 2)
        //     setTimeout(()=>{
        //         history.push('/main');
        //     }, 2000)
        //     return
        // }
        Toast.loading('正在加载数据中...', 0, null, true)
        getUserInfo((data)=>{
            console.log(data.resultdata)
            setMoney(data.resultdata.CreditMoney)
        })

        Axios('get','/GetMoneyRecord',{
            type: '',
            way: 'self',
            pageIndex: 1,
            pageSize: 8,
        }).then(data =>{
            Toast.hide()
            console.log(data.resultdata)
            setList(data.resultdata)
        }).catch(err =>{
    
        })
    }, [])

    const [list, setList] = useState([])
    const [money, setMoney] = useState('')

    if(!localStorage.getItem('user_token')) {
        return <Redirect to='/login' />
    }

    return (<Fragment>
        <DvaHeader Left={true} Title={'账户明细'} zIndex={99}></DvaHeader>
        <ContenWarp>
            <div className='balance'>
                您当前的余额：<span className='num'>{money}</span> (溪币)
            </div>
            <div className='list'>
                {
                    list.map((item, index) => (
                        <div className='li' key={index}>
                            {
                                item.ShopName ? (<div className='title' style={{ fontWeight: 700, textAlign: 'center', lineHeight: 1.5, justifyContent: 'center' }}>
                                    店铺：{item.ShopName}
                                </div>) : null
                            }
                            {
                                item.ShopName ? (<div className='title' style={{ textAlign: 'left', marginTop: 15, color: '#000' }}>
                                    做单旺旺号：{item.Aliww}
                                </div>) : null
                            }
                            <div className='title' style={{marginTop: 15}}>
                                <div className='left'>{item.Way}</div>
                                <div className={ item.InOrOut == -1 ? 'right fail' : 'right success'}><span style={{ fontWeight: 500, fontSize: 14, color: 'rgba(0,0,0,.65)' }}>{item.Description}</span> {item.InOrOut == -1 ? '-' : '+'}{item.Money}溪币</div>
                            </div>
                            <div className='spec'>
                                <div className='left'>{item.CreateDate}</div>
                                <div className='right'>变动前余额：{item.Before}</div>
                            </div>
                            <div className='spec'>
                                {
                                    item.OrderID ? (<div className='left'>单号ID：{item.OrderID}</div>) : null
                                }
                            </div>
                        </div>
                    ))
                }
                {
                  list.length == 0 ? (<Empty description={'暂无明细数据'} image={Empty.PRESENTED_IMAGE_SIMPLE} />) : null
                }
            </div>  
        </ContenWarp>
    </Fragment>)
}

export default UserMoney