export const entireTime = (time)=>{
	time = new Date(parseFloat(time));
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();	
	var h = time.getHours();
	var f = time.getMinutes();
	var s = time.getSeconds();
	
	return y+'-'+add0(m)+'-'+add0(d) + '  ' + add0(h) + ':' + add0(f) + ':' + add0(s);
}

function add0(m){return m<10?'0'+m:m }

export const stringToDate3 = (str) => {
    if(!str) return 0
    
    var tempStrs = str.split(" ");

    var dateStrs = tempStrs[0].split("-");

    var year = parseInt(dateStrs[0], 10);

    var month = parseInt(dateStrs[1], 10) - 1;

    var day = parseInt(dateStrs[2], 10);

    var timeStrs = tempStrs[1].split(":");

    var date = new Date(year, month, day, '00', '00', '00');
    let count = parseInt((new Date().getTime() - date.getTime())/1000)
    if(count >= 60*60*24*3) {
        return false
    }else {
        return true
    }
     
}

export const stringToDate = (str) => {
    if(!str) return 0
    str = str.order.CreateDate
    var tempStrs = str.split(" ");

    var dateStrs = tempStrs[0].split("-");

    var year = parseInt(dateStrs[0], 10);

    var month = parseInt(dateStrs[1], 10) - 1;

    var day = parseInt(dateStrs[2], 10);

    var timeStrs = tempStrs[1].split(":");

    var hour = parseInt(timeStrs [0], 10);

    var minute = parseInt(timeStrs[1], 10);

    var second = parseInt(timeStrs[2], 10);

    var date = new Date(year, month, day, hour, minute, second);

    return parseInt((new Date().getTime() - new Date(date).getTime())/1000) ;
}

export const stringToDate2 = (str) => {
    if(!str) return 0
    str = str.order.CreateDate
    var tempStrs = str.split(" ");

    var dateStrs = tempStrs[0].split("-");

    var year = parseInt(dateStrs[0], 10);

    var month = parseInt(dateStrs[1], 10) - 1;

    var day = parseInt(dateStrs[2], 10);

    var timeStrs = tempStrs[1].split(":");

    var hour = parseInt(timeStrs [0], 10);

    var minute = parseInt(timeStrs[1], 10);

    var second = parseInt(timeStrs[2], 10);

    var date = new Date(year, month, day, hour, minute, second);

    return parseInt(( new Date(date).getTime() + 1000*60*60 - new Date().getTime())/1000) ;
}


export const getQueryVariable = (variable) => {
    var query = window.location.href.split('?')[1]
    if(!query) return '';
    var vars = query.split("&");
    for (var i=0;i<vars.length;i++) {
        var pair = vars[i].split("=");
        if(pair[0] == variable){return pair[1];}
    }
    return '';
}