import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Tabs, List, Modal, Toast } from 'antd-mobile';
// import { Link } from 'react-router-dom'
// import { Toast } from 'antd-mobile';
import { DvaOrderCell ,DvaHeader} from '../../common/Template'
// import QRCode  from 'qrcode.react';
import { Redirect, Link } from 'react-router-dom'
import {
    Container,
    VideoWarp,
    ImgWarp,
    JFCell,
    CellTitle,
    CellContaner,
    JFWarp,
    JFStep,
    JFBox,
    JFExplain,
    RuleItem,
    RuleTitle,
    RuleText,
    TableTitle,
    TatleBox,
    JFItem,
    JFList, 
    JFLi, 
    ItemLeft, 
    ItemRight,
    PopupWarp,
    PopupTitle,
    PopupCell,
    Popuplabel,
    PopupContent,
    PopupList,
    PopupInput,
    PopupSubmit,
    PopupMoreWarp
} from './style'

class TaskInfo extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        this.props.intail(JSON.parse(localStorage.getItem('user_token')))
    }


    render() { 
        const { GiftMore, newGiftList, applyGift, popupCloose, popupShow, newPopupList, giftSelect, level, name, mobile, address, changeMobile, changeName, changeAddress, giftSubmit, newGiftHistory, moreCloose, giftMoreShow, newMoreData } = this.props
        let token = JSON.parse(localStorage.getItem('user_token'))
        const giftList = newGiftList.toJS()
        const popupList = newPopupList.toJS()
        const giftHistory = newGiftHistory.toJS()
        const moreData = newMoreData.toJS()
        // console.log(moreData.record)
        // console.log(JSON.stringify(moreData))

        const ListData = [{JFType: '等级奖励', Remark: '已发货', CreateTime: '2020-8-10 15:31:21'},{JFType: '等级奖励', Remark: '待发货', CreateTime: '2020-8-10 15:31:21'},{JFType: '积分兑换', Remark: '已发货', CreateTime: '2020-8-10 15:31:21'},{JFType: '等级奖励', Remark: '已发货', CreateTime: '2020-8-10 15:31:21'},{JFType: '积分兑换', Remark: '待发货', CreateTime: '2020-8-10 15:31:21'}]
        
        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                <Modal
                    visible={GiftMore}
                    transparent
                    onClose={ moreCloose }
                    footer={[{ text: '确定', onPress: () => { moreCloose() } }]}
                >
                    {
                        JSON.stringify(moreData) === '{}' ? null : (
                            <Fragment>
                                <PopupMoreWarp>
                                    <div className='cell'>
                                        <span className='label davBold'>礼包类型：</span>
                                        <span className='content'>等级{moreData.level}礼包</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>发货状态：</span>
                                        <span className='content'>{moreData.record.IsDeliver == 0 ? '未发货' : '已发货'}</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>兑换的礼品：</span>
                                        <span className='content'>{moreData.record.ReceiveGiftName}</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>收货人：</span>
                                        <span className='content'>{moreData.record.ReceviceName}</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>联系号码：</span>
                                        <span className='content'>{moreData.record.UserMobile}</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>收货地址：</span>
                                        <span className='content'>{moreData.record.UserAddress}</span>
                                    </div>
                                    <div className='cell'>
                                        <span className='label davBold'>兑换时间：</span>
                                        <span className='content'>{moreData.record.CreateTime}</span>
                                    </div>
                                </PopupMoreWarp>
                            </Fragment>
                        )
                    }
                </Modal>

                <Modal
                    popup
                    visible={popupShow}
                    onClose={ popupCloose }
                    animationType="slide-up"
                    afterClose={ popupCloose }
                >
                    <PopupWarp>
                        <PopupTitle className='davBold'>lv{level}等级礼包领取</PopupTitle>
                        <PopupCell>
                            <Popuplabel>请选择礼品:</Popuplabel>
                            <PopupContent>
                                <PopupList>
                                   {
                                       popupList.length ? (<Fragment>
                                            {
                                                popupList.map((item, index) => {
                                                    return (<div key={index} onClick={()=>{giftSelect(index, popupList)}} className={ item.isCheck ? 'gift-item active' : 'gift-item'}>{item.value}</div>)
                                                })
                                            }
                                       </Fragment>) : (<div className='no-data'>
                                           没有可选择的礼品哦！！！
                                       </div>)
                                   }
                                </PopupList>
                            </PopupContent>
                        </PopupCell>
                        <PopupCell>
                            <Popuplabel>收货人姓名:</Popuplabel>
                            <PopupContent>
                                <PopupInput placeholder='请输入收货人姓名' value={name} onChange={()=>{ changeName(this.nameInput.value) }} ref={name => this.nameInput = name}></PopupInput>
                            </PopupContent>
                        </PopupCell>
                        <PopupCell className='to-top'>
                            <Popuplabel>联系号码:</Popuplabel>
                            <PopupContent>
                                <PopupInput placeholder='请输入联系号码' value={mobile} onChange={()=>{ changeMobile(this.mobileInput.value) }} ref={mobile => this.mobileInput = mobile} ></PopupInput>
                            </PopupContent>
                        </PopupCell>
                        <PopupCell className='to-top'>
                            <Popuplabel>收货地址:</Popuplabel>
                            <PopupContent>
                                <PopupInput placeholder='请输入收货地址' value={address} onChange={()=>{ changeAddress(this.addressInput.value) }} ref={address => this.addressInput = address}></PopupInput>
                            </PopupContent>
                        </PopupCell>
                        <PopupSubmit>
                            <div className='popup-btn' onClick={()=>{ giftSubmit(level, popupList, name, mobile, address) }}>确认并领取</div>
                        </PopupSubmit>
                    </PopupWarp>
                </Modal>

                <Container>
                    <DvaHeader Left = {true}  Title = {'我的礼品列表'} zIndex = { 99 }></DvaHeader>  

                    <JFCell className = "rule_info" style={{marginTop:'80px'}}>
                        <CellTitle className = "rule">可领取的等级礼包</CellTitle>
                        <JFList>
                            {
                                giftList.map((item, index) => {
                                    return (<JFLi className='jf-li' key={index}>
                                    <ItemLeft>
                                        <div>LV{item.level}等级礼包</div>
                                    </ItemLeft>
                                    <ItemRight>
                                        {
                                            item.Receive == 1 ? (<div className='right-btn default'>已领取</div>) : (<div className='right-btn' onClick={()=>{ applyGift(item.GiftList, item.level) }}>申请领取</div>)
                                        }
                                    </ItemRight>
                                </JFLi>)
                                })
                            }
                        </JFList>
                    </JFCell>

                    <JFCell className = "rule_info">
                        <CellTitle className = "rule">礼品明细</CellTitle>
                        <CellContaner>
                            <TableTitle>
                                <TatleBox className = "type">兑换类型</TatleBox> 
                                <TatleBox className = "mask">发货状态</TatleBox>
                                <TatleBox className = "gift">兑换礼品</TatleBox>
                                <TatleBox className = "time">兑换时间</TatleBox>
                                <TatleBox className = "more">查看详细</TatleBox>
                            </TableTitle>
                            {
                                giftHistory.length ? (<Fragment>
                                    {
                                        giftHistory.map((item, index) => {
                                            return (
                                                <JFItem key={index}>
                                                    <TatleBox className={index % 2 ? ("type item") : ("type item active")}>等级{item.level}礼包</TatleBox>
                                                    <TatleBox className={index % 2 ? ("mask") : ("mask active")}>{item.record.IsDeliver == 0 ? '未发货' : '已发货'}</TatleBox>
                                                    <TatleBox className={index % 2 ? ("gift") : ("gift active")}>{item.record.ReceiveGiftName}</TatleBox>
                                                    <TatleBox className={index % 2 ? ("time") : ("time active")}>{item.record.CreateTime}</TatleBox>
                                                    <TatleBox className={index % 2 ? ("more") : ("more active")} onClick={()=>{ giftMoreShow(item) }}>查看</TatleBox>
                                                </JFItem>
                                            )
                                        })
                                    }
                                </Fragment>
                                ) : (<div className="nodata">
                                    您还没有任何礼品数据哦...
                                </div>)
                            }
                        </CellContaner>
                    </JFCell>
                </Container>
            </Fragment>)   
        }
    }
}

const mapStateToProps = (state)=>{
    return {
        newGiftList: state.getIn(['giftList', 'giftList']),
        popupShow: state.getIn(['giftList', 'popupShow']),
        newPopupList: state.getIn(['giftList', 'popupList']),
        level: state.getIn(['giftList', 'level']),
        name: state.getIn(['giftList', 'name']), 
        mobile: state.getIn(['giftList', 'mobile']), 
        address: state.getIn(['giftList', 'address']),
        newGiftHistory: state.getIn(['giftList', 'giftHistory']),
        GiftMore: state.getIn(['giftList', 'GiftMore']),
        newMoreData: state.getIn(['giftList', 'moreData']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        intail(token) {
            if(JSON.parse(localStorage.getItem('user_info')).EnabledMark <= 0 || JSON.parse(localStorage.getItem('user_info')).DeleteMark == 1) {
                setTimeout(()=>{
                    console.log('http://' + window.location.host + '/#/login')
                    window.location.href = 'http://' + window.location.host + '/#/login'
                    localStorage.removeItem('user_token')
                }, 2000)
            }
            dispatch(actionCreatore.intailAction(token))
        },
        applyGift(list, level) {
            dispatch(actionCreatore.applyGiftAction(list, level))
        },
        popupCloose() {
            dispatch(actionCreatore.setPopupShow(false))
        },
        giftSelect(index, list) {
            for (let i = 0; i < list.length; i++) {
                const el = list[i];
                el.isCheck = false
            }
            list[index].isCheck = true
            dispatch(actionCreatore.setPopupList(list))
        },
        changeMobile(value) {
            dispatch(actionCreatore.setMobile(value))
        }, 
        changeName(value) {
            dispatch(actionCreatore.setName(value))
        }, 
        changeAddress(value) {
            dispatch(actionCreatore.setAddress(value))
        },
        giftSubmit(level, popupList, name, mobile, address) {
            // console.log(level, popupList, name, mobile, address)
            dispatch(actionCreatore.giftSubmitAction(level, popupList, name, mobile, address))
        },
        moreCloose() {
            dispatch(actionCreatore.setGiftMore(false))
        },
        giftMoreShow(item) {
            dispatch(actionCreatore.setGiftMoreShow(item))
        }
    } 
}
 
export default connect(mapStateToProps, mapDispatchToProps)(TaskInfo);