import styled from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh - ${ToRem(44)} );
    background: #fff;
    margin-top: ${ToRem(44)};
    .notice-warp {

    }
    .popup-warp {
        min-height: 80vh;
    }
`

export const LoginWarpper = styled.div`
    position: relative;
    flex: 1;
    padding: ${ToRem(45)} ${ToRem(25)} 0;
`

export const LoginBox = styled.div`
    background-color: #fff;
    border-radius: ${ToRem(5)};
    overflow: hidden;
    padding: ${ToRem(5)} 0;
    .msg-box {
        padding-left: ${ToRem(5)};
        line-height: ${ToRem(51)};
        font-size: ${ToRem(14)};
        text-align: start;
        color: ${MV.fz_2};
        margin-top: ${ToRem(-15)};
        display: flex;
        justify-content: space-between;
        align-items: center;
        .wangwang {
            color: ${MV.dva_color};
        }
        .msg-btn {
            font-size: ${ToRem(16)};
            color: #409EFF;
            font-weight: 700;
            margin-right: ${ToRem(10)};
            &.active {
                color: ${MV.bg_blue2};
            }
        }
    }
    .upload_text{
        font-size: ${ToRem(14)}; 
        color: rgba(0,0,0,.65);
        text-align: center;
        margin-bottom: 5px;
        font-weight: 700;
    }
    .upload-help {
        font-size: ${ToRem(14)}; 
        color: #409eff;
        text-align: center;
        margin-top: 5px;
    }
`

export const InputBox = styled.div`
    position: relative;
    margin-bottom: ${ToRem(16)};
    width: 100%;
    &.has-msg {
        padding-bottom: 0;
        margin-bottom: 0;
    }
`


export const NavInput = styled.div`
    width: 100%;
    font-size: 0.45333rem;
    border-radius: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    i {
        font-size: ${ToRem(20)};
        font-weight: 700;
        width: ${ToRem(25)};
        text-align: center;
        color: ${MV.dva_color};
    }
    input {
        line-height: ${ToRem(45)};
        font-size: ${ToRem(17)};
        border: none;
        outline: none;
        width: calc(100% - ${ToRem(30)} );
        margin-left: ${ToRem(5)};
        border-radius: 0;
        color: #2b2b2b;
        background-color: transparent;
        &.code {
            width: calc(100% - ${ToRem(125)} );
        }
        &::placeholder {
            color: #dcddde;
        }
        &.active {
            color: #EC503F;
        }
    }
    .inputTpye {
        position: absolute;
        top: ${ToRem(15)};
        right: 0.26667rem;
        font-size: ${ToRem(16)};
        font-weight: 700;
        z-index: 1;
        color: ${MV.bg_brand};
        .iconfont {
            color: #909399;
            font-size: ${ToRem(25)};
        }
        &.avtiive {
            color: #EC503F;
        }
        &.active2 {
            color: ${MV.fz_3};
        }
        &.help-color {
            top: ${ToRem(14)};
            color: ${MV.dva_color};
            font-size: ${ToRem(14)};
            .iconfont {
                color: ${MV.dva_color};  
                font-weight: 500;
                font-size: ${ToRem(16)};
                margin-right: ${ToRem(5)};
            }
        }
    }
`

export const StartUse = styled.div`
    margin-top: 0.74667rem;
    width: 100%;
    height: 1.12rem;
    line-height: 1.12rem;
    text-align: center;
    border-radius: 0.56rem;
    background-color: #d7d7d7;
    font-size: 0.4rem;
    color: #ffffff;
    &.active {
        background-color: #EC503F;
        color: #ffffff !important;
    }
`

export const ForgetBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: ${ToRem(18)};
    font-size: ${ToRem(14)};
    line-height: ${ToRem(14)};
    .forget {
        text-decoration: none;
        color: #a4a4a4;
    }
    .to-rigister {
        color: #409eff; 
    }
`

export const MsgWarp = styled.div`
    color: #EC503F;
    height: ${ToRem(19)};
    line-height: ${ToRem(19)};
    font-size: ${ToRem(12)};
    font-weight: bold;
    text-align: center;
`

export const TaoCode = styled.div`
    border: 1px solid #D5D5D5;
    border-radius: 5px;
    margin-bottom: 10px;
    line-height: 1.5;
    padding: 0 10px;
    font-size: 14px;
    .box-warp {
        padding: 10px 0px;
    }
    .title {
        font-size: 14px;
        font-weight: 700;
        color: ${MV.dva_color};
        border-bottom: 1px solid #f6f6f6;
        .desp {
            font-weight: 500;
            color: ${MV.fz_4};
        }
    }
    .cell {
        padding: 10px 0px;
        border-bottom: 1px solid #f6f6f6;
        .cell-title {
            .help {
                color: rgb(64, 158, 255);
                margin-left: 20px;
            }
        }
        .pading {
            padding: 10px; 
        }
        .body-warp {
            padding: 0 10px;
        }
        .upload-warp {
            display: flex;
            justify-content: center;
            align-items: flex-end;
            margin-top: ${ToRem(0)};
            &.flex-warp{
                justify-content: space-evenly;
            }
            .upload-box {
                .upload-desc {
                    margin-top: 7px;
                    text-align: center;
                    color: rgba(0,0,0,.45);
                    &.active {
                        color: rgb(64,158,255);
                    }
                }
                .upload-title {
                    margin-bottom: 5px;
                    font-weight: 700;
                    text-align: center;
                }
            }
            .upload-img-box {
                width: ${ToRem(100)};
                background: #ddd;
                border-radius: ${ToRem(5)};
                overflow: hidden;
                .img-box {
                    width: 100%;
                    height: 100%;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .big-box {
                margin-left: ${ToRem(8)};
                font-size: ${ToRem(14)};
                line-height: ${ToRem(16)};
                color: ${MV.bd_color7};
                &.flex-span  {
                    margin-left: 0;
                    text-align: center;
                    margin-top: 7px;
                    line-height: 21px;
                }
            }
        }
    }
`

export const HandleWarp = styled.div`
    padding: ${ToRem(8)} ${ToRem(10)};
    color: ${MV.fz_2};
`

export const InputWarp = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: ${ToRem(40)};
    line-height: ${ToRem(40)};
`

export const InputBoxs = styled.div`
    position: relative;
    border: ${ToRem(1)} solid ${MV.bd_color1};
    border-right: none;
    border-radius: ${ToRem(5)} 0 0 ${ToRem(5)};
    height: 100%;
    overflow: hidden;
    width: calc(100% - ${ToRem(75)});
    .clear {
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        padding: 0px 6px;
        background: #fff;
        z-index: 1;
        display: flex;
        align-items: center;
    }
    &.input-big {
        width: 100% !important;
        border-right: ${ToRem(1)} solid ${MV.bd_color1} !important;
    }
    input {
        font-size: ${ToRem(16)};
        border: none;
        outline: none;
        margin: 0 0;
        padding: 0 0;
        height: ${ToRem(39)};
        line-height: ${ToRem(39)};
        margin-left: ${ToRem(30)};
        width: calc(100% - ${ToRem(30)});
        color: ${MV.fz_2};
        position: absolute;
        top: 0;
        left: 0;
        &::placeholder {
            color: ${MV.fz_4};
        }
    }
`

export const IconWarp = styled.div`
    position: absolute;
    display: inline-flex;
    top: ${ToRem(9)};
    left: ${ToRem(5)};
    line-height: ${ToRem(22)};
    width: ${ToRem(22)};
    height: ${ToRem(22)};
    .iconfont {
        font-size: ${ToRem(20)};
        text-align: center;
        line-height: ${ToRem(22)};
        width: ${ToRem(22)};
        height: ${ToRem(22)};
    }
`

export const InputBtn = styled.div`
    width: ${ToRem(75)};
    background-color: ${MV.dva_color};
    text-align: center;
    color: #fff;
    font-size: ${ToRem(16)};
    line-height: ${ToRem(40)};
    border-radius: 0 ${ToRem(5)} ${ToRem(5)} 0;
`