import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'

import {
    UploadWarp
} from './style'
import { Toast, Modal } from 'antd-mobile';
const alert = Modal.alert;

class DvaUpload extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    componentWillUnmount(){
        
    }
    render() {
        const {  clickUpload, classID ,myStyle} = this.props 
        const childrenBckll = (props) =>{
            return props.children
        }

        return (<Fragment>
            <UploadWarp onClick={ ()=>{ clickUpload(classID) } }  style ={myStyle} >
                {
                    childrenBckll(this.props)
                }                               
            </UploadWarp>
            {/* <Input 
                type='file' 
                id={classID} 
                accept='image/*' 
                style={{display: 'none'}} 
                ref={(input)=>{this.filrInput=input}}
                onChange={ (info)=>{ uploadFile(info, fileId, fieldName, callbackHandle) } }
            /> */}
        </Fragment>)
    }
}

const mapStateToProps = (state)=>{
    return {

    }
}


const mapDispatchToProps = (dispatch)=>{
    return {
        clickUpload(classID){
            document.getElementById(classID).click()
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DvaUpload);