import { actionTypes } from './index'
import { Toast } from 'antd-mobile';
import {Axios} from '../../../common/Axios'
import { getUserInfo } from '../../main/page/home/store/actionCreatore'
import { handleInital } from '../page/order/store/actionCreatore'

const setTabSelect = (value) => ({
    type: actionTypes.SET_TABSELECT,
    value
})

const setTitleValue = (value) => ({
    type: actionTypes.SET_TITLEVALUE,
    value
})

const fnHome = (dispatch, nav) => {
    nav.check = 0
    sessionStorage.setItem('nav_data', JSON.stringify(nav))
    dispatch(setTitleValue('首页'))
}

const fnOrder = (dispatch, nav) => {
    nav.check = 1
    sessionStorage.setItem('nav_data', JSON.stringify(nav))
    dispatch(setTitleValue('任务消息'))
}

const fnVideo = (dispatch, nav) => {
    nav.check = 2
    sessionStorage.setItem('nav_data', JSON.stringify(nav))
    dispatch(setTitleValue('新手教学'))
}

const fnMy = (dispatch, nav) => {
    nav.check = 3
    sessionStorage.setItem('nav_data', JSON.stringify(nav))
    dispatch(setTitleValue('个人中心'))
}

//paraName 等找参数的名称
const GetUrlParam = (paraName, url) => {
    if (!url) url = document.location.toString()
    let arrObj = url.split("?");
    if (arrObj.length > 1) {
        let arrPara = arrObj[1].split("&");
        var arr;
        for (var i = 0; i < arrPara.length; i++) {
            arr = arrPara[i].split("=");
            if (arr != null && arr[0] == paraName) {
                return arr[1];
            }
        }
        return "";
    } else {
        return "";
    }
}

