const orderFilter = (data) =>{
    for (let i = 0; i < data.length; i++) {
        const el = data[i];
        el.id = el.ProblemID;
        el.applyReas = el.ProblemType;
        if(el.Status === '待处理') {el.isPlatPro = 0;} else {el.isPlatPro = 1;} 
        if(el.BrusherStatus === '已处理') {el.isYouPro = 1;} else {el.isYouPro = 0;} 
        el.applyData = el.CreateTime;
        el.proRes = el.Description;
    }
    return  data
}


export const setBaga =(value) =>({
    type: actionTypes.SET_BAGE,
    value
})

export const ChangeTabSelect = (value, nav) => {
    return (dispatch) => {
        dispatch(setTabSelect(value))
        // console.log(nav)
        switch (value){
            case 'home': 
                fnHome(dispatch, nav);
                break; 
            case 'order': 
                fnOrder(dispatch, nav);
                break;
            case 'video':
                fnVideo(dispatch, nav)
                break; 
            case 'my':
                console.log('切换。。。my')
                dispatch(getUserInfo())
                fnMy(dispatch, nav)
                break;
            default:
                break;   
        }
    }
}