import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    giftList: [],
    popupShow: false,
    popupList: [],
    level: 0,
    name: '',
    mobile: '',
    address: '',
    giftHistory: [],
    GiftMore: false,
    moreData: {}
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_GIFT_MORE:
            return state.set('GiftMore', action.value) 
        case actionTypes.SET_MORE_DATA:
            return state.set('moreData', action.obj)  
        case actionTypes.SET_GIFT_LIST:
            return state.set('giftList', action.list)   
        case actionTypes.SET_GIFT_HISTORY:
            return state.set('giftHistory', action.list)   
        case actionTypes.SET_SELECT_LEVEL:
            return state.set('level', action.value) 
        case actionTypes.SET_POPUP_SHOW:
            return state.set('popupShow', action.value)   
        case actionTypes.SET_POPUP_LIST:
            return state.set('popupList', action.list)  
        case actionTypes.SET_NAME:
            return state.set('name', action.value) 
        case actionTypes.SET_MOBILE:
            return state.set('mobile', action.value) 
        case actionTypes.SET_ADDRESS:
            return state.set('address', action.value)     
        default:
            return state   
    }
}   