import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    quesType: '',
    quesSuggest: '',
    quesState: '',
    quesInfo: '',
    taskTime: '',
    taskId: '',
    shopName: '',
    taskNum: '',
    taskKey: '',
    youProSug: '',
    youProTime: '',
    platProSug: '',
    platProTime: '',
    payMoney: '',
    mustMoney: '',
    ImgPath: '',
    BrusherImgPath: '',
    taskInfoObj: {},
    sign: false,
    reason: '情选择',
    isOpa: 0,
    wid: 0,
    hei: 0,
    reasonText: '请选择',
    isChange: false,
    isViews: false,
    select_img: '', 
    viewerShow: false,
    isPing: false,
    isTuwen: false,
    newchosenList: [{
        reason: '请选择',
    },{
        reason: '其它 手动输入原因',
    }
    ]
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_TABINDEX:
            return state.merge({
                initalIndex: action.index,
                isLoading: action.isloading
            })
        case actionTypes.SET_NEW_CHOSENLIST:
            return state.set('newchosenList', action.list) 
        case actionTypes.SET_ISTUWEN:
            return state.set('isTuwen', action.value) 
        case actionTypes.SET_ISPING:
            return state.set('isPing', action.value) 
        case actionTypes.SET_MSG_OBJ:
            return state.set('taskInfoObj', action.obj)  
        case actionTypes.SET_SUG:
            return state.set('quesSuggest', action.value)  
        case actionTypes.SET_SIGN:
            return state.set('sign', action.value)  
        case actionTypes.SET_IS_OPA:
            return state.set('isOpa', action.value)  
        case actionTypes.SET_CHOOSE:
            return state.set('reason', action.value)  
        case actionTypes.SET_WID:
            return state.set('wid', action.value)  
        case actionTypes.SET_HEI:
            return state.set('hei', action.value)  
        case actionTypes.SET_REAS_TEXT:
            return state.set('reasonText', action.value)  
        case actionTypes.SET_IS_CHANGE:
            return state.set('isChange', action.value)
        case actionTypes.SET_IS_VIEWS:
            return state.set('isViews', action.value)
        case actionTypes.SET_SELECT_IMG:
            return state.set('select_img', action.value)
        case actionTypes.SET_VIEWER_SHOW:
            return state.set('viewerShow', action.value)
                   
        default:
            return state   
    }
}   