import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Link } from 'react-router-dom'
import { Toast, Picker, List } from 'antd-mobile';
import { Redirect } from 'react-router-dom'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    NavNote,
    StartUse,
    Tip,
    MsgWarp,
    InputRight,
    EditItem,
    InputContent,
    InputDiv,
    InputTitle
} from './style'

import { DvaHeader } from '../../common/Template'

class BankCardRevise extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        this.props.handleIntal()
    }

    render() { 
        const CustomChildren = props => (<EditItem onClick={props.onClick} className='user-address'>
            <InputContent>
                <InputDiv >{props.extra}</InputDiv>
            </InputContent>
        </EditItem>)

        const { newBankList, changeBank, SelectBank, SelectBankIndex, history, msg, isActive, isUser, isPass, inpuType, loginUser, loginUserText,loginPass, handleStart, handkeInputPassBlur, handleInputPassFocus, handleInputPassChange, handleInputUserChange, handleInputUserFocus, handkeInputUserBlur, handleInputType } = this.props
        const bankList = newBankList.toJS()
        const userInfo = JSON.parse(localStorage.getItem('user_info'))

        let token = localStorage.getItem('user_token')
        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                <DvaHeader Left={true}  Title='银行卡绑定'></DvaHeader>
                <Container>
                    <Tip>姓名必须和身份证申请写的一致，请不要使用信用卡绑定，<span>不支持银行卡列表上没有显示的银行</span>，如果绑定错误，请联系专属客服</Tip>
                    <LoginWarpper>
                        <LoginBox>
                            <InputBox>
                                <NavInput>
                                    <input 
                                        type="text"
                                        ref={(input)=>{this.inputUser=input}}
                                        onChange={ ()=>{handleInputUserChange( this.inputUser, this.inputPass)} }
                                        placeholder='请输入银行卡关联人姓名'
                                        value = {loginUser}
                                    />
                                </NavInput>
                            </InputBox>
    
                            <InputBox className='bank-input'>
                                <InputTitle className = 'dva-ellipsis'>银行名称</InputTitle>      
                                <InputRight>
                                    <div className = { SelectBankIndex ? 'picker-warp active' : 'picker-warp' }>
                                        <Picker
                                            extra={ SelectBankIndex ? SelectBank : '请选择银行' }
                                            className = 'AAAAAAAAAAAA'
                                            data={bankList}
                                            value={SelectBankIndex}
                                            onChange={(value)=>{changeBank(value, bankList)}}
                                            cols = {1}
                                            title = { '选择银行类型' }
                                        >
                                            <CustomChildren>Customized children</CustomChildren>
                                        </Picker> 
                                    </div>   
                                    <i className='iconfont ' >&#xe6ab;</i>
                                </InputRight>                     
                            </InputBox>
                            
    
                            <InputBox>
                                <NavInput>
                                    <input 
                                        type="number"
                                        ref={(input)=>{this.inputPass=input}}
                                        onChange={ ()=>{handleInputPassChange( this.inputUser, this.inputPass)} }
                                        placeholder = '请输入银行卡账号'
                                        value = {loginPass}
                                    />
                                    
                                </NavInput>
                                {/* <NavNote  className={isPass? '' : 'none'}>请输入银行卡账号</NavNote> */}
                            </InputBox>
                            <MsgWarp>{ msg }</MsgWarp>
                            <StartUse className={isActive?'active':''}  onClick={ ()=>{handleStart(isActive, loginUser, loginPass, SelectBank, history)} } >完成</StartUse>
                        </LoginBox>
                    </LoginWarpper>
                </Container>
            </Fragment>)
        }
    }
}

const mapStateToProps = (state)=>{
    return {
        SelectBankIndex: state.getIn(['bankCard','SelectBankIndex']),
        SelectBank: state.getIn(['bankCard','SelectBank']),
        newBankList: state.getIn(['bankCard','bankList']),
        loginUser: state.getIn(['bankCard','loginUser']),
        loginPass: state.getIn(['bankCard','loginPass']),
        isPass: state.getIn(['bankCard','isPass']),
        isUser: state.getIn(['bankCard','isUser']),
        isActive: state.getIn(['bankCard','isActive']),
        pact: state.getIn(['bankCard','pact']),
        inpuType: state.getIn(['bankCard','inpuType']),
        isLogin: state.getIn(['bankCard','isLogin']),
        msg: state.getIn(['bankCard','msg']),
        loginUserText: state.getIn(['bankCard','loginUserText']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleIntal() {
            dispatch(actionCreatore.intalAction())
        },
        changeBank(value, bankList){
            console.log(value)
            dispatch(actionCreatore.BankAction(value, bankList))
        },
        handleInputPassFocus(){
            dispatch(actionCreatore.PassFocusAction())
        },
        handkeInputPassBlur(value){
            dispatch(actionCreatore.PassBlurAction(value))
        },
        handleInputPassChange(user, pass){
            dispatch(actionCreatore.changeLoginPassAction(user.value, pass.value))
        },
        handleInputUserChange(user, pass){
            dispatch(actionCreatore.changeLoginUserAction(user.value, pass.value))
        },
        handleInputUserFocus(){
            dispatch(actionCreatore.UserFocusAction())
        },
        handkeInputUserBlur(value){
            dispatch(actionCreatore.UserBlurAction(value))
        },
        handleStart(isActive, user, pass, bank_type, history){
            if(isActive){
                dispatch(actionCreatore.loginSubmitAction(user, pass, bank_type, history))
                Toast.loading('数据提交中...', 0, () => {}, true);
            }else{
                console.log('拒绝提交')
            }
        },
        handlePact(user, pass, pact){
            dispatch(actionCreatore.PactAction(user.value, pass.value, pact))
        },
        handleInputType(inputType){
            dispatch(actionCreatore.InputTypeAction(inputType))
        },
        getStartState(user, pass, pact, isActive, inpuType, isUser, isPass){
            dispatch(actionCreatore.changeStartState(user, pass, pact, isActive, inpuType, isUser, isPass))
        },
    }
}
    
export default connect(mapStateToProps, mapDispatchToProps)(BankCardRevise);