// 获取url参数
export const getQueryVariable = (variable, query) => {
    // var query = window.location.href.split('?')[1]
    if(!query) return '';
    var vars = query.split("&");
    for (var i=0;i<vars.length;i++) {
        var pair = vars[i].split("=");
        if(pair[0] == variable){return pair[1];}
    }
    return '';
}