import styled from 'styled-components'
import { ToRem } from '../../Js/remToPx'
import { MV } from '../../Css'

export const CellWarp = styled.div`
    background-color: #fff;
    padding: ${ToRem(10)};
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`

export const CellLeft = styled.div`
    display: flex;
    position: relative;
    line-height: ${ToRem(34)};
    i {
        color: #EC503F;
        font-size: ${ToRem(28)};
        position: absolute;
        line-height: ${ToRem(28)};
        width: ${ToRem(28)};
        height:  ${ToRem(28)};
        top: ${ToRem(3)};
        text-align: center;
    }
` 

export const Title = styled.div`
    font-size: ${ToRem(16)};
    margin-left: ${ToRem(10)};
    color: #303133;
    line-height: ${ToRem(34)};
    font-weight: 600;
    margin-left: ${ToRem(35)};
`

export const CellRight = styled.div`
    display: flex;
    line-height: ${ToRem(34)};
    i {
        font-size :${ToRem(18)} ;
        color: ${MV.fz_1};
        line-height: ${ToRem(34)};
        height: ${ToRem(34)};
        text-align: center;
     }

`

export const Remark = styled.div`
    margin-right:${ToRem(10)};
    display: flex;
    line-height: ${ToRem(34)};
`

export const DegWarp = styled.div`
    
    font-size: ${ToRem(12)};
    background-color: #EC503F;
    color: #fff;
    border-radius: 50%;
    overflow: hidden;
    width: ${ToRem(22)};
    height: ${ToRem(22)};
    line-height: ${ToRem(34)};
    text-align: center;
`

export const TextWarp = styled.div`
    max-width: ${ToRem(150)};
    text-align: right;
    margin-right: ${ToRem(10)};
    font-size: ${ToRem(16)};
    color: ${MV.fz_2};
    line-height: ${ToRem(34)};

`



export const MustInput =styled.div`
    width: auto;
    text-align: left;
    margin-right: ${ToRem(10)};
    font-size: ${ToRem(16)};
    color: #EC503F;
    line-height: ${ToRem(34)};

`
