export const videoStatus = (item) => {
    if(!item) return ''
    let Status = item.Status
    let IsReturnCapital = item.IsReturnCapital
    if(Status === 'WaitOper') return '待操作'
    if(Status === 'OK' && IsReturnCapital === 0) return '待返还到余额'
    if(Status === 'OK' && IsReturnCapital === 1) return '已返还到余额'
    if(Status === 'TimeOut') return '超时未完成'
    if(Status === 'Dispose') return '此单被释放，无效'
    if(Status === 'UserCancle') return '用户取消'
}