import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { Toast , Modal, List, Radio, DatePicker} from 'antd-mobile';
import { Redirect } from 'react-router-dom'
import { actionCreatore } from './store'
import {
    Container,
    MyWarp,
    MyList,
    CellBox
} from './style'

import { DvaCell,DvaHeader} from '../../../../../../common/Template'
const RadioItem = Radio.RadioItem;

const prompt = Modal.prompt;
const operation = Modal.operation;

class UserInfor extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        this.props.handleNew(JSON.parse(localStorage.getItem('user_info')))
    }
      
    render() {  
        const { handleToBasic, handleZfb, handleTaoBao, handleSex, birthday, radioIndex, handleBank, handlePhone, history, handleRealName, userInfo, ChangeRadio, changeBirthday} = this.props
        let token = localStorage.getItem('user_token')
        const user = userInfo.toJS()
        const nowTimeStamp = Date.now();
        const now = new Date(nowTimeStamp);
        // console.log(user.Birthday)
        const sexList = [{
            label: '男',
            value: 1
        }, {
            label: '女',
            value: 2
        }]

        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                    <Container>
                        <DvaHeader Left={true}  Title={'信息修改'}></DvaHeader>
                        <MyWarp> 
                            <MyList>
                                <DvaCell cellStyle={{ marginTop: 0 }} userInfo={user} IconStyle = { { color: '#EC503F', fontSize: 26, top: 5 , width: 28 , height: 28 ,lineHight: 28} } leftIcon = "&#xe6b8;" title ="手机号修改" textVal = {user.Mobile}  rightIcon= "&#xe6ab;" history = { history }  handleCell ={handlePhone}></DvaCell>

                                <DvaCell top = "1px" IconStyle = { { color: '#EC503F', fontSize: 26 , top: 4 , width: 26 , height: 26 ,lineHight: 26 } } leftIcon = "&#xe6cf;" title ="修改密码"  textVal = {0} rightIcon= "&#xe6ab;" history = { history } handleCell ={ ()=>{
                                    history.push('/changePass/change')
                                } }></DvaCell>
                                {/* <DvaCell cellStyle={{ marginTop: 1 }} IconStyle = { { color: '#EC503F', fontSize: 26, top: 5 , width: 28 , height: 28 ,lineHight: 28} } leftIcon = "&#xe6d5;" title ="姓名" textVal = {user.RealName}  rightIcon= "&#xe6ab;" history = { history }  handleCell ={() =>{handleRealName(user.RealName)}}></DvaCell> */}

                                <DvaCell cellStyle={{ marginTop: 1 }} IconStyle = { { color: '#EC503F', fontSize: 26, top: 5 , width: 28 , height: 28 ,lineHight: 28} } leftIcon = "&#xe6d5;" title ="基本信息"   rightIcon= "&#xe6ab;" history = { history }  handleCell ={handleToBasic}></DvaCell>

                                {/* <DvaCell 
                                    cellStyle={{ marginTop: 1 }}
                                    IconStyle={{ color: '#EC503F', fontSize: 26, top: 5, width: 28, height: 28, lineHight: 28 }} 
                                    leftIcon="&#xe6ca;" title="性别" 
                                    textVal = { user.Gender === 0 ? '女' : (user.Gender === 1 ? '男' : '未设置') } 
                                    rightIcon="&#xe6ab;" history={history} 
                                    handleCell ={
                                        () => operation([
                                            { text: '男', onPress: () => handleSex('1') },
                                            { text: '女', onPress: () => handleSex('0') },
                                        ])
                                    }
                                ></DvaCell> */}

                                {/* <CellBox>
                                    <span>
                                        <i className='iconfont' >&#xe65d;</i>
                                    </span>
                                    <DatePicker
                                        maxDate={now}
                                        mode="date"
                                        minDate={new Date(1970, 1, 1, 0, 0, 0)}
                                        title="选择日期"
                                        extra={user.Birthday ? user.Birthday.split(' ')[0] : '请选择'}
                                        value={birthday}
                                        onChange={(date) => { changeBirthday(date) }}
                                        style={{ width: '100%' }}
                                    >
                                        <List.Item arrow="horizontal">生日</List.Item>
                                    </DatePicker>
                                </CellBox> */}

                                {/* <DvaCell cellStyle={{ marginTop: 1 }} IconStyle = { { color: '#EC503F', fontSize: 27 , top: 5 , width: 30 , height: 30 ,lineHight: 30 } } leftIcon = "&#xe6b4;" title ="银行卡"  textVal = {0} rightIcon= "&#xe6ab;" history = { history }  textVal = { user.BankCardNum ? user.BankCardNum : '未绑定' }  handleCell ={handleBank}></DvaCell> */}

                                {/* <DvaCell cellStyle={{ marginTop: 1 }} IconStyle={{ color: '#EC503F', fontSize: 25, top: 4.5, width: 25, height: 25, lineHight: 25 }}
                                leftIcon="&#xe6ac;" title="淘宝账号" textVal={0} rightIcon="&#xe6ab;" textVal={'查看账户信息'} history={history} handleCell={handleTaoBao}></DvaCell> */}

                                {/* <DvaCell cellStyle={{ marginTop: 1 }} IconStyle={{ color: '#EC503F', fontSize: 25, top: 4.5, width: 25, height: 25, lineHight: 25 }}
                                leftIcon="&#xe6f3;" title="支付宝账号" textVal={0} rightIcon="&#xe6ab;" textVal={'查看账户信息'} history={history} handleCell={handleZfb}></DvaCell> */}
                                
                            </MyList>
                        </MyWarp>
                    </Container>
                </Fragment>)
            }
        }
}

const mapStateToProps = (state)=>{
    return {
        userInfo: state.getIn(['userInfor','userInfo']),
        radioIndex: state.getIn(['userInfor','radioIndex']),
        birthday: state.getIn(['userInfor','birthday']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleToBasic(history) {
            history.push('/basic')
        },
        handleTaoBao(history){
            history.push('/taobao')
        },
        handleZfb(history){
            history.push('/zfb')
        },
        handleSex(sex) {
            Toast.loading('数据提交中...', 0, () => {}, true);
            dispatch(actionCreatore.changeSex(sex))
        },
        changeBirthday(date) {
            Toast.loading('数据提交中...', 0, () => {}, true);
            dispatch(actionCreatore.changeBirthday(date))
        },
        ChangeRadio(index) {
            dispatch(actionCreatore.setRadioIndex(index))
        },
        handleBank(history){
            history.push('/bankcard')
        },
        handlePhone(history, userInfo) {
            if(userInfo.EnabledMark === 1) {
                Toast.info('您的账号审核已通过，请不要修改手机号,当前手机号:' + userInfo.Mobile, 2, null, false)
                // console.log('您的账号审核已通过，请不要修改手机号')
            }else {
                history.push('/mobilerevise')
            }
            
        },
        handleRealName(RealName) {
            Toast.info('姓名不可直接修改，请联系客服修改', 2, null, false)
            // prompt('修改姓名', '', [
            //     { text: '取消' },
            //     { text: '确定', onPress: (value) => {
            //         dispatch(actionCreatore.setRealName(value, RealName))
            //     }  },
            //   ], 'default', RealName)
        },
        handleNew(user){
            // if(JSON.parse(localStorage.getItem('user_info')).EnabledMark <= 0 || JSON.parse(localStorage.getItem('user_info')).DeleteMark == 1) {
            //     setTimeout(()=>{
            //         console.log('http://' + window.location.host + '/#/login')
            //         window.location.href = 'http://' + window.location.host + '/#/login'
            //         localStorage.removeItem('user_token')
            //     }, 2000)
            // }
            dispatch(actionCreatore.setUser(user))
        }
    }    
}
 
export default connect(mapStateToProps, mapDispatchToProps)(UserInfor);