import React, { Fragment, useState, useEffect, useRef } from 'react';
import { ContenWarp, TaoCode, HandleWarp, InputWarp, InputBoxs, IconWarp, InputBtn } from './style'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Button, Modal, Toast } from 'antd-mobile';
import { Link, useHistory, Redirect } from 'react-router-dom'
import { Axios } from '../../common/Axios'
import axios from 'axios'
import { PathImg, PathImgBig, defaultImg } from '../../common/Js/orderFiltter'
import HtmlImageCompress from 'html-image-compress'
import { Input } from 'antd'
import URL from '../../common/Axios/NEW_URL'

const Tixian = (props) => {
    let history = useHistory();
    useEffect(() => {
        String.prototype.replaceAll = function (s1, s2) {
            return this.replace(new RegExp(s1, "gm"), s2);
        }
        getUserInfo((res) => {
            setUser(res)
            setAliww1(res.AliAccount)
            setAliww2(res.Aliww1)
            setAliww3(res.Aliww2)
        })

    }, [])

    const getUserInfo = (callback) => {
        Axios('get', '/GetBrusherInfo', {}).then(data => {
            localStorage.setItem('user_info', JSON.stringify(data.resultdata))
            callback(data.resultdata)
        }).catch(err => {

        })
    }

    const handlePopup = (int) => {
        setPopupType(int)
        setTimeout(() => {
            setPopupShow(true)
        }, 0)
    }

    const TaoInput = (e, int) => {
        let input = e.target.value
        console.log(input)
        if (int == -1) {
            setTaoInput0(input)
        } else if (int == 1) {
            setTaoInput1(input)
        } else {
            setTaoInput2(input)
        }
    }

    const GetUrlParam = (paraName, url) => {
        if (!url) url = document.location.toString()
        let arrObj = url.split("?");
        if (arrObj.length > 1) {
            let arrPara = arrObj[1].split("&");
            var arr;
            for (var i = 0; i < arrPara.length; i++) {
                arr = arrPara[i].split("=");
                if (arr != null && arr[0] == paraName) {
                    return arr[1];
                }
            }
            return "";
        } else {
            return "";
        }
    }

    const handleTaoTest = (int) => {
        let value = ''
        if (int == -1) {
            value = taoInput0
        } else if (int == 1) {
            value = taoInput1
        } else {
            value = taoInput2
        }
        if (!value) {
            Toast.fail('淘口令不能为空', 2, null, false);
            return
        }
        let str = value
        Toast.loading("解析中...", 0, null, true)

        var indexName = str.indexOf("&name=");
        if (indexName >= 0) {
            var indexPG = str.indexOf("&isPublicGroup=");
            var FinalAliww = str.substring(indexName + 6, indexPG);

            FinalAliww = decodeURI(FinalAliww);


            if (int == -1) {
                setWwCode0(FinalAliww)
            } else if (int == 1) {
                setWwCode1(FinalAliww)
            } else {
                setWwCode2(FinalAliww)
            }
            Toast.hide()
            return
        }


        Axios('get', '/ChangeTaoKoulingToLink', {
            token: 0,
            TaoKouLing: str
        }).then(data => {
            let res = JSON.parse(JSON.stringify(data.resultdata.data))
            console.log(data)
            if (data.type == 1) {
                let url = res.url
                let psname = decodeURI(GetUrlParam("name", url))
                if (psname) {
                    Toast.hide()
                    Toast.success(`解析成功，结果为 ${psname}`, 2, null, false)
                    if (int == -1) {
                        setWwCode0(psname)
                    } else if (int == 1) {
                        setWwCode1(psname)
                    } else {
                        setWwCode2(psname)
                    }
                    return
                }
            } else {
                Toast.fail(res.msg, 2, null, false);
            }
            Toast.hide()
            Toast.fail("解析失败，请重试", 2, null, false)
        }).catch(err => {
            console.log(err)
            Toast.hide()
            Toast.fail("解析失败，请重试", 2, null, false)
        })
    }

    const miInput = (e, int) => {
        let input = e.target.value
        console.log(input)
        if (int == -1) {
            setMiInput0(input)
        } else if (int == 1) {
            setMiInput1(input)
        } else {
            setMiInput2(input)
        }
    }

    const handleMiTest = (int) => {
        let str = ''
        if (int == -1) {
            str = miInput0
        } else if (int == 1) {
            str = miInput1
        } else {
            str = miInput2
        }
        if (!str) {
            Toast.fail('问候语不能为空', 2, null, false);
            return
        }
        Toast.loading("解析中...", 0, null, true)
        var xiaomiIndex = str.indexOf("Hi，");
        let psname = ''
        if (xiaomiIndex >= 0) {
            var xiaomiEndIndex = str.indexOf("，好时光当然用来逛淘宝");
            if (xiaomiEndIndex <= 0) {
                xiaomiEndIndex = str.indexOf("，淘淘新宝贝");
            } if (xiaomiEndIndex <= 0) {
                xiaomiEndIndex = str.indexOf("，购物");
            }
            if (xiaomiEndIndex <= 0) {
                xiaomiEndIndex = str.indexOf("，H");
            }
            if (xiaomiEndIndex <= 0) {
                xiaomiEndIndex = str.indexOf("，我是小蜜");
            }
            psname = str.substring(xiaomiIndex + 3, xiaomiEndIndex);
        }
        if (!psname) {
            Toast.hide()
            Toast.fail('提取到的旺旺号为空', 2, () => { }, true);
            return
        }
        Toast.hide()
        Toast.success(`解析成功，结果为 ${psname}`, 2, null, false)
        if (int == -1) {
            setWwCode0(psname)
        } else if (int == 1) {
            setWwCode1(psname)
        } else if (int == 2) {
            setWwCode2(psname)
        }
    }

    const handleUpload = (index) => {
        setSelectIndex(index)
        document.getElementById('uploadInput').click()
    }

    const getBase64 = (img, callback) => {
        let htmlImageCompress = new HtmlImageCompress(img, { width: 1200, height: 800, quality: 0.9 })
        htmlImageCompress.then(function (result) {
            // 成功后执行 
            let file = result.file; // 压缩后的图片文件(file)对象
            let fileSize = result.fileSize // 压缩后的图片文件(file)大小
            let base64 = result.base64; // 压缩后的base64图片
            let origin = result.origin; // 压缩前的图片文件（file）对象


            callback(base64);
        }).catch((err) => {
            // 处理失败会执行 
            const reader = new FileReader();
            reader.addEventListener('load', () => {
                let newB64 = reader.result;
                callback(newB64);
            });
            reader.readAsDataURL(img);
        })
    }


    const beforeUpload = (file) => {
        // console.log('订单照片开始上传', file)
        if (file == undefined) {
            Toast.fail('请拍照或上传图片', 2, null, false);
            return false;
        }

        const isLt2M = file.size / 1024 / 1024 < 13;
        if (!isLt2M) {
            Toast.fail('图片大小不超过13M', 2, null, false);
        }
        return isLt2M;
    }

    const SaveImgToServer = (base64, orderIds, fieldName, callback) => {
        Toast.loading("正在上传...", 0, null, true);
        //上传到OSS
        Axios('post2', '/SaveImg', {
            token: JSON.parse(localStorage.getItem('user_token')),
            base64,
            orderIds,
            fieldName
        }).then(data => {
            Toast.hide();
            callback(data);
        }).catch(err => {
            Toast.fail('上传失败,请重试!', 2, null, false);
        })
    }

    const getIdInfo = (path, callback) => {
        Toast.loading("正在识别身份信息...", 0, null, true);
        Axios('get2', '/RecognizeOrderPic', {
            token: JSON.parse(localStorage.getItem('user_token')),
            picpath: URL.IMG_UPLOAD.replace("hapi", "") + path
            // picpath: 'http://newasd.cifcrm.com:8661//Resource/BrusherUpload//20201103/H2fae9fb11898d46.png'
        }).then(res => {
            Toast.hide();
            callback(res.message);
        }).catch(err => {
            Toast.hide();
            Toast.fail('识别失败，截图不清晰或图片信息不正确!', 2, null, false);
        })
    }

    const changeFile = (info) => {
        let file = info.target.files[0];
        if (beforeUpload(file)) {
            getBase64(file, imgInfo => {
                SaveImgToServer(imgInfo, '', '', function (res) {
                    let path = res.resultdata
                    if (selectIndex === -1) {
                        setWwImg0(path)
                    } else if (selectIndex === 1) {
                        setWwImg1(path)
                    } else if (selectIndex === 2) {
                        setWwImg2(path)
                    }

                    getIdInfo(path, (data) => {
                        console.log(data)
                        let str1 = data.split('淘宝账号')[1]
                        if (str1) {
                            let psname = ''
                            if (str1.indexOf('修改手机号') > -1) {
                                psname = str1.split('修改手机号')[0]
                            } else {
                                psname = str1.split('手机号')[0]
                            }
                            psname = psname.replaceAll('>', '').substring(0, 25)
                            console.log(psname)
                            if (psname) {
                                Toast.hide()
                                Toast.success(`识别成功，结果为 ${psname}`, 2, null, false)
                                if (selectIndex === -1) {
                                    setWwCode0(psname)
                                } else if (selectIndex === 1) {
                                    setWwCode1(psname)
                                } else if (selectIndex === 2) {
                                    setWwCode2(psname)
                                }
                                return
                            } else {
                                Toast.fail('识别失败，您的截图不包含旺旺号,请重新截图,付款信息包含关键字  淘宝账号 和 修改手机号', 2, null, false);
                                return
                            }
                        } else {
                            Toast.fail('识别失败，您的截图不包含旺旺号,请重新截图,付款信息包含关键字  淘宝账号 和 修改手机号', 2, null, false);
                            return
                        }
                    })
                })
            })
        }
    }

    const handleAtTest = (int) => {
        let psname = ''
        if (int == -1) {
            psname = wwCode0
            setIsWwCode0(false)
        } else if (int == 1) {
            psname = wwCode1
            setIsWwCode1(false)
        } else if (int == 2) {
            psname = wwCode2
            setIsWwCode2(false)
        }
        Toast.loading('正在验号...', 0, null, true);
        if (psname) {
            Axios('YanHao', '/CheckAliAccount', {
                RelationId: 0,
                account: psname
            }).then(res => {
                Toast.hide()
                console.log(res)
                if (res.resultdata == 1) {
                    let fromData = {}
                    if (int == -1) {
                        setLock1(true)
                        fromData.AliAccount = psname
                    } else if (int == 1) {
                        setLock2(true)
                        fromData.Aliww1 = psname
                    } else if (int == 2) {
                        setLock3(true)
                        fromData.Aliww2 = psname
                    }

                    Axios('post', '/UpdateBrusherInfo', fromData).then(res => {
                        Toast.hide();
                        console.log(res)
                        if (res.type == 1) {
                            Toast.success('恭喜，验号并提交成功', 2, null, true);
                            if (int == -1) {
                                setIsWwCode0(true)
                                setAliww1(psname)
                            } else if (int == 1) {
                                setIsWwCode1(true)
                                setAliww2(psname)
                            } else if (int == 2) {
                                setIsWwCode2(true)
                                setAliww3(psname)
                            }
                        } else {
                            Toast.fail(res.message, 2, null, false)
                        }
                    }).catch(err => {
                        Toast.fail('提交失败,请重试!', 2, null, false);
                    })
                    return
                } else {
                    Toast.fail(res.message, 2, () => { }, true);
                }
                Axios('get', '/SaveYHLog', {
                    aliAccount: psname,
                    IsPass: 1
                }).then(data => {
                    console.log(data)
                })
            }).catch(err => {
                Toast.fail('网络错误，请稍后再试', 2, () => { }, true);
                console.log(err)
            })
        } else {
            Toast.hide()
            setTimeout(() => {
                Toast.fail('请先提取旺旺号或提取到正确格式旺旺号后再验号', 2, () => { }, true);
            }, 0)
        }
    }

    const myInput = useRef(null)
    const [wwImg0, setWwImg0] = useState(null)
    const [wwImg1, setWwImg1] = useState(null)
    const [wwCode0, setWwCode0] = useState(null)
    const [wwCode1, setWwCode1] = useState(null)
    const [isWwCode0, setIsWwCode0] = useState(null)
    const [isWwCode1, setIsWwCode1] = useState(null)
    const [taoInput0, setTaoInput0] = useState('')
    const [taoInput1, setTaoInput1] = useState('')
    const [miInput0, setMiInput0] = useState('')
    const [miInput1, setMiInput1] = useState('')
    const [wwImg2, setWwImg2] = useState(null)
    const [wwCode2, setWwCode2] = useState(null)
    const [isWwCode2, setIsWwCode2] = useState(null)
    const [taoInput2, setTaoInput2] = useState('')
    const [miInput2, setMiInput2] = useState('')
    const [popupShow, setPopupShow] = useState(false)
    const [popupType, setPopupType] = useState(null)
    const [selectIndex, setSelectIndex] = useState(null)
    const [user, setUser] = useState({})
    const [lock1, setLock1] = useState(true)
    const [lock2, setLock2] = useState(true)
    const [lock3, setLock3] = useState(true)
    const [Aliww1, setAliww1] = useState('')
    const [Aliww2, setAliww2] = useState('')
    const [Aliww3, setAliww3] = useState('')

    if (!localStorage.getItem('user_token')) {
        return <Redirect to='/login' />
    }

    return (<Fragment>
        <DvaHeader Left={true} Title={'我的旺旺号'} zIndex={99}></DvaHeader>
        <ContenWarp>
            <Modal
                popup
                visible={popupShow}
                onClose={() => { setPopupShow(false) }}
                animationType="slide-up"
                afterClose={() => { setPopupShow(false) }}
            >
                <div className='popup-warp' style={{ height: '80vh', display: 'block' }}>
                    {
                        popupType === 'shibie' ? (<>
                            <div className='popup-title'>获取旺旺号图片流程</div>
                            <div className='popup-body'>
                                <div className='popup-box'>
                                    <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击右上角设置图标进入设置页面</p>
                                    <img src="/imgs/tb1.png" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>二、进入设置页面后，点击 <span>账户与安全</span> 进入账户与安全页面</p>
                                    <img src="/imgs/tb2.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>三、进入账户与安全页面后，截图保存图片并上传即可</p>
                                    <img src="/imgs/tb3.png" alt="" style={{ width: '100%' }} />
                                </div>
                            </div>
                        </>) : (popupType === 'tao' ? (<>
                            <div className='popup-title'>获取淘口令流程</div>
                            <div className='popup-body'>
                                <div className='popup-box'>
                                    <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击右上角通讯录图标进入通讯录页面</p>
                                    <img src="/imgs/tao1.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>二、进入通讯录页面后，点击右上角的 <span>添加好友</span> 图标进入添加好友页面</p>
                                    <img src="/imgs/tao2.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>三、进入添加好友页，点击 <span>我的淘口令</span> 会弹出一个分享的弹出层</p>
                                    <img src="/imgs/tao3.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>四、点击弹出层的 <span>复制链接</span>，即可复制淘口令</p>
                                    <img src="/imgs/tao4.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                            </div>
                        </>) : (popupType === 'mi' ? (<>
                            <div className='popup-title'>获取小蜜问候语流程</div>
                            <div className='popup-box'>
                                <img src="/imgs/mi.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                        </>) : null))
                    }
                </div>
            </Modal>

            <Input
                type='file'
                id='uploadInput'
                accept='image/*'
                style={{ display: 'none' }}
                onChange={(info) => { changeFile(info) }}
            ></Input>

            <TaoCode style={{ marginTop: 30 }}>
                <div className='h1'>旺旺主号</div>
                {
                    (Aliww1) && lock1 ? (<>
                        <div className='ww1'>
                            <i className='iconfont' style={{ fontWeight: 700, color: '#ec503f', marginRight: 5 }}>&#xe6f3;</i>
                            已绑定的旺旺主号：
                            <div className='content'>
                                <span className='count'>{Aliww1}</span>
                                {/* <span className='revise-btn' onClick={() => { setLock1(false) }}>修改</span> */}
                            </div>
                        </div>
                    </>) : (<>
                        <div className='title box-warp'>
                            解析旺旺号 <span className='desp'> 再验证旺旺号</span>
                        </div>
                        <div className='cell'>
                            <div className='cell-title'>①、淘口令解析 <span className='help' onClick={() => { handlePopup('tao') }}>如何获取淘口令？</span></div>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs>
                                        <IconWarp>
                                            <i className='iconfont'>&#xe6f5;</i>
                                        </IconWarp>
                                        <input
                                            placeholder='请复制淘口令'
                                            type="text"
                                            onChange={(e) => { TaoInput(e, -1) }}
                                        />
                                    </InputBoxs>
                                    <InputBtn onClick={() => { handleTaoTest(-1) }}>解析</InputBtn>
                                </InputWarp>
                            </HandleWarp>
                        </div>
                        {/* <div className='cell'>
                            <div className='cell-title'>②、小蜜问候语解析 <span className='help' onClick={() => { handlePopup('mi') }}>如何获取小蜜问候语？</span></div>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs>
                                        <IconWarp>
                                            <i className='iconfont'>&#xe6f5;</i>
                                        </IconWarp>
                                        <input
                                            placeholder='请复制小蜜问候语'
                                            type="text"
                                            onChange={(e) => { miInput(e, -1) }}
                                        />
                                    </InputBoxs>
                                    <InputBtn onClick={() => { handleMiTest(-1) }}>解析</InputBtn>
                                </InputWarp>
                            </HandleWarp>
                        </div> */}
                        {/* <div className='cell'>
                            <div className='cell-title'>②、旺旺号图片识别 <span className='help' onClick={() => { handlePopup('shibie') }}>如何获取旺旺号图片？</span></div>
                            <div className='pading'>
                                <div className='upload-warp'>
                                    <div onClick={() => { handleUpload(-1) }} className='upload-img-box'>
                                        <div className='img-box'>
                                            <img className={wwImg1 ? '' : 'default-img'} src={wwImg0 ? (PathImg(wwImg0)) : null} alt="" onClick={() => { return false }} />
                                        </div>
                                    </div>
                                    {
                                        wwImg0 ? (null) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                    }
                                </div>
                            </div>
                        </div> */}
                        <div className='title box-warp'>验证旺旺号</div>
                        <div className='cell'>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs className='dva-ellipsis' style={{ border: 'none' }}>
                                        解析的旺旺号：<span style={{ color: '#409eff' }}>{wwCode0}</span>
                                    </InputBoxs>
                                    {
                                        isWwCode0 ? (<span style={{ color: '#67C23A' }}>验号已通过</span>) : (<InputBtn style={{ borderRadius: 5, width: 111 }} onClick={() => { handleAtTest(-1) }}>验号并提交</InputBtn>)
                                    }
                                </InputWarp>
                            </HandleWarp>
                        </div>
                    </>)
                }
            </TaoCode>

            <TaoCode style={{ marginTop: 30 }}>
                <div className='h1'>旺旺小号1</div>

                {
                    (Aliww2) && lock2 ? (<>
                        <div className='ww1'>
                            <i className='iconfont' style={{ fontWeight: 700, color: '#ec503f', marginRight: 5 }}>&#xe6f3;</i>
                            已绑定的旺旺小号1：
                            <div className='content'>
                                <span className='count'>{Aliww2}</span>
                                <span className='revise-btn' onClick={() => { setLock2(false) }}>修改</span>
                            </div>
                        </div>
                    </>) : (<>
                        <div className='title box-warp'>
                            解析旺旺号 <span className='desp'> 再验证旺旺号</span>
                        </div>
                        <div className='cell'>
                            <div className='cell-title'>①、淘口令解析 <span className='help' onClick={() => { handlePopup('tao') }}>如何获取淘口令？</span></div>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs>
                                        <IconWarp>
                                            <i className='iconfont'>&#xe6f5;</i>
                                        </IconWarp>
                                        <input
                                            placeholder='请复制淘口令'
                                            type="text"
                                            onChange={(e) => { TaoInput(e, 1) }}
                                        />
                                    </InputBoxs>
                                    <InputBtn onClick={() => { handleTaoTest(1) }}>解析</InputBtn>
                                </InputWarp>
                            </HandleWarp>
                        </div>
                        {/* <div className='cell'>
                                    <div className='cell-title'>②、小蜜问候语解析 <span className='help' onClick={() => { handlePopup('mi') }}>如何获取小蜜问候语？</span></div>
                                    <HandleWarp>
                                        <InputWarp>
                                            <InputBoxs>
                                                <IconWarp>
                                                    <i className='iconfont'>&#xe6f5;</i>
                                                </IconWarp>
                                                <input
                                                    placeholder='请复制小蜜问候语'
                                                    type="text"
                                                    onChange={(e) => { miInput(e, 1) }}
                                                />
                                            </InputBoxs>
                                            <InputBtn onClick={() => { handleMiTest(1) }}>解析</InputBtn>
                                        </InputWarp>
                                    </HandleWarp>
                                </div> */}
                        {/* <div className='cell'>
                                    <div className='cell-title'>②、旺旺号图片识别 <span className='help' onClick={() => { handlePopup('shibie') }}>如何获取旺旺号图片？</span></div>
                                    <div className='pading'>
                                        <div className='upload-warp'>
                                            <div onClick={() => { handleUpload(1) }} className='upload-img-box'>
                                                <div className='img-box'>
                                                    <img className={wwImg1 ? '' : 'default-img'} src={wwImg1 ? (PathImg(wwImg1)) : null} alt="" onClick={() => { return false }} />
                                                </div>
                                            </div>
                                            {
                                                wwImg1 ? (null) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                            }
                                        </div>
                                    </div>
                                </div> */}
                        <div className='title box-warp'>验证旺旺号</div>
                        <div className='cell'>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs className='dva-ellipsis' style={{ border: 'none' }}>
                                        解析的旺旺号：<span style={{ color: '#409eff' }}>{wwCode1}</span>
                                    </InputBoxs>
                                    {
                                        isWwCode1 ? (<span style={{ color: '#67C23A' }}>验号已通过</span>) : (<InputBtn style={{ borderRadius: 5, width: 111 }} onClick={() => { handleAtTest(1) }}>验号并提交</InputBtn>)
                                    }
                                </InputWarp>
                            </HandleWarp>
                        </div>
                    </>)
                }

            </TaoCode>

            <TaoCode style={{ marginTop: 35 }}>
                <div className='h1'>旺旺小号2</div>
                {
                    user.Aliww1 ? (<>
                        {
                            (Aliww3) && lock3 ? (<>
                                <div className='ww1'>
                                    <i className='iconfont' style={{ fontWeight: 700, color: '#ec503f', marginRight: 5 }}>&#xe6f3;</i>
                                    已绑定的旺旺小号2：
                                    <div className='content'>
                                        <span className='count'>{Aliww3}</span>
                                        <span className='revise-btn' onClick={() => { setLock3(false) }}>修改</span>
                                    </div>
                                </div>
                            </>) : (<>
                                <div className='title box-warp'>
                                    解析旺旺号 <span className='desp'>, 再验证旺旺号</span>
                                </div>
                                <div className='cell'>
                                    <div className='cell-title'>①、淘口令解析 <span className='help' onClick={() => { handlePopup('tao') }}>如何获取淘口令？</span></div>
                                    <HandleWarp>
                                        <InputWarp>
                                            <InputBoxs>
                                                <IconWarp>
                                                    <i className='iconfont'>&#xe6f5;</i>
                                                </IconWarp>
                                                <input
                                                    placeholder='请复制淘口令'
                                                    type="text"
                                                    onChange={(e) => { TaoInput(e, 2) }}
                                                />
                                            </InputBoxs>
                                            <InputBtn onClick={() => { handleTaoTest(2) }}>解析</InputBtn>
                                        </InputWarp>
                                    </HandleWarp>
                                </div>
                                {/* <div className='cell'>
                                            <div className='cell-title'>②、小蜜问候语解析 <span className='help' onClick={() => { handlePopup('mi') }}>如何获取小蜜问候语？</span></div>
                                            <HandleWarp>
                                                <InputWarp>
                                                    <InputBoxs>
                                                        <IconWarp>
                                                            <i className='iconfont'>&#xe6f5;</i>
                                                        </IconWarp>
                                                        <input
                                                            placeholder='请复制小蜜问候语'
                                                            type="text"
                                                            onChange={(e) => { miInput(e, 2) }}
                                                        />
                                                    </InputBoxs>
                                                    <InputBtn onClick={() => { handleMiTest(2) }}>解析</InputBtn>
                                                </InputWarp>
                                            </HandleWarp>
                                        </div> */}
                                {/* <div className='cell'>
                                            <div className='cell-title'>②、旺旺号图片识别 <span className='help' onClick={() => { handlePopup('shibie') }}>如何获取旺旺号图片？</span></div>
                                            <div className='pading'>
                                                <div className='upload-warp'>
                                                    <div onClick={() => { handleUpload(2) }} className='upload-img-box'>
                                                        <div className='img-box'>
                                                            <img className={wwImg1 ? '' : 'default-img'} src={wwImg2 ? (PathImg(wwImg2)) : null} alt="" onClick={() => { return false }} />
                                                        </div>
                                                    </div>
                                                    {
                                                        wwImg2 ? (null) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                                    }
                                                </div>
                                            </div>
                                        </div> */}
                                <div className='title box-warp'>验证旺旺号</div>
                                <div className='cell'>
                                    <HandleWarp>
                                        <InputWarp>
                                            <InputBoxs className='dva-ellipsis' style={{ border: 'none' }}>
                                                解析的旺旺号：<span style={{ color: '#409eff' }}>{wwCode2}</span>
                                            </InputBoxs>
                                            {
                                                isWwCode2 ? (<span style={{ color: '#67C23A' }}>验号已通过</span>) : (<InputBtn style={{ borderRadius: 5, width: 111 }} onClick={() => { handleAtTest(2) }}>验号并提交</InputBtn>)
                                            }
                                        </InputWarp>
                                    </HandleWarp>
                                </div>
                            </>)

                        }
                    </>) : (<>
                        <div className='lock'>
                            <div className='lock-top'>
                                <i className='iconfont davBold' style={{}}>&#xe60f;</i>
                            </div>
                            <div className='lock-bottom'>
                                添加旺旺小号1后解锁
                            </div>
                        </div>
                    </>)
                }
            </TaoCode>
        </ContenWarp>
    </Fragment>)
}

export default Tixian