export const Kefu = styled.div`
    position: fixed;
    z-index: 100;
    background: #33a3f4;
    text-align: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color: #fff;
    font-size: 14px;
    bottom: 100px;
    right: 6px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    line-height: 1;
    .kefu-cell {
        width: 100%;
        text-align: center;
    }
    .icon-img {
        .iconfont {
            font-size: 18px;
        }
    }
`

const twinkles = keyframes`
    0%{
        color: ${MV.dva_color};
    }
    33%{
        color: #409eff;
    }
    66% {
        color: #E6A23C;
    }
    100%{
        color: ${MV.dva_color};
    }
`

export const WWmodal = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.65);
    z-index: 1000;
    font-size: 16px;
    line-height: 1.4;
    .box-warp-shad {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .box-warp {
        z-index: 1;
        width: 80%;
        max-height: 80vh;
        margin: 0 auto;
        border-radius: 8px;
        overflow: hidden;
        background-color: #fff;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 50%;
        transform: translate3d(-50%, -50%, 0);
        .title {
            text-align: center;
            font-weight: 700;
            font-size: 18px;
            color: rgba(0,0,0,.65);
            padding: 10px;
        }
        .content {
            color: #fff;
            font-size: 16px;
            width: 90%;
            margin: 0 auto;
            min-height: 50px;
            max-height: 50vh;
            overflow: hidden auto;
            .list-warp {
                width: 100%;
                height: auto;
                .ww-item {
                    padding: 8px;
                    font-size: 14px;
                    color: rgba(0,0,0,.65);  
                    transition: all 0.2s;
                    &:active {
                        background-color: #eee;
                    } 
                    &.active {
                        background-color: ${MV.dva_color};
                        color: #ffff;
                    }
                    span {
                        margin-left: 8px;
                    }
                }
            }
        }
        .handle {
            margin-top: 20px;
            width: 100%;
            padding: 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-top: 1px solid #eee;
        }
        .btn {
            text-align: center;
            width: 45%;
            padding: 10px 0;
            font-size: 16px;
            border-radius: 8px;
            overflow: hidden;
            &.cancel {
                background-color: #fff;
                color: rgba(0,0,0,.65);
                border: 1px solid rgba(0,0,0,.65);
            }
            &.confiram {
                color: #fff;
                background-color: ${MV.dva_color};
            }
        }
    }
`

