import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from '../store'
import { Toast } from 'antd-mobile';
import { Redirect } from 'react-router-dom'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse,
    ForgetBox,
    MsgWarp
} from '../style'

import { DvaHeader } from '../../../common/Template'

class MobileRevise extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        this.props.handleInital()
    }

    render() { 
        const { handleGetNewCode, isGetNewCode, newCodeTime, history, nextMsg, isNextActive, pass, ChangePass, opass, ChangeOpass, handleSubmit } = this.props
         
        let token = localStorage.getItem('user_token')
        let old_mobile = JSON.parse(sessionStorage.getItem('old_mobile')) 
        let old_code = JSON.parse(sessionStorage.getItem('old_code')) 
        
        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                <DvaHeader Left={true}  Title='手机号修改'></DvaHeader>
                <Container>
                    <LoginWarpper>
                        <LoginBox>
                            <InputBox className= 'dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe6d6;</i>
                                    <input 
                                        type="number"
                                        value = {pass}
                                        ref={(input)=>{this.inputPass=input}}
                                        onChange={ ()=>{ChangePass( this.inputPass.value, opass )} }
                                        placeholder = '请输入新手机号'
                                    />
                                </NavInput>
                            </InputBox>
    
                            <InputBox className= 'dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe635;</i>
                                    <input 
                                        type="password"
                                        value = {opass}
                                        ref={(input)=>{this.inputOpass=input}}
                                        onChange={ ()=>{ChangeOpass( pass, this.inputOpass.value )} }
                                        placeholder = '请输入验证码'
                                    />
                                    {
                                        isGetNewCode ? (<span className='inputTpye' onClick={ ()=>{ handleGetNewCode(pass) } } >
                                            获取验证码
                                        </span>) : (<span className='inputTpye avtiive' >
                                            {newCodeTime} 秒
                                        </span>)
                                    }
                                </NavInput>
                            </InputBox>
    
                            <MsgWarp>{ nextMsg }</MsgWarp>
                            <StartUse className={isNextActive?'active':''}  onClick={ ()=>{ handleSubmit( old_mobile, pass, opass, old_code, isNextActive, history )} } >完成</StartUse>
                            <ForgetBox>
                                
                            </ForgetBox>
                        </LoginBox>
                    </LoginWarpper>
                </Container>
            </Fragment>)
        }
    }
}

const mapStateToProps = (state)=>{
    return {
        isNextActive: state.getIn(['mobile','isNextActive']),
        pass: state.getIn(['mobile','pass']),
        opass: state.getIn(['mobile','opass']), 
        nextMsg: state.getIn(['mobile','nextMsg']),
        isGetNewCode: state.getIn(['mobile','isGetNewCode']), 
        newCodeTime: state.getIn(['mobile','newCodeTime']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleInital() {
            if(JSON.parse(localStorage.getItem('user_info')).EnabledMark <= 0 || JSON.parse(localStorage.getItem('user_info')).DeleteMark == 1) {
                setTimeout(()=>{
                    console.log('http://' + window.location.host + '/#/login')
                    window.location.href = 'http://' + window.location.host + '/#/login'
                    localStorage.removeItem('user_token')
                }, 2000)
            }
            dispatch(actionCreatore.initalAction(false))
        },
        handleGetNewCode(newUser) {
            dispatch(actionCreatore.getNewCodeAction(newUser))
        },
        handleSubmit(user, newUser, code, oldCode, isNextActive, history) {
            if(isNextActive){
                dispatch(actionCreatore.submitAction(user, newUser, code, oldCode, history))
            }
        },
        ChangePass(pass, opass) {
            dispatch(actionCreatore.changePassAction(pass, opass))
        },
        ChangeOpass(pass, opass) {
            dispatch(actionCreatore.changeOpassAction(pass, opass))
        }, 
    }
}
 
export default connect(mapStateToProps, mapDispatchToProps)(MobileRevise);