import  URL  from '../Axios/NEW_URL'
export const ToRem = (value)=>{
    let newValue = parseFloat(value)
    let rem = (10/375)*newValue
    rem += 'rem'
    return rem;
}


export const IPfilter = (int) => {
    console.log(int)
    if(int === 1) {
        let str = URL.GetTaskServerIP
        return getIP(str)
    }else if(int === 2) {
        let str = URL.BASE1
        return getIP(str)
    }else {
        let url = int
        return getIP(url)
    }
    
}

const getIP = (url) => {
    console.log(url)
    if(url) {
        let arr = url.split('.')
        let str = ''
        if(arr.length) {
            str = arr[arr.length - 1]
            return str
        }else {
            return ''
        }
    }else {
        return ''
    }
}