import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Tabs, List } from 'antd-mobile';
import { StickyContainer, Sticky } from 'react-sticky';
// import { Link } from 'react-router-dom'
// import { Toast } from 'antd-mobile';
import { DvaOrderCell } from '../../../../common/Template'
import {orderInfoFilter} from '../../../../common/Js/textFilter'
import { Empty, Pagination, Input } from 'antd';

import {
    Container,
    TaobaoWarp,
    LoadingWarpper,
    LoadingBox,
    LoadingText,
    OrderList,
    OrderCell,
    ItemTop,
    TopLeft,
    ApplyReas,
    IsPro,
    IsPlatPro,
    IsYouPro,
    ApplyDate,
    ListBottom,
    PaginationWarp
} from './style'

function renderTabBar(props) {
    return (<Sticky>
        {({ style }) => <span style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></span>}
    </Sticky>);
}

class Order extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            
        }
    }
    componentDidMount(){
        let nav = JSON.parse(sessionStorage.getItem('nav_data'))
        if(!nav){
            let navData = {
                home: {
                    tab: 0,
                    index: 0
                },
                order: {
                    tab: 1,
                    index: 0
                },
                video: {
                    tab: 2,
                    index: 0
                },
                my: {
                    tab: 3,
                    index: 0
                },
                check: 0
            }
            sessionStorage.setItem('nav_data', JSON.stringify(navData))
            this.props.handleInital(navData)
        }else {
            this.props.handleInital(nav)
        }

    }

    render() { 
        const { changePno, listTotal, listPage, listPno, animite, isLoading, initalIndex, handleTab, changeTab, applyReas, isPlatPro, isYouPro, applyData ,proRes, onList, offList, listData, handleInfo, history, tabs,
         } = this.props
        const nav = JSON.parse(sessionStorage.getItem('nav_data'))
        // const tabs = [
        //     { title: '待处理', sub: '1' },
        //     { title: '已处理', sub: '2' },
        // ]
        const newTabs = tabs.toJS()
        const ListData = listData.toJS()

        const itemRender = (current, type, originalElement) => {
            if (type === 'prev') {
              return <a style={{color: "rgba(0, 0, 0, .65)", lineHeight: '16px'}}>上一页</a>;
            }
            if (type === 'next') {
              return <a style={{color: "rgba(0, 0, 0, .65)", lineHeight: '16px'}}>下一页</a>;
            }
            return originalElement;
        }

        return (<Fragment>
            <Container>
                {/* {
                    isLoading ? (<Tabs tabs={tabs}
                        initialPage={initalIndex}
                        renderTab={tab => <span>{tab.title}</span>}
                        prerenderingSiblingsNumber={0}
                        tabBarUnderlineStyle={{border: '2px #fff solid', bottom: '4px'}}
                        tabBarActiveTextColor='#fff'
                        tabBarInactiveTextColor = '#fff'
                        tabBarBackgroundColor = '#EC503F'
                        onTabClick = {(tab, index)=>{ handleTab(index) }}
                        onChange = {(tab, index)=>{ changeTab(index, nav) }}
                        
                    >  
                    </Tabs>) : null
                } */}
                {
                    isLoading ? (
                        <Tabs tabs={newTabs}
                            initialPage={initalIndex}
                            renderTab={tab => <span>{tab.title}</span>}
                            // renderTabBar={renderTabBar}
                            prerenderingSiblingsNumber={0}
                            tabBarUnderlineStyle={{border: '2px #fff solid', bottom: '4px'}}
                            tabBarActiveTextColor='#fff'
                            tabBarInactiveTextColor = '#fff'
                            tabBarBackgroundColor = '#dda535'
                            onChange = {(tab, index)=>{ changeTab(index, nav, onList, offList, listTotal, listPage, listPno ) }}
                        >  
                            {
                                animite ? (<Fragment>
                                    <TaobaoWarp>
                                            <LoadingWarpper>
                                                <LoadingBox className='dot1'></LoadingBox>
                                                <LoadingBox className='dot2'></LoadingBox>
                                                <LoadingBox className='dot3'></LoadingBox>
                                                <LoadingBox className='dot4'></LoadingBox>
                                                <LoadingBox className='dot5'></LoadingBox>
                                            </LoadingWarpper>
                                            <LoadingText>正在加载中. . .</LoadingText>
                                        </TaobaoWarp>
                                </Fragment>) : (<Fragment>

                                        <OrderList>
                                            {ListData.length ?(null) : (<div className = 'null_data'>无数据</div>) }
                                            {
                                                ListData.map((item, index)=> {
                                                    return (
                                                        <OrderCell key ={index} onClick={() => handleInfo(item.id, history)}>   
                                                            <ItemTop>
                                                                <TopLeft>
                                                                    <ApplyReas> {item.ShopName}反馈：{item.applyReas}</ApplyReas>

                                                                    <IsPro className="AAAA">
                                                                        <div className = "brusher-num">旺旺号：{item.BrusherAliAccount}</div>
                                                                        <div className ="is-done">
                                                                            <IsPlatPro className={item.isPlatPro ? 'active' : 'roof'}>
                                                                                {item.isPlatPro ? '平台已处理' : '待平台处理'}
                                                                            </IsPlatPro>
                                                                            <IsPlatPro className={item.isYouPro ? 'active plat' : 'plat'}>
                                                                                {item.isYouPro ? '您已处理' : '您未处理'}
                                                                            </IsPlatPro>
                                                                        </div>
                                                                    </IsPro>
                                                                </TopLeft>
                                                            </ItemTop>
                                                        <ListBottom>
                                                            { orderInfoFilter(item.proRes) }
                                                            <ApplyDate>{item.applyData}</ApplyDate>
                                                        </ListBottom>
                                                    </OrderCell>)
                                                })                                            
                                            }
                                        </OrderList>

                                        <PaginationWarp>
                                            <div className='total-div'>共{listTotal}条</div>
                                            <Pagination defaultCurrent={1} total={listTotal} defaultPageSize={listPage} pageSize={listPage} current={listPno} onChange={(pno) => { changePno(pno, listPage, initalIndex) }} itemRender={itemRender} simple />
                                        </PaginationWarp>
                                </Fragment>)
                            }
                        </Tabs>
                    ) : null
                }

            </Container>
        </Fragment>)
    }
}

const mapStateToProps = (state)=>{
    return {
        initalIndex: state.getIn(['order','initalIndex']),
        isLoading: state.getIn(['order','isLoading']),
        animite: state.getIn(['order','animite']),
        applyReas: state.getIn(['order','applyReas']),
        isPlatPro: state.getIn(['order','isPlatPro']),
        isYouPro: state.getIn(['order','isYouPro']),
        applyData: state.getIn(['order','applyData']),
        proRes: state.getIn(['order','proRes']),
        onList: state.getIn(['order','onList']),
        offList: state.getIn(['order','offList']),
        listData: state.getIn(['order','listData']),
        tabs: state.getIn(['order','tabs']),
        listTotal: state.getIn(['order','listTotal']),
        listPage: state.getIn(['order','listPage']),
        listPno: state.getIn(['order','listPno']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        changePno(pno, page, int) {
            console.log(pno, page, int)
            dispatch(actionCreatore.MesListRequest(int, page, pno)) 
        },
        handleInital(nav) {
            dispatch(actionCreatore.handleInital(nav))
        },
        handleTab(index) {
            // console.log('click' + index)
        }, 
        changeTab(index, nav, onList, offList, listTotal, listPage, listPno) {
            // console.log('change' + index)
            dispatch(actionCreatore.changeTab(index, nav, onList, offList, listTotal, listPage, listPno ))
        },
        handleInitalList(){
            dispatch(actionCreatore.handleInitalListAction())
        },
        handleInfo(id, history){
            // console.log(id)
            history.push('/taskinfo/'+id)
        }
    }
}
 
export default connect(mapStateToProps, mapDispatchToProps)(Order);