import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    code: '',
    isCode: true,
    pass: '',
    opass: '',
    msg: '',
    nextMsg: '',
    isActive: false,
    isNextActive: false,
    codeTime: 5,
    isGetCode: true,
    newCodeTime: 5,
    isGetNewCode: true,
    nextDis: false
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_CODE:
            return state.set('code', action.value)
        case actionTypes.SET_IS_CODE:
            return state.set('isCode', action.value)
        case actionTypes.SET_IS_ACTIVE:
            return state.set('isActive', action.value)
        case actionTypes.SET_MSG:
            return state.set('msg', action.value)
        case actionTypes.SET_NEXT_MSG:
            return state.set('nextMsg', action.value)
        case actionTypes.SET_IS_NEXT_ACTIVE:
            return state.set('isNextActive', action.value)
        case actionTypes.SET_PASS:
            return state.set('pass', action.value)
        case actionTypes.SET_OPASS:
            return state.set('opass', action.value)
        case actionTypes.SET_CODE_TIME:
            return state.set('codeTime', action.value)
        case actionTypes.SET_IS_GET_CODE:
            return state.set('isGetCode', action.value)
        case actionTypes.SET_NEW_CODE_TIME:
            return state.set('newCodeTime', action.value)
        case actionTypes.SET_IS_GET_NEW_CODE:
            return state.set('isGetNewCode', action.value)
        default:
            return state   
    }
}   