import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../../../common/Js/remToPx'
import {MV} from '../../../../common/Css'
import { fz_2 } from '../../../../common/Css/motive1'

export const SearchWarp = styled.div`
    background-color: #fff;
    padding: ${ToRem(10)};
`

export const TermTime = styled.div`
    
    border-bottom: ${ToRem(0.5)} solid #ddd;
    font-size: ${ToRem(14)};
    color: ${MV.fz_2};
`

export const ContenWarp = styled.div`
    font-size: ${ToRem(14)};
    background-color: #f6f6f6;
    
    .list-warp {
        background-color: #f6f6f6;
        .err-msg {
            text-align: center;
            margin-top: 30px;
            font-size: 16px;
            color: ${MV.dva_color};
        }
        .list-item {
            padding: 10px;
            width: 95%;
            margin: 0 auto;
            background-color: #fff;
            border-radius: 8px;
            overflow: hidden;
            margin-top: 20px;
            .type {
                padding: 0px 14px;
                line-height: 30px;
                display: inline-block;
                margin-left:-20px;
                background: ${MV.dva_color};
                color: #fff;
                border-radius: 5px;
            }
            .good-time {
                color: ${MV.dva_color};
            }
            .title {
                .renwu {

                }
            }
            .good-info {
                margin-top: 10px;
                display: flex;
             
                .img-warp {
                    width: 150px;
                    padding-right: 0px;
                    display: flex;
                    align-items: center;
                    img {
                        width: 100%;
                    }
                }
                .good-desc {
                    margin-left:10px;
                    .good-title {
                        color: rgba(0,0,0,.85);
                    }
                    .good-text {
                        margin-top: 15px;
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                    }
                }
            }
        }
    }
`