import React from 'react';
import {  Sticky } from 'react-sticky';
import { Tabs } from 'antd-mobile';

export const renderTabBar = (props) => {
    return (<Sticky>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
    </Sticky>);
}

export const jfFunction = (max, min, sum) => {
    let str =''
    let count = max - min
    let nubmer = sum - min
    str = ((nubmer/count)*100).toFixed(2)
    return str 
}

export const differFn = (fact, must) => {
    return (must - fact).toFixed(2)
}

export const differPriceFn = (list) => {
    let totalPrice = 0
    for (let i = 0; i < list.length; i++) {
        const el = list[i];
        totalPrice += (el.must_price - el.fact_price)
    }
    return totalPrice
}

export const alllengthFn = (list) => {
    return list.length
}

export const UnokFn = (list) => {
    let sum = 0
    for (let i = 0; i < list.length; i++) {
        const el = list[i];
        if(!el.fact_price){
            sum += 1
        }
    }
    return sum
}