import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

export const ContenWarp = styled.div`
    padding-top: ${ToRem(44)};
    font-size: ${ToRem(14)};
    background-color: #fff;
    padding-bottom: 30px;
    .ww1 {
        width: 95%;
        margin: 0 auto;
        font-size: 16px;
        display: flex;
        margin-bottom: 20px;
        flex-wrap: wrap;
        .content {
            width: 100%;
        }
        .count {
            color: #b84969;
            font-weight: 700;
        }
        .revise-btn {
            font-size: 13px;
            margin-left: 28px;
            padding: 0 8px;
            background-color: ${MV.dva_color};
            border-radius: 5px;
            color: #fff;
            line-height: 24px;
        }
    }
`

export const TaoCode = styled.div`
    border: 1px solid #D5D5D5;
    border-radius: 5px;
    margin-bottom: 10px;
    line-height: 1.5;
    padding: 0 10px;
    font-size: 14px;
    position: relative;
    width: 95%;
    margin: 0 auto;
    padding-top: 30px;
    .lock {
        text-align: center;
        .lock-top {
            .iconfont {
                font-size: 42px;
                font-weight: 700;
                color: rgba(0,0,0,.45);
            }
        }
        .lock-bottom {
            color: rgba(0,0,0,.45);
            font-size: 16px;
            margin-bottom: 20px;
            span {
                color: #b84969;
                font-weight: 700;
            }
        }
    }
    .h1 {
        position: absolute;
        padding: 0px 25px;
        line-height: 30px;
        background: #fff;
        font-weight: 700;
        font-size: 16px;
        top: -15px;
        left: 50%;
        transform: translateX(-50%);
    }
    .box-warp {
        padding: 10px 0px;
    }
    .title {
        font-size: 14px;
        font-weight: 700;
        color: ${MV.dva_color};
        border-bottom: 1px solid #f6f6f6;
        .desp {
            font-weight: 500;
            color: ${MV.fz_4};
        }
    }
    .cell {
        padding: 10px 0px;
        border-bottom: 1px solid #f6f6f6;
        .cell-title {
            .help {
                color: rgb(64, 158, 255);
                margin-left: 20px;
            }
        }
        .pading {
            padding: 10px; 
        }
        .body-warp {
            padding: 0 10px;
        }
        .upload-warp {
            display: flex;
            justify-content: center;
            align-items: flex-end;
            margin-top: ${ToRem(0)};
            &.flex-warp{
                justify-content: space-evenly;
            }
            .upload-box {
                .upload-desc {
                    margin-top: 7px;
                    text-align: center;
                    color: rgba(0,0,0,.45);
                    &.active {
                        color: rgb(64,158,255);
                    }
                }
                .upload-title {
                    margin-bottom: 5px;
                    font-weight: 700;
                    text-align: center;
                }
            }
            .upload-img-box {
                width: ${ToRem(100)};
                background: #ddd;
                border-radius: ${ToRem(5)};
                overflow: hidden;
                .img-box {
                    width: 100%;
                    height: 100%;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .big-box {
                margin-left: ${ToRem(8)};
                font-size: ${ToRem(14)};
                line-height: ${ToRem(16)};
                color: ${MV.bd_color7};
                &.flex-span  {
                    margin-left: 0;
                    text-align: center;
                    margin-top: 7px;
                    line-height: 21px;
                }
            }
        }
    }
`

export const HandleWarp = styled.div`
    padding: ${ToRem(8)} ${ToRem(10)};
    color: ${MV.fz_2};
`

export const InputWarp = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: ${ToRem(40)};
    line-height: ${ToRem(40)};
`

export const InputBoxs = styled.div`
    position: relative;
    border: ${ToRem(1)} solid ${MV.bd_color1};
    border-right: none;
    border-radius: ${ToRem(5)} 0 0 ${ToRem(5)};
    height: 100%;
    overflow: hidden;
    width: calc(100% - ${ToRem(75)});
    &.input-big {
        width: 100% !important;
        border-right: ${ToRem(1)} solid ${MV.bd_color1} !important;
    }
    input {
        font-size: ${ToRem(16)};
        border: none;
        outline: none;
        margin: 0 0;
        padding: 0 0;
        height: ${ToRem(39)};
        line-height: ${ToRem(39)};
        margin-left: ${ToRem(30)};
        width: calc(100% - ${ToRem(30)});
        color: ${MV.fz_2};
        position: absolute;
        top: 0;
        left: 0;
        &::placeholder {
            color: ${MV.fz_4};
        }
    }
`

export const IconWarp = styled.div`
    position: absolute;
    display: inline-flex;
    top: ${ToRem(9)};
    left: ${ToRem(5)};
    line-height: ${ToRem(22)};
    width: ${ToRem(22)};
    height: ${ToRem(22)};
    .iconfont {
        font-size: ${ToRem(20)};
        text-align: center;
        line-height: ${ToRem(22)};
        width: ${ToRem(22)};
        height: ${ToRem(22)};
    }
`

export const InputBtn = styled.div`
    width: ${ToRem(75)};
    background-color: ${MV.dva_color};
    text-align: center;
    color: #fff;
    font-size: ${ToRem(16)};
    line-height: ${ToRem(40)};
    border-radius: 0 ${ToRem(5)} ${ToRem(5)} 0;
`