import { actionTypes } from './index'
import { Toast, Modal } from 'antd-mobile';
import { Axios } from '../../../common/Axios';
import { phoneTest } from '../../../utils/test/index'
const alert = Modal.alert;

const setSubmit = (value)=>({
    type: actionTypes.SET_IS_ACTIVE,
    value
})  

const setIsNextActive = (value)=>({
    type: actionTypes.SET_IS_NEXT_ACTIVE,
    value
})  

const changeState = (user, code, dispatch) => {
    if( user && code){
        dispatch(setSubmit(true))
    }else{
        dispatch(setSubmit(false))
    }
}

const changeStateNext = (pass, opass, dispatch) => {
    if( pass && opass){
        dispatch(setIsNextActive(true))
    }else{
        dispatch(setIsNextActive(false))
    }
}

const setUser = (value) => ({
    type: actionTypes.SET_USER,
    value
})

export const setIsUser = (value)=>({
    type: actionTypes.SET_IS_USER,
    value
})

export const changeUserAction = (user, code) => {
    return (dispatch) => {
        dispatch(setUser(user))
        changeState(user, code, dispatch)
    }
}

