import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../../../common/Js/remToPx'
import {MV} from '../../../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh - ${ToRem(44)} );
    background-color: #FAF8FA;
`

export const PaginationWarp = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
    .total-div {
        padding: 0 ${ToRem(8)};
        color: rgba(0, 0, 0, .65);
        font-size: ${ToRem(14)};
        line-height: ${ToRem(16)};
    }
    .ant-pagination-simple-pager {
        color: rgba(0, 0, 0, .65) !important;
        input {
            color: rgba(0, 0, 0, .65) !important;
        }
    }
`

export const TaobaoWarp = styled.div`
    background-color: #FAF8FA;
    padding: ${ToRem(0)} ${ToRem(10)} ${ToRem(10)} ${ToRem(10)};
    width: 100%;
    min-height: calc(100vh - ${ToRem(44)} - ${ToRem(45)} - ${ToRem(40)});
    position: relative;
    font-size: ${ToRem(14)};
`

export const LoadingWarpper = styled.div`
    position: absolute;
    top: 30%;
    left: 40%;
    margin-left: 10%;
    transform: translate3d(-50%,-50%,0);
    
`
const slide = keyframes`
    0%{transform: scale(1);}
    50%{opacity: .3; transform: scale(2);}
    100%{transform: scale(1);}
`
export const LoadingBox = styled.div`
    width: ${ToRem(24)};
    height: ${ToRem(24)};
    background: #3ac;
    border-radius: 100%;
    display: inline-block;
    animation: ${slide} 1s infinite;  
    &.dot1 {
        animation-delay: .1s;
        background: #32aacc;
    }
    &.dot2 {
        animation-delay: .2s;
        background: #64aacc;
    }
    &.dot3 {
        animation-delay: .3s;
        background: #96aacc;
    }
    &.dot4 {
        animation-delay: .4s;
        background: #c8aacc;
    }
    &.dot5 {
        animation-delay: .5s;
        background: #faaacc;
    }
`

export const LoadingText = styled.div`
    position: absolute;
    top: 40%;
    left: 40%;
    margin-left: 10%;
    transform: translate3d(-50%,-50%,0);
    font-size: ${ToRem(18)};
`

export const OrderList = styled.div`
    padding: 0 ${ToRem(10)};
    margin-top: ${ToRem(10)};
    .null_data{
        text-align: center;
        font-size: ${ToRem(25)};
        margin-top: ${ToRem(50)};
        color: ${MV.dva_color};
    }
`

export const OrderCell = styled.div`
    width: 100%;
    background-color: #fff;
    color: ${MV.fz_1};
    padding: ${ToRem(10)};
    border-radius: ${ToRem(8)};
    margin-top: ${ToRem(10)};

    
`

export const ItemTop = styled.div`
    position: relative;
    &::after { 
        position: absolute;
        content: '';
        height: ${ToRem(1)};
        background-color: ${MV.bg_grey3};
        left: 50%;
        bottom: 0;
        width: 200%;
        transform: translateX(-50%) scale(0.5);
    }
    
`

export const TopLeft = styled.div`
    
    
`

export const ApplyReas = styled.div`
    font-size: ${ToRem(18)};
    color: #000;
    width: 100%;
    font-weight: 600;
    text-align: left;
`

export const IsPro = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: ${ToRem(10)} 0;
    .is-done{
        display: flex;
        justify-content: flex-end;
    }
    .brusher-num{
        font-size: 16px;
        color: ${MV.bg_warning};
    }
`

export const IsPlatPro = styled.div`
    background-color: ${MV.dva_color};
    border-radius: ${ToRem(8)};
    height: ${ToRem(25)};
    line-height: ${ToRem(25)};
    padding: 0px ${ToRem(8)};
    color: #fff;
    font-size: ${ToRem(14)};
    &.roof {
        background-color: ${MV.bg_warning};
    }
    &.active{
        background-color: ${MV.bg_success};
    }
    &.plat{
        margin-left: ${ToRem(10)}; 
    }
`


export const ApplyDate = styled.div`
    padding-top: ${ToRem(5)};
    text-align: right;   
    font-size: ${ToRem(14)};
    color: #000;
  
    
`

export const ListBottom = styled.div`
    padding: ${ToRem(10)};
    font-size: ${ToRem(16)};
    color: #000;
`

