import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'

import {
    CellWarp,
    CellLeft,
    Title,
    CellRight,
    Remark,
    DegWarp,
    TextWarp,
    MustInput
} from './style'


class DvaCell extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    componentWillUnmount(){
        
    }
    render() {
        const { userInfo, rightRender, history, NumberStyle, IconStyle, top, leftIcon,  title,  deg, textVal, rightIcon,  handleCell, mustInput, TitleStyle, handleChange, cellStyle } = this.props 

        return (<Fragment>
            <CellWarp  style={cellStyle}  onClick = { ()=>{ handleCell(history, userInfo) }  }  >
                <CellLeft>
                    {
                        leftIcon ? (<i className='iconfont davBold' style={ IconStyle }>{leftIcon}</i>) : null
                    }
                    <Title style={ TitleStyle }>{title}</Title>
                    {
                        mustInput? (<MustInput className='dva-ellipsis'>({mustInput})</MustInput>) : null
                    }
                </CellLeft>
                <CellRight>
                     <Remark>
                         {
                            textVal ? (<TextWarp className='dva-ellipsis'  style={ NumberStyle } >{textVal}</TextWarp>) : null
                         }
                         {
                            deg ? (<DegWarp>{deg}</DegWarp>) : null
                         }
                        {
                            rightRender
                        }
                     </Remark>
                        {
                            rightIcon?( <i className='iconfont ' >{rightIcon}</i>) : null
                        }
                </CellRight>

            </CellWarp>
        </Fragment>)
    }
}

const mapStateToProps = (state)=>{
    return {

    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
       
        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DvaCell);