import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    selectedTab: 'home',
    titleValue: '抢任务',
    baga: 0
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_TABSELECT:
            return state.set('selectedTab', action.value)
        case actionTypes.SET_TITLEVALUE:
            return state.set('titleValue', action.value)
        case actionTypes.SET_BAGE:
            return state.set('baga', action.value)
            
        default:
            return state    
    }
}   