import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'

import {
    HeaderWarp,
    HeaderBack,
    BackBox,
    HeaderTitle,
    TitleBox,
    HeaderRight
} from './style'

const createHistory = require("history").createHashHistory
const history = createHistory()

class DvaCart extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {}
    }
    componentWillUnmount() {
        // console.log(12314)
    }
    render() {
        const { Left, Title, handleBack, Right, zIndex, background, handleLeft, LeftEvent } = this.props
        // console.log(Title)
        return (<Fragment>
            <HeaderWarp style={{ zIndex: zIndex, background: background }}>
                {
                    LeftEvent ? (<>
                    <HeaderBack onClick={handleLeft}>
                        <BackBox>
                            <i className='iconfont'>&#xe668;</i>
                        </BackBox>
                    </HeaderBack>
                    </>) : (
                            <HeaderBack onClick={handleBack}>
                                {
                                    Left ? (<BackBox>
                                        <i className='iconfont'>&#xe668;</i>
                                    </BackBox>) : null
                                }
                            </HeaderBack>
                        )
                }
                <HeaderTitle>
                    <TitleBox > {Title} </TitleBox>
                </HeaderTitle>
                <HeaderRight>
                    {Right}
                </HeaderRight>
            </HeaderWarp>
        </Fragment>)
    }
}

const mapStateToProps = (state) => {
    return {

    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleBack() {
            history.goBack();
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DvaCart);