import styled from 'styled-components'
import { ToRem } from '../../Js/remToPx'

export const CartWarp = styled.div`
    text-align: center;
`

export const ImgWarp = styled.div`
    width: 100%;
    text-align: center;
    line-height: 1;
    padding: ${ToRem(18)} 0;
    img {
        width: ${ToRem(63)};
        height: ${ToRem(63)};
    }
`

export const TextInfo = styled.div`
    padding: ${ToRem(18)};
    .title {
        display: block;
        font-weight: bold;
        font-size: ${ToRem(27)};
        line-height: ${ToRem(27)};
        text-shadow: ${ToRem(3)} ${ToRem(3)} ${ToRem(7)} #000;
        color: #cfd3d6;
    }
`