import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

export const ContenWarp = styled.div`
    margin-top: ${ToRem(44)};
    font-size: ${ToRem(14)};
    .balance {
        background: #f5f5ff;
        line-height: 65px;
        text-align: center;
        font-size: 18px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        .num {
            color: ${MV.dva_color};
            font-weight: 700;
        }
    }
    .list {
        .li {
            padding: 8px;
            border-bottom: 1px solid rgba(0, 0, 0, .1);
            font-size: 16px;
            .title {
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: ${MV.dva_color};
                .left {

                }
                .right {
                    font-weight: 700;
                    &.fail {
                        color: ${MV.dva_color};
                    }
                    &.success {
                        color: ${MV.bg_success};
                    }
                }
            }
            .spec {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-top: 15px;
                .left {
                    color: rgba(0,0,0,.55);
                }
                .right {

                }
            }
        }
    }
`