import styled from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

export const Container = styled.div`
    min-height: calc(100vh);
    margin-top: ${ToRem(44)};
    background-color: #FAF8FA;
`

export const PopupWarp = styled.div`
    height: 80vh; 
    padding: 8px;
    padding-top: 0;
    font-size: 14px;
    position: relative;
`

export const PopupTitle = styled.div`
    line-height: 35px;
    font-size: 16px;
    color: ${MV.fz_0};
    text-align: center;
`

export const PopupCell = styled.div`
    font-size: 14px;
    text-align: left;
    margin-top: 5px;
    &.to-top {
        margin-top: 20px;
    }
`

export const Popuplabel = styled.div`
    font-size: 14px;
    color: ${MV.dva_color};
    line-height: 21px;
`

export const PopupContent = styled.div`
   margin-top: 7px;  
`

export const PopupList = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: wrap;
    .no-data {
        font-size: 14px;
        line-height: 27px;
    }
    .gift-item {
        width: 45%;
        text-align: center;
        border-radius: 5px;
        border: 1px solid ${MV.fz_2};
        color: ${MV.fz_2};
        margin-bottom: 10px;
        line-height: 27px;
        font-size: 14px;
        &.active {
            background-color: ${MV.dva_color};
            color: #fff;
            border: 1px solid ${MV.dva_color};
        }
    }
`

export const PopupInput = styled.input`
    width: 100%;
    line-height: 27px;
    font-size: 14px;
    color: #333;
    padding: 0 8px;
`

export const PopupSubmit = styled.div`
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 100;
    .popup-btn {
        font-size: 16px;
        /* font-weight: 700; */
        line-height: 35px;
        width: 95%;
        margin: 0 auto;
        margin-bottom: 5px;
        color: #fff;
        background-color: ${MV.dva_color};
    }
`

export const CellContaner = styled.div`
    position: relative;
    margin-top: ${ToRem(10)};
    .PageButton{
        margin: 0 ${ToRem(10)};
        
    }
    .arrow-align{

    }
    .nodata{
        font-size: ${ToRem(12)};
        line-height: ${ToRem(50)};
        color: #666;
    }
`


export const VideoWarp = styled.div`
    
    width: 100%;
    height: 100%;
    
`

export const ImgWarp = styled.div`
    width: 97%; 
    border-radius: ${ToRem(8)} ${ToRem(8)} 0 0;
    overflow: hidden;
    margin: 0 auto;
    img {
        width: 100%;
    }
`


export const JFCell = styled.div`
    text-align: center;
    font-size: ${ToRem(16)};
    position: relative;
    color: #e6e4e4;
    &.rule_info{
        margin-top: ${ToRem(30)};
        padding-bottom: ${ToRem(30)};
    }
    &.rule_info2{
        padding-bottom: ${ToRem(30)};
    }
`

export const CellTitle = styled.div`
    line-height: ${ToRem(30)};
    font-size: ${ToRem(18)};
    font-weight: 700;
    color: #000;
    &.rule{
        font-size: ${ToRem(20)};
        color: #000;
        margin-bottom: ${ToRem(5)};
    }
`

export const JFWarp = styled.div`
    position: relative;
`

export const JFStep = styled.div`
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 95%;
    margin: 0 auto;
    margin-top: ${ToRem(10)};
    i{
        color: #6FBCF6;
    }
`

export const JFBox = styled.div`
    width: ${ToRem(85)};
    height: ${ToRem(50)};
    color: #fff;
    background-color: #fb7299;
    font-size: ${ToRem(15)};
    line-height: ${ToRem(50)};
    &.box2{
        
    }
    &.box3{
        
    }
`

export const JFExplain = styled.div`
   
    width: 80%;
    margin: 0 auto;
    margin-top: ${ToRem(15)};
    font-size: ${ToRem(14)};
    color: #424242;
    line-height: ${ToRem(18)};
`

export const RuleItem = styled.div`
    font-size: ${ToRem(14)};
    text-align: left;
    padding: 0 ${ToRem(10)};
`


export const RuleTitle = styled.div`
    font-weight: 600;
    line-height: ${ToRem(20)};
    color: ${MV.dva_color};
    &.title_box{
        margin-top: ${ToRem(20)};
    }
`

export const RuleText = styled.div`
    color: #000;
    line-height: ${ToRem(20)};

`

export const TableTitle = styled.div`
    width: 95%;
    display: flex;
    margin: 0 auto;
    text-align: center;
    font-size: ${ToRem(12)};
    line-height: ${ToRem(20)};
    .time {
        
    }
    .more {
        border-radius: 0 ${ToRem(5)} 0 0;
        text-decoration: none !important;
    }
`

export const TatleBox = styled.div`
    background-color: #33A3F4;
    padding: ${ToRem(5)} ${ToRem(2)};
    color: #fff;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    &.type{
        width: 25%;
        border-radius: ${ToRem(5)} 0 0 0;
        &.item{
            border-radius: 0;
        }
    }
    &.mask{
        width: 15%;
    }
    &.gift {
        width: 20%;
    }
    &.time{
        width: 20%;
    }
    &.more {
        width: 20%;
        text-decoration: underline;
    }
    &.active{
        background-color: rgba(51, 163, 244, 0.7);
    }
    

`

export const  JFItem = styled.div` 

    width: 95%;
    display: flex;
    margin: 0 auto;
    text-align: center;
    font-size: ${ToRem(12)};
    line-height: ${ToRem(20)};
    
`
   
export const JFList = styled.div`
    .jf-li:first-child {
        margin-top: 0;
    }
`

export const JFLi = styled.div`
    width: 85%;
    margin: 0 auto;
    color: #666;
    font-size: 14px;
    line-height: 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
`

export const ItemLeft = styled.div`

`

export const ItemRight = styled.div`
    .right-btn {
        color: #fff;
        background-color: ${MV.dva_color};
        border-radius: 5px;
        font-size: 12px;
        width: 64px;
        text-align: center;
        &.default {
            background-color: #de938b;
        }
    }
`

export const PopupMoreWarp = styled.div`
    height: 250px;
    overflow: scroll;
    color: ${MV.fz_1};
    font-size: 14px;
    line-height: 1.5;
    text-align: left;
    .cell {
        margin-top: 5px;
        .label {

        }
        .content {
            margin-left: 8px;
        }
    }
`