import React, { Fragment, useState, useEffect, useRef } from 'react';
import { Toast, Modal, Picker, Button } from 'antd-mobile';
import { Redirect, useHistory } from 'react-router-dom'
import { SwiperList, PathImg, PathImgBig, defaultImg } from '../../common/Js/orderFiltter'
import { Input } from 'antd';
import WxImageViewer from 'react-wx-images-viewer';
import { Axios } from '../../common/Axios';
import HtmlImageCompress from 'html-image-compress'
import URL from '../../common/Axios/NEW_URL'
import { phoneTest, emailTest } from '../../utils/test/index'
import { city as myCityList } from '../../common/Js/city';

import {
    Container,
    GroupCell,
    GroupTitle,
    GroupBody,
    Tip,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    InputRight,
    EditItem,
    InputContent,
    InputDiv,
    InputTitle,
    MyImgViewer
} from './style'
import './style.css'

import { DvaHeader, DvaCell } from '../../common/Template'

const UserBasic = () => {
    const bankInput = useRef(null)
    const fileInput = useRef(null)
    const inputName = useRef(null)
    const bankNameInput = useRef(null)
    const inputZFB = useRef(null)

    let history = useHistory();
    const alert = Modal.alert;

    useEffect(() => {
        Toast.loading('正在加载数据中...', 0, null, true)
        setPageLoading(true)
        getUserInfo(data => {
            Toast.hide()
            setPageLoading(false)
            setUser(data)
            if (data.BankCategory) setSelectBankIndex(1)
            bankInput.current.value = data.BankCardNum
            setZfbImg(data.ZFBPP)
            setTbImg(data.TBPP)
            setIdImg(data.IDCardFront)
            setUnIdImg(data.IDCardBack)
            setName(data.RealName)
            setZFB(data.ExtData)
            setIsNeedId(data.IsNeedUpSFZ)
            getBankList(Bankdata => { 
                setBankList(Bankdata)
                for (let i = 0; i < Bankdata.length; i++) {
                    const el = Bankdata[i];
                    if(el.label === data.BankCategory) {
                        setSelectBankIndex([i])
                        break;
                    }
                }
            })
        })
        getAddressList(data => {
            // console.log(data)
        })
    }, [])

    let token = localStorage.getItem('user_token')

    const getAddressList = (callback) => {
        Axios('get', '/GetHHLHProvince', {
        }).then(data => {
            callback(data)
        }).catch(err => {

        })
    }

    const handleRealName = (RealName) => {
        // Toast.info('姓名不可直接修改，通过上传并识别身份证正面照或支付宝截图自动填写', 2, null, false)
        // prompt('修改姓名', '', [
        //     { text: '取消' },
        //     { text: '确定', onPress: (value) => {
        //         dispatch(actionCreatore.setRealName(value, RealName))
        //     }  },
        //   ], 'default', RealName)
    }

    const getUserInfo = (callback) => {
        Axios('get', '/GetBrusherInfo', {}).then(data => {
            localStorage.setItem('user_info', JSON.stringify(data.resultdata))
            callback(data.resultdata)
        }).catch(err => {

        })
    }

    const getBankList = (callback) => {
        Axios('get', '/GetDataDic', {
            code: 'JS_BankCategory'
        }).then(data => {
            let arr = data.resultdata
            for (let i = 0; i < arr.length; i++) {
                const el = arr[i];
                el.label = el.F_ItemName
                el.value = i
            }
            callback(arr)
        }).catch(err => {

        })
    }

    const getAddressBankList = (Address, callback) => {
        Toast.loading('正在加载数据中...', 0, null, true)
        Axios('get', '/GetBankListByCityName', {
            CityName: Address,
            BacnkCategory: user.BankCategory
        }).then(data => {
            Toast.hide()
            let arr = data.resultdata
            for (let i = 0; i < arr.length; i++) {
                const el = arr[i];
                el.label = el.branch_bank_name
                el.value = i
                el.HangLianHao = el.HangLianHao
            }
            callback(arr)
        }).catch(err => {

        })
    }

    const changeBank = (value, bankList) => {
        console.log(bankList, value)
        let val = bankList[value].label
        user.BankCategory = val
        setSelectBankIndex(value)
        setUser(user)
    }

    const handleInputPassChange = () => {
        let inputVal = bankInput.current.value
        user.BankCardNum = inputVal
        setUser(user)
    }

    const viewerClose = () => {
        setViewerShow(false)
    }

    const getshibieInfo = (path, callback) => {
        Toast.loading("正在识别图片信息...", 0, null, true);
        Axios('get2', '/RecognizeOrderPic', {
            token: JSON.parse(localStorage.getItem('user_token')),
            picpath: URL.IMG_UPLOAD.replace("hapi","") + path
        }).then(res => {
            Toast.hide();
            callback(res.message);
        }).catch(err => {
            Toast.hide();
            Toast.fail('识别失败，截图不清晰或图片信息不正确!', 2, null, false);
        })
    }

    const changeFile = (input, user) => {
        let file = input.target.files[0];
        if (beforeUpload(file)) {
            getBase64(input.target.files[0], imgInfo => {
                SaveImgToServer(imgInfo, '', '', 'IDInfo', function (path) {
                    if (uploadType === 'ZFB') {
                        setZfbImg(path)
                        user.ZFBPP = path
                        if (IsNeedId !== 1) {
                            getshibieInfo(path, (data) => {
                                console.log(data)
                                let str1 = data.split('实名认证')[1]
                                debugger

                                if (str1) {
                                    let psname = str1.split('已实名')[0]
                                    if(psname.length>5)
                                    {
                                         psname = str1.split('已认证')[0]
                                    }
                                    console.log(psname)
                                   // psname.replace("显","")
                                    if (psname) {
                                        Toast.hide()
                                        Toast.success(`识别成功，识别的姓名为 ${psname}`, 2, null, false)
                                        user.RealName = psname
                                        setName(psname)
                                        return
                                    } else {
                                        Toast.fail('识别失败，您的截图不包含姓名,请重新截图,截图信息包含关键字  实名认证 和 已实名', 2, null, false);
                                        return
                                    }
                                } else {
                                    Toast.fail('识别失败，您的截图不包含姓名,请重新截图,截图信息包含关键字  实名认证 和 已实名', 2, null, false);
                                    return
                                }
                            })
                        }
                    } else if (uploadType === 'TB') {
                        setTbImg(path)
                        user.TBPP = path
                    } else if (uploadType === 'ID') {
                        setIdImg(path)
                        user.IDCardFront = path
                        getIdInfo(path, (data) => {
                            user.IDCardNum = data.num
                            if (user.IDCardNum.length !== 18) {
                                alert('用户身份证信息不可遮挡,身份证号为18位');
                                return
                            }
                            user.Address = data.address
                            user.RealName = data.name
                            if (data.sex === '男') {
                                user.Gender = 1
                            } else if (data.sex === '女') {
                                user.Gender = 0
                            }
                            user.Birthday = birthdayFillter(data.birth)
                            setName(data.name)
                        })
                    } else if (uploadType === 'UNID') {
                        setUnIdImg(path)
                        user.IDCardBack = path
                        console.log('身份证反面')
                    }
                    setUser(user)
                })
            })
        }
    }

    // 识别的生日格式化处理
    const birthdayFillter = (time) => {
        let str = ''
        if (time) {
            let i = time.substring(0, 4)
            let k = time.substring(4, 6)
            let m = time.substring(6, 8)
            let s = '-'
            let h = '00:00:00'
            str += (i + s + k + s + m + ' ' + h)
        }
        return str
    }

    // 识别正面身份照信息
    const getIdInfo = (path, callback) => {
        // callback(data)
        Toast.loading("正在识别身份信息...", 0, null, true);
        Axios('get2', '/GetIDInfo', {
            PicPath: path
        }).then(res => {
            Toast.hide();
            callback(res.resultdata);
        }).catch(err => {
            Toast.hide();
            Toast.fail('识别失败,请上传正确的身份证照片!', 2, null, false);
        })
    }

    const beforeUpload = (file) => {
        if (file == undefined) {
            Toast.fail('请拍照或上传图片', 2, null, false);
            return false;
        }

        const isLt2M = file.size / 1024 / 1024 < 13;
        if (!isLt2M) {
            Toast.fail('图片大小不超过13M', 2, null, false);
        }
        return isLt2M;
    }

    const getBase64 = (img, callback) => {
        let htmlImageCompress = new HtmlImageCompress(img, { width: 1200, height: 800, quality: 0.9 })
        htmlImageCompress.then(function (result) {
            // 成功后执行 
            let file = result.file; // 压缩后的图片文件(file)对象
            let fileSize = result.fileSize // 压缩后的图片文件(file)大小
            let base64 = result.base64; // 压缩后的base64图片
            let origin = result.origin; // 压缩前的图片文件（file）对象
            callback(base64);
        }).catch((err) => {
            // 处理失败会执行 
            const reader = new FileReader();
            reader.addEventListener('load', () => {
                let newB64 = reader.result;
                callback(newB64);
            });
            reader.readAsDataURL(img);
        })
    }

    const SaveImgToServer = (base64, orderIds, fieldName, Module, callback) => {
        Toast.loading("正在上传...", 0, null, true);
        //上传到OSS
        Axios('post2', '/SaveImg', {
            token: JSON.parse(localStorage.getItem('user_token')),
            base64,
            orderIds,
            fieldName,
            Module
        }).then(data => {
            Toast.hide();
            callback(data.resultdata);
        }).catch(err => {
            Toast.fail('上传失败,请重试!', 2, null, false);
        })
    }

    const popupCloose = () => {
        setZfbShow(false)
    }

    const handlePopup = (int) => {
        setPopupType(int)
        setTimeout(() => {
            setZfbShow(true)
        }, 0)
    }

    const handleUpload = (int) => {
        let status = false
        if (user.EnabledMark === 1) {
            if (int == 'ID') {
                if (!user.IDCardNum) {
                    status = true
                }
            } else if (int == 'UNID') {

                status = true

            }
        } else {
            status = true
        }

        if (int == "ZFB" && user.ZFBPP == null) status = true
        if (int == "TB" && user.TBPP == null) status = true

        if (status) {
            setUploadType(int)
            fileInput.current.input.click()
        } else {
            Toast.info('您的账号审核已通过，请不要修改身份证信息和支付宝截图，以及淘宝账号截图信息。', 2, null, false)
        }
    }

    const clickName = () => {
        if (user.EnabledMark === 1) {
            Toast.info('您的账号审核已通过，请不要手动修改姓名', 2, null, false)
        }
        // Toast.info('请不要手动修改姓名,必须通过支付宝截图的实名识别', 2, null, false)
    }

    const handleViewer = (img) => {
        setSelectImg(img)
        setTimeout(() => {
            setViewerShow(true)
        }, 0)
    }

    const handleCardShow = (img) => {
        setSelectImg(img)
        setTimeout(() => {
            setIdCardShow(true)
        }, 0)
    }

    const changeName = () => {
        let input = inputName.current.value
        setName(input)
        user.RealName = input
        setUser(user)
    }

    const handleSubmit = () => {
        let CreateUserName = user.CreateUserName
        if (!user.RealName) {
            Toast.fail('用户姓名不能为空（通过上传并识别身份证正面照自动填写）', 2, null, false)
            return
        }
        if (user.RealName.length < 2 || user.RealName.length > 10) {
            Toast.fail('用户姓名的长度为2-10位！！！）', 2, null, false)
            return
        }
        if(!phoneTest.test(ZFB)){
            if(!emailTest.test(ZFB)){
                Toast.fail('支付宝格式不正确，请输入手机格式或邮箱格式', 2, null, false)
                return
            }
        }
        if (IsNeedId === 1) {
            if (!user.IDCardFront) {
                Toast.fail('身份证正面照片不能为空，请先上传！！', 2, null, false)
                return
            }
            if (!user.IDCardFront) {
                Toast.fail('身份证正面照片不能为空，请先上传！！', 2, null, false)
                return
            }
            if (!user.IDCardNum) {
                Toast.fail('您上传的身份证不正确,请上传正确的身份证图片', 2, null, false)
                return
            }
        }
        if(CreateUserName !== '极品王紫发') {
            // console.log('当前不为极品王紫发，银行卡不能为空')
            // return 
            if (!user.BankCategory) {
                Toast.fail('银行名称不能为空，请先选择！！', 2, null, false)
                return
            }
            if (!user.BankCardNum) {
                Toast.fail('银行账号不能为空，请先填写！！', 2, null, false)
                return
            }
        }
        // if(!user.BankUserName) {
        //     Toast.fail('支行地址不能为空，请先填写！！', 2, null, false)
        //     return
        // }
        if (!user.ZFBPP) {
            Toast.fail('支付宝截图不能为空，请先上传！！', 2, null, false)
            return
        }
        if (!user.TBPP) {
            Toast.fail('淘宝截图不能为空，请先上传！！', 2, null, false)
            return
        }
        // console.log(user)
        // return
        Toast.loading('正在提交中...', 0, null, true)
        Axios('post', '/UpdateBrusherInfo', {
            token: JSON.parse(localStorage.getItem('user_token')),
            ZFBPP: user.ZFBPP,
            RealName: user.RealName,
            IDCardFront: user.IDCardFront,
            IDCardBack: user.IDCardBack,
            BankCategory: user.BankCategory,
            BankCardNum: user.BankCardNum,
            TBPP: user.TBPP,
            IDCardNum: user.IDCardNum,
            Address: user.Address,
            ExtData: user.ExtData,
            BankUserName: user.BankUserName,
            HangLianHao: user.HangLianHao
        }).then(res => {
            Toast.hide();
            if (res.type == 1) {
                Toast.success(res.message, 2, null, true)
                // setTimeout(()=>{
                //     history.push('/main');
                // }, 2000)
            } else {
                Toast.fail(res.message, 2, null, false)
            }
        }).catch(err => {
            Toast.fail('用户信息提交失败,请重试!', 2, null, false);
        })
    }

    const AddressChildren = props => (<EditItem onClick={props.onClick} className='user-address'>
        <InputContent>
            <InputDiv>{props.extra}</InputDiv>
        </InputContent>
    </EditItem>)

    const changeCity = (value, citys) => {
        let omit, city, address, omitIndex, cityIndex
        for (let i = 0; i < citys.length; i++) {
            if (citys[i].value === value[0]) {
                omit = citys[i].label
                omitIndex = i
                break;
            }
        }
        for (let k = 0; k < citys[omitIndex].children.length; k++) {
            if (citys[omitIndex].children[k].value === value[1]) {
                city = citys[omitIndex].children[k].label
                cityIndex = k
                break;
            }
        }
        // for(let m=0; m<citys[omitIndex].children[cityIndex].children.length; m++){
        //     if(citys[omitIndex].children[cityIndex].children[m].value === value[2]){
        //         address = citys[omitIndex].children[cityIndex].children[m].label
        //         break;
        //     }
        // }
        setOmit(omit)
        setCity(city)
        // setAddress(address)
        setSelectAddress(value)
        getAddressBankList(city, (res) => {
            setAddressBankList(res)
        })
    }

    const reviseBankName = () => {
        if (!isShow) {
            setSelectAddress('')
        }
        setIsShow(!isShow)
    }

    const bankNameOk = () => {
        // if(!selectAddress) {
        //     Toast.fail("请先填写支行地址", 2, null, false) 
        //     return
        // }
        console.log(omit, city, address, bankName)
        user.BankUserName = omit + city + address + bankName
        setUser(user)
        reviseBankName()
    }

    const bankNameChange = (value, list) => {
         // return
         debugger;
        let val = list[value].label
        setSelectAddressBank(value)
        setBankName(val)
       
        user.HangLianHao = list[value].lianhanghao
        setUser(user)
    }

    const clickZFB = () => {

    }
    
    const changeZFB = () => {
        let input = inputZFB.current.value
        setZFB(input)
        user.ExtData = input
        setUser(user)
    }

    const [user, setUser] = useState({})
    const [bankList, setBankList] = useState([])
    const [SelectBankIndex, setSelectBankIndex] = useState(null)
    const [viewerShow, setViewerShow] = useState(false)
    const [selectImg, setSelectImg] = useState(null)
    const [zfbShow, setZfbShow] = useState(false)
    const [zfbImg, setZfbImg] = useState(null)
    const [tbImg, setTbImg] = useState(null)
    const [uploadType, setUploadType] = useState(null)
    const [IdImg, setIdImg] = useState(null)
    const [UnIdImg, setUnIdImg] = useState(null)
    const [name, setName] = useState(null)
    const [popupType, setPopupType] = useState(null)
    const [IsNeedId, setIsNeedId] = useState(-1)
    const [myCity, setMyCity] = useState(JSON.parse(myCityList))
    const [omit, setOmit] = useState('')
    const [city, setCity] = useState('')
    const [address, setAddress] = useState('')
    const [bankName, setBankName] = useState('')
    const [selectAddress, setSelectAddress] = useState(null)
    const [isShow, setIsShow] = useState(true)
    const [idCardShow, setIdCardShow] = useState(false)
    const [selectAddressBank, setSelectAddressBank] = useState(null)
    const [addressBankList, setAddressBankList] = useState([])
    const [PageLoading, setPageLoading] = useState(true)
    const [ZFB, setZFB] = useState(null)

    const CustomChildren = props => (<EditItem onClick={props.onClick} className='user-address'>
        <InputContent>
            <InputDiv >{props.extra}</InputDiv>
        </InputContent>
    </EditItem>)

    const AddressBankChildren = props => (<EditItem onClick={props.onClick} className='user-address'>
        <InputContent>
            <InputDiv className='dva-ellipsis'>{props.extra}</InputDiv>
        </InputContent>
    </EditItem>)

    if (!token) {
        Toast.fail('用户信息过期，请重新登录', 1, null, true)
        return <Redirect to="/login" />
    } else {
        return (<Fragment>
            {
                viewerShow ? (<WxImageViewer onClose={viewerClose} urls={SwiperList(selectImg)} index={0} />) : null
            }
            {
                idCardShow ? (<MyImgViewer onClick={() => { setIdCardShow(false) }}>
                    <div className='img-warp'>
                        <img src={SwiperList(selectImg)} alt="" />
                        <div className='spec'>只作精品平台认证,不作其它用途使用</div>
                    </div>
                </MyImgViewer>) : null
            }
            <Input
                type='file'
                id='uploadInput'
                accept='image/*'
                style={{ display: 'none' }}
                ref={fileInput}
                onChange={(input) => { changeFile(input, user) }}
            ></Input>

            <Modal
                popup
                visible={zfbShow}
                onClose={popupCloose}
                animationType="slide-up"
                afterClose={popupCloose}
            >
                <div className='popup-warp'>
                    {
                        popupType === 'idcard' ? (<>
                            <div className='popup-title'>身份证照片示例</div>
                            <div className='popup-body'>
                                <div className='popup-box'>
                                    <p className='desc'>身份证正面照片清晰完整无障碍物，能够正确识别身份信息</p>
                                    <img src="/imgs/idcard.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>身份证反面照片清晰完整无障碍物，能够正确识别身份信息</p>
                                    <img src="/imgs/unidcard.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                            </div>
                        </>) : (popupType === 'ZFB' ? (<>
                            <div className='popup-title'>获取支付宝截图流程</div>
                            <div className='popup-body'>
                                <div className='popup-box'>
                                    <p className='desc'>一、打开支付宝主页，点击右上角,  进入点击“账号与安全”</p>
                                    <img src="/imgs/zfb1.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>二、进入个人信息页面后，截图保存图片并上传即可</p>
                                    <img src="/imgs/zfb2.png" alt="" style={{ width: '100%' }} />
                                </div>
                            </div>
                        </>) : (popupType === 'TB' ? (<>
                            <div className='popup-title'>获取淘宝截图流程</div>
                            <div className='popup-body'>
                                <div className='popup-box'>
                                    <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击右上角设置图标进入设置页面</p>
                                    <img src="/imgs/tb1.png" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>二、进入设置页面后，点击 <span>账户与安全</span> 进入账户与安全页面</p>
                                    <img src="/imgs/tb2.jpg" alt="" style={{ width: '100%' }} />
                                </div>
                                <div className='popup-box'>
                                    <p className='desc'>三、进入账户与安全页面后，截图保存图片并上传即可</p>
                                    <img src="/imgs/tb3.png" alt="" style={{ width: '100%' }} />
                                </div>
                            </div>
                        </>) : null))
                    }
                </div>
            </Modal>

            <DvaHeader Left={true} Title='用户基本信息'></DvaHeader>
            {
                PageLoading ? null : (<Container>
                    {
                        IsNeedId === 1 ? (<GroupCell>
                            <GroupTitle>
                                <div className='title davBold'>身份证信息 <span style={{ fontSize: 12 }}>{IsNeedId === 1 ? "（必填）" : "（选填）"}</span></div>
                                <div className='spec davBold' onClick={() => { handlePopup('idcard') }}>身份证照片示例</div>
                            </GroupTitle>
                            <GroupBody>
                                <Tip>用户姓名必须与支付宝实名认证的姓名和银行卡绑定的姓名，以及身份证上的姓名保持一致，否则无法提现</Tip>
                                <div className='pading'>
                                    <div className='upload-warp flex-warp'>
                                        <div className='upload-box'>
                                            <div className='upload-title'>身份证正面照片</div>
                                            <div onClick={() => { handleUpload('ID') }} className='upload-img-box'>
                                                <div className='img-box'>
                                                    <img className={IdImg ? '' : 'default-img'} src={IdImg ? (PathImg(IdImg)) : null} alt="" onClick={() => { return false }} />
                                                </div>
                                            </div>
                                            {/* {
                                                IdImg ? (<div className='big-box flex-span' onClick={() => { handleViewer(PathImgBig(IdImg, defaultImg)) }}>查看大图</div>) : (<div className='upload-desc'>暂无上传图片</div>)
                                            } */}
                                            {
                                                IdImg ? (<div className='big-box flex-span' onClick={() => { handleCardShow(PathImgBig(IdImg, defaultImg)) }}>查看大图</div>) : (<div className='upload-desc'>暂无上传图片</div>)
                                            }
                                        </div>
                                        <div className='upload-box'>
                                            <div className='upload-title'>身份证反面照片</div>
                                            <div onClick={() => { handleUpload('UNID') }} className='upload-img-box'>
                                                <div className='img-box'>
                                                    <img className={UnIdImg ? '' : 'default-img'} src={UnIdImg ? (PathImg(UnIdImg)) : null} alt="" onClick={() => { return false }} />
                                                </div>
                                            </div>
                                            {/* {
                                                UnIdImg ? (<div className='big-box flex-span' onClick={() => { handleViewer(PathImgBig(UnIdImg, defaultImg)) }}>查看大图</div>) : (<div className='upload-desc'>暂无上传图片</div>)
                                            } */}
                                            {
                                                UnIdImg ? (<div className='big-box flex-span' onClick={() => { handleCardShow(PathImgBig(UnIdImg, defaultImg)) }}>查看大图</div>) : (<div className='upload-desc'>暂无上传图片</div>)
                                            }
                                        </div>
                                    </div>
                                </div>
                            </GroupBody>
                        </GroupCell>) : null
                    }

                    {/* <GroupCell>
                        <GroupTitle>
                            <div className='title davBold'>用户姓名 <span style={{fontSize: 12}}>（必填）</span></div>
                        </GroupTitle>
                        <GroupBody>
                            <Tip>用户姓名必须与支付宝实名认证的姓名和银行卡绑定的姓名，以及身份证上的姓名保持一致，否则无法提现</Tip>
                            <div className='pading'>
                                <DvaCell cellStyle={{ marginTop: 1, padding: 0, borderBottom: '1px solid #f6f6f6' }} TitleStyle={{ marginLeft: 0, fontWeight: 500 }} title ="姓名" textVal = {name}  rightIcon= "&#xe6ab;"  handleCell ={() =>{handleRealName(name)}}></DvaCell>
                            </div>
                        </GroupBody>
                    </GroupCell> */}

                    <GroupCell>
                        <GroupTitle>
                            <div className='title davBold'>银行卡绑定 <span style={{ fontSize: 12 }}>（必填）</span></div>
                        </GroupTitle>
                        <GroupBody>
                            <Tip>银行卡必须与身份证上的姓名保持一致，请不要使用信用卡绑定，<span>不支持银行卡列表上没有显示的银行</span>，如果绑定错误，请联系专属客服</Tip>
                            <Tip>用户姓名必须与支付宝实名认证的姓名和银行卡绑定的姓名，以及身份证上的姓名保持一致，否则无法提现</Tip>
                            
                            <div className='pading' onClick={clickName}>
                                <DvaCell cellStyle={{ marginTop: 1, padding: 0, paddingLeft: 26, borderBottom: '1px solid #f6f6f6' }} TitleStyle={{ marginLeft: 0, fontWeight: 500 }} title="姓名" rightIcon="&#xe6ab;" handleCell={() => { handleRealName(name) }} rightRender={(<>
                                    <input type="text" placeholder='请输入用户名' value={name} style={{ border: 'none', textAlign: 'right', fontSize: 16, width: 220 }}
                                        onChange={changeName}
                                        ref={inputName}
                                        disabled={user.EnabledMark === 1 ? true : false}

                                    />
                                </>)} ></DvaCell>
                            </div>
                            <div className='pading' onClick={clickZFB}>
                                <DvaCell cellStyle={{ marginTop: 1, padding: 0, paddingLeft: 26, borderBottom: '1px solid #f6f6f6' }} TitleStyle={{ marginLeft: 0, fontWeight: 500 }} title="支付宝账号" rightIcon="&#xe6ab;" handleCell={() => {  }} rightRender={(<>
                                    <input type="text" placeholder='请输入支付宝账号' value={ZFB} style={{ border: 'none', textAlign: 'right', fontSize: 16, width: 220 }}
                                        onChange={changeZFB}
                                        ref={inputZFB}
                                    />
                                </>)} ></DvaCell>
                            </div>
                            <LoginWarpper>
                                <LoginBox>
                                    {/* <InputBox>
                                        <NavInput>
                                            <input
                                                type="text"
                                                ref={(input) => { this.inputUser = input }}
                                                onChange={() => { handleInputUserChange(this.inputUser, this.inputPass) }}
                                                placeholder='请输入银行卡关联人姓名'
                                                value={loginUser}
                                            />
                                        </NavInput>
                                    </InputBox> */}
                                    <InputBox className='bank-input'>
                                        <InputTitle className='dva-ellipsis'>银行名称</InputTitle>
                                        <InputRight>
                                            <div className={SelectBankIndex ? 'picker-warp active' : 'picker-warp'}>
                                                <Picker
                                                    extra={SelectBankIndex ? user.BankCategory : '请选择银行'}
                                                    className='AAAAAAAAAAAA'
                                                    data={bankList}
                                                    value={SelectBankIndex}
                                                    onChange={(value) => { changeBank(value, bankList) }}
                                                    cols={1}
                                                    title={'选择银行类型'}
                                                >
                                                    <CustomChildren>Customized children</CustomChildren>
                                                </Picker>
                                            </div>
                                            <i className='iconfont ' >&#xe6ab;</i>
                                        </InputRight>
                                    </InputBox>
                 

                                    <InputBox>
                                        <NavInput>
                                            <div className='input-label'>银行账号:</div>
                                            <input
                                                type="number"
                                                ref={bankInput}
                                                onChange={handleInputPassChange}
                                                placeholder='请输入银行卡账号'
                                            />
                                        </NavInput>
                                    </InputBox>
                                </LoginBox>
                            </LoginWarpper>
                        </GroupBody>
                    </GroupCell>

                    <GroupCell>
                        <GroupTitle>
                            <div className='title davBold'>上传支付宝截图 <span style={{ fontSize: 12 }}>（必填）</span></div>
                            <div className='spec davBold' onClick={() => { handlePopup('ZFB') }}>如何获取支付宝信息？</div>
                        </GroupTitle>
                        <GroupBody>
                            <Tip>支付宝手机号必须与注册平台的手机号保持一致，且姓名也需保持一致(否则无法提现)，</Tip>
                            <div className='pading'>
                                <div className='upload-warp'>
                                    <div onClick={() => { handleUpload('ZFB') }} className='upload-img-box'>
                                        <div className='img-box'>
                                            <img className={zfbImg ? '' : 'default-img'} src={zfbImg ? (PathImg(zfbImg)) : null} alt="" onClick={() => { return false }} />
                                        </div>
                                    </div>
                                    {
                                        zfbImg ? (<div className='big-box' onClick={() => { handleViewer(PathImgBig(zfbImg, defaultImg)) }}>查看大图</div>) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                    }
                                </div>
                            </div>
                        </GroupBody>
                    </GroupCell>

                    <GroupCell>
                        <GroupTitle>
                            <div className='title davBold'>上传淘宝账号截图 <span style={{ fontSize: 12 }}>（必填）</span></div>
                            <div className='spec davBold' onClick={() => { handlePopup('TB') }}>如何获取淘宝账号？</div>
                        </GroupTitle>
                        <GroupBody>
                            <Tip>淘宝账号手机号必须与注册平台的手机号保持一致(否则无法提现)</Tip>
                            <div className='pading'>
                                <div className='upload-warp'>
                                    <div onClick={() => { handleUpload('TB') }} className='upload-img-box'>
                                        <div className='img-box'>
                                            <img className={tbImg ? '' : 'default-img'} src={tbImg ? (PathImg(tbImg)) : null} alt="" onClick={() => { return false }} />
                                        </div>
                                    </div>
                                    {
                                        tbImg ? (<div className='big-box' onClick={() => { handleViewer(PathImgBig(tbImg, defaultImg)) }}>查看大图</div>) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                    }
                                </div>
                            </div>
                        </GroupBody>
                    </GroupCell>

                    <div className='bottom-warp'>
                        <div className='bottom-btn' onClick={handleSubmit} >提交资料</div>
                    </div>
                </Container>)
            }
        </Fragment>)
    }


}

export default UserBasic