import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh - ${ToRem(44)} );
    background: #fff;
    margin-top: ${ToRem(44)};
`

export const LoginWarpper = styled.div`
    position: relative;
    flex: 1;
    padding: ${ToRem(10)} ${ToRem(10)} 0;
`

export const LoginBox = styled.div`
    background-color: #fff;
    border-radius: ${ToRem(5)};
    overflow: hidden;
    padding: ${ToRem(5)} 0;
`

export const InputBox = styled.div`
    position: relative;
    box-sizing: border-box;
    margin-bottom: 0.45333rem;
    padding: ${ToRem(10)} 0;
    width: 100%;
    border-bottom: 0.02667rem solid #f5f5f5;
    font-size: ${ToRem(17)};
    color: ${MV.fz_2};
    &.bank-input {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
`

export const NavInput = styled.div`
    width: 100%;
    font-size: 0.45333rem;
    border-radius: 0;
    input {
        border: none;
        outline: none;
        width: calc(100% - ${ToRem(30)} );
        margin-left: ${ToRem(20)};
        line-height: ${ToRem(23)};
        font-size: ${ToRem(17)};
        border-radius: 0;
        color: #2b2b2b;
        background-color: transparent;
        &::placeholder {
            color: #dcddde;
        }
    }
    .inputTpye {
        position: absolute;
        top: ${ToRem(6)};
        right: 0.26667rem;
        font-size: 0.53333rem;
        font-weight: 800;
        z-index: 1;
        .iconfont {
            color: #909399;
            font-size: ${ToRem(25)};
        }
    }
`

export const NavNote = styled.div`
    position: absolute;
    top: ${ToRem(11)};
    left: ${ToRem(20)};
    width: 100%;
    z-index: 0;
    line-height: ${ToRem(23)};
    font-size: 0.42667rem;
    pointer-events: none;
    color: #dcddde;
    &.none {
        display: none;
    }
`

export const StartUse = styled.div`
    margin-top: 0.74667rem;
    width: 100%;
    height: 1.12rem;
    line-height: 1.12rem;
    text-align: center;
    border-radius: 0.56rem;
    background-color: #d7d7d7;
    font-size: 0.4rem;
    color: #ffffff;
    &.active {
        background-color: #EC503F;
    }
`

export const ForgetBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: ${ToRem(18)};
    font-size: ${ToRem(14)};
    line-height: ${ToRem(14)};
    .forget {
        text-decoration: none;
        color: #a4a4a4;
    }
    .to-rigister {
        color: #EC503F; 
    }
`

export const MsgWarp = styled.div`
    color: #EC503F;
    height: ${ToRem(19)};
    line-height: ${ToRem(19)};
    font-size: ${ToRem(12)};
    font-weight: bold;
    text-align: center;
`

export const Tip = styled.div`
    color: ${MV.fz_2};
    line-height: ${ToRem(16)};
    font-size: ${ToRem(14)};
    text-align: left;
    margin-bottom: ${ToRem(25)} ;
    padding: ${ToRem(10)} ${ToRem(5)};
    background-color: #eee;
    margin-bottom: ${ToRem(5)};
    span
    {
        font-size: ${ToRem(14)};
        color: #EC503F;
    }
`

export const InputTitle = styled.div`
    color: ${MV.fz_1};
    width: ${ToRem(100)};
    margin-left: ${ToRem(20)};
    font-size: ${ToRem(17)};
    line-height: ${ToRem(25)};
    height: ${ToRem(25)};
    position: absolute;
`

export const InputRight = styled.div`
    width: 100%;
    background-color: transparent;
    text-align: right;
    position: relative;
    .picker-warp {
        margin-right: ${ToRem(25)};
        color: ${MV.fz_3};
        &.active {
            color: ${MV.fz_1};
        }
    }
    i {
        position: absolute;
        line-height: ${ToRem(20)};
        width: ${ToRem(20)};
        text-align: center;
        height: ${ToRem(16)};
        top: ${ToRem(5)};
        right: 0;
    }
`

export const OpenList = styled.div`
    color: #EC503F;
    position: absolute;
    top: 0.4rem;
    right: ${ToRem(20)};
    text-align: right;
    width: 100%;
    z-index: 0;
    height: 0.42667rem;
    line-height: 0.42667rem;
    font-size: 0.42667rem;
    pointer-events: none;
    line-height: ${ToRem(25)};
    height: ${ToRem(25)};
    top: ${ToRem(10)};
    display: flex;
    justify-content: flex-end;
    align-items: center;
    right: ${ToRem(3)};
    i{ 
        color: ${MV.fz_1};
        font-size: ${ToRem(16)};
        line-height: ${ToRem(16)};
        height: ${ToRem(16)};
        width: ${ToRem(16)};
        text-align: center;
        position: absolute;
        top: ${ToRem(6.5)};
    }
   
`


export const modal = styled.div`
    color: #EC503F;
    position: absolute;
    top: 0.4rem;
    right: ${ToRem(20)};
    text-align: right;
    width: 100%;
    z-index: 0;
    height: 0.42667rem;
    line-height: 0.42667rem;
    font-size: 0.42667rem;
    pointer-events: none;
    i{ 
        color: #000;
    }
   
`

export const EditItem = styled.div`
    
`

export const InputContent = styled.div`
    
`

export const InputDiv = styled.div`
    font-size: ${ToRem(17)};
    line-height: ${ToRem(25)};
    height: ${ToRem(25)};
`