import React, { Fragment} from 'react';
import { ContenWarp } from './style'
import './style.css'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Accordion, Toast } from 'antd-mobile';
import { Redirect } from 'react-router-dom'

const Help = (props) => {

    if(!localStorage.getItem('user_token')) {
        Toast.fail('登录失效，请先登录', 2, null, false)
        return <Redirect to='/login' />
    }
    return (<Fragment>
        <DvaHeader Left={true} Title={'常见问题'} zIndex={99}></DvaHeader>
        <ContenWarp>
            <WhiteSpace size="md"></WhiteSpace>
            <Accordion defaultActiveKey="-1" className="my-accordion">
                <Accordion.Panel header="平台提现时效是多久">
                    流水正常一般财务核验后24小时内转款到账
                </Accordion.Panel>
                <Accordion.Panel header="任务多久会自动释放" className="pad">
                 领取到任务后40分钟内未完成会自动回收,请亲们尽快完成任务哦
                </Accordion.Panel>
                <Accordion.Panel header="是否需要评价" className="pad">
                     亲们注意问题反馈中的问题,都是产生了售后需要亲们及时处理,包括评价内容
                </Accordion.Panel>
            </Accordion>
        </ContenWarp>
    </Fragment>)
}

export default Help