import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    userInfo: {},
    radioIndex: 1,
    birthday: null,
})

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_BIRTHDAY:
            return state.set('birthday', action.value)
        case actionTypes.SET_USER:
            return state.set('userInfo', action.obj)
        case actionTypes.SET_RADIO_INDEX:
            return state.set('radioIndex', action.value)
        default:
            return state   
    }
}   