import { actionTypes } from './index'
import { setUserInfo } from '../../home/store/actionCreatore'
import { Toast, Modal} from 'antd-mobile'
import { Axios } from '../../../../../common/Axios'
import { fromJS } from 'immutable'

const alert = Modal.alert;

export const handleKF = (id, account, name) => {
    debugger;
    return (dispatch)=>{
        console.log(id, account, name)
        let customer = {
            head: 'http://leek.lxiyuan.com:8660//imgs/head.png',
            "名称": account,
            "邮箱": '',
            "手机": account,
            "会员账号": account,
            "会员等级": '用户'
        }
        // console.log(customer)
        customer = JSON.stringify(customer)
        // console.log(customer)
        customer = encodeURIComponent(customer)
        // console.log(customer)
        let list = [{
            text: '取消操作', onPress: () => console.log(), style: 'color: "rgba(0,0,0,.65)"'
        },{ 
            text: '在线客服', onPress: () => LinkToPlace(`/chat.html?customer=${customer}`), style: 'default'
        }]
        alert('请选择客服前往聊天', '', list)
    }
}

const LinkToPlace = (path) => {
    window.location.href = path
}

export const handleInital = () => {
    return (dispatch)=>{
        // Axios('get','/GetIsCancel',{
        //     token: JSON.parse(localStorage.getItem('user_token')),
        // }).then(data =>{
        //     if(data.resultdata === 1){//要取消报单。
        //         dispatch(setIsCancel(true))
        //     }
        //     if(data.resultdata === 0){ //要申请报单。
        //         dispatch(setIsCancel(false))
        //     }
        // }).catch(err =>{
            
        // })
        Axios('get','/GetSystemSettingValueByCode',{
            code: 'MaxBDNum',
        }).then(data =>{
            let sum = parseInt(data.resultdata)
            dispatch(setMaxBDNum(sum))
        }).catch(err =>{
            
        })
        getUserInfo(dispatch)

        Axios('get','/GetSystemSettingValueByCode',{
            code: 'WeiXinService',
        }).then(data =>{
            if(data.type === 1){
                let code = data.resultdata
                dispatch(setwxList(code.split(',')))
            }else {
                Toast.fail(data.message, 2, null, false)
            }
            
        }).catch(err =>{
            
        })
    } 
}

const setwxList = (list) => ({
    type: actionTypes.SET_WXLIST,
    list: fromJS(list)
})

export const setWeiXinService = (value) => ({
    type: actionTypes.SET_WEIXINSERVICE,
    value
})

export const handleNotice = (val, userInfo) => {
    return (dispatch) => {
        // console.log(val, userInfo)
        Toast.loading('正在修改', 0, null, true)
        Axios('get','/ModifySMSNotice',{
            state: val === 1 ? 0 : 1
        }).then(data =>{
            userInfo.IsSMSNotice = val === 1 ? 0 : 1
            dispatch(setUserInfo(userInfo))
            Toast.info(`${val === 1 ? '关闭' : '开启'}成功`)
        }).catch(err =>{
            
        })
        // userInfo.IsSMSNotice = val === 1 ? 0 : 1
        // dispatch(setUserInfo(userInfo))
        // Toast.info(`${val === 1 ? '关闭' : '开启'}成功`)
    }
}

const getUserInfo = (dispatch) => {
    Axios('get','/GetBrusherInfo',{}).then(data =>{
        let sum = data.resultdata.TotalTask
        dispatch(setCenterBdNum(sum))
    }).catch(err =>{
        
    })
}

const setCenterBdNum = (value) => ({
    type: actionTypes.SET_CENTERBDNUM,
    value
})

const setMaxBDNum = (value) => ({
    type: actionTypes.SET_MAXBDNUM,
    value
})

export const changeApply = (IsCancel, BDNum = 0) => { 
    return (dispatch)=>{
        IsCancel ? (IsCancel = 1) : (IsCancel =  0)
        Axios('get', '/ApplyBD', {
            IsCancel,
            token: JSON.parse(localStorage.getItem('user_token')),
            bdnum: IsCancel ? null : BDNum
        }).then(data =>{
            if(IsCancel === 1 ){
                dispatch(setIsCancel(false))
            }else if(IsCancel === 0){
                dispatch(setIsCancel(true))
                dispatch(changeBDModal(false))
            }
            Axios('get','/GetIsCancel',{
                token: JSON.parse(localStorage.getItem('user_token')),
            }).then(data =>{
                if(data.resultdata === 1){//要取消报单。
                    dispatch(setIsCancel(true))
                }
                if(data.resultdata === 0){ //要申请报单。
                    dispatch(setIsCancel(false))
                }
            }).catch(err =>{
                
            })
            Axios('post', '/GetJFRecord', {  
                    rows: 5,
                    page: 1,  
                    sidx: "CreateTime",
                    sord: "DESC",
            }).then(data =>{
                console.log(data.resultdata)
                dispatch(setJFList(data.resultdata.list)) 
                dispatch(setJFPage(data.resultdata.page))
            }).catch(err =>{
                
            })
            getUserInfo(dispatch)
            Toast.info(data.message, 2, null, false)
        }).catch(err =>{
             
        })
        
    }
}

export const changeBDModal = value => ({
    type: actionTypes.SET_BDMODAL,
    value
})

export const changeBDNum = value => ({
    type: actionTypes.SET_BDNUM,
    value
})

const setIsCancel = value =>({
    type: actionTypes.SET_IS_CANCEL,
    value
})

const setJFList = (list)=>({
    type: actionTypes.SET_JF_LIST,
    list: fromJS(list)
})

const setJFPage = (obj)=>({
    type: actionTypes.SET_JF_PAGE,
    obj: fromJS(obj)
})
