import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    userMessage: JSON.parse(localStorage.getItem('user_info')),
    isCancel: false,   //isCancel为0时为申请报单，为1时为取消报单
    JFList: [],
    JFPage: {},
    maxBDNum: 1,
    BDModal: false,
    BDNum: 1,
    centerBDNum: 0,
    WeiXinService: '',
    wxList: []
}) 

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_WXLIST:
            return state.set('wxList', action.list) 
        case actionTypes.SET_WEIXINSERVICE:
            return state.set('WeiXinService', action.value) 
        case actionTypes.SET_CENTERBDNUM:
            return state.set('centerBDNum', action.value) 
        case actionTypes.SET_MEESSAGE:
            return state.set('userMessage', action.obj) 
        case actionTypes.SET_BDNUM:
            return state.set('BDNum', action.value)
        case actionTypes.SET_BDMODAL:
            return state.set('BDModal', action.value)
        case actionTypes.SET_MAXBDNUM:
            return state.set('maxBDNum', action.value) 
        case actionTypes.SET_IS_CANCEL:
            return state.set('isCancel', action.value)
        case actionTypes.SET_JF_LIST:
            return state.set("JFList", action.list)
        case actionTypes.SET_JF_PAGE:
            return state.set("JFPage", action.obj)
        default:
            return state    
    }
}   