export const setIsTuwen = (value) => ({
    type: actionTypes.SET_ISTUWEN,
    value
})

const FliterTuwen = (data) => {
    let int = false;
    if(data.problem.videoPath) {
        int = true  
    }
    for (let i = 0; i < data.myImgPath.length; i++) {
        const el = data.myImgPath[i];
        if(el) {
            int = true 
        }
    }
    return int
}

const Fliter = (data) =>{
    const el = data;
    // el.problem.videoPath = 'base'
    el.quesType = el.problem.ProblemType;
    el.quesSuggest = null;
    el.quesState  = el.problem.Status;
    el.quesInfo = el.problem.Description;
    el.taskTime = el.order.DoneTime; 
    el.taskId = el.order.GroupNum;
    el.shopName = el.order.ShopName;
    el.taskNum = el.order.BrusherAliAccount;
    el.taskKey = el.order.KeyWord;
    el.youProSug = el.problem.HandUserDetail;
    el.youProTime = el.problem.HandTime;
    el.platProSug = el.problem.PlatformDetail;
    el.platProTime = el.problem.PlatformHandTime;
    el.mustMoney = el.order.NeedPayMoney;
    el.payMoney = el.order.PayMoneyOld;
    el.myImgPath = [
        el.problem.ImgPath,
        el.problem.ImgPath2,
        el.problem.ImgPath3,
        el.problem.ImgPath4,
        el.problem.ImgPath5,
    ]
    return data
}


