export const submitAction = (obj, reasonText) => {
    return (dispatch)=>{
        Axios('get','/SaveProblem',{
            problemId: obj.problem.ProblemID,
            PicPath: obj.problem.BrusherImgPath,
            Remark: reasonText,
            token: JSON.parse(localStorage.getItem('user_token')),
        }).then(data =>{
            Toast.info(data.message, 2, null, false)
            if(data.type == 1){
                obj.youProSug = reasonText
                // console.log(obj)
                dispatch(setMsgList(obj))
                window.location.reload()
            }
            // let fliter =  Fliter(data.resultdata)
            
        }).catch(err =>{

        })
    }
}

