import React, { Fragment, useState, useEffect, useRef} from 'react';
import { ContenWarp, Tip } from './style'
import './style.css'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Modal, Toast } from 'antd-mobile';
import QRCode  from 'qrcode.react';
import { Axios } from '../../common/Axios';
import { Link, useHistory, Redirect } from 'react-router-dom'

const Invite = (props) => {
    let history = useHistory();
    const alert = Modal.alert; 
    const [popupShow, setPopupShow] = useState(false)
    const [user, setUser] = useState({})
    const [fenInfo, setFenInfo] = useState({})
    const inputRef = useRef(null)

    useEffect(()=>{
        getUserInfo(data => { 
            setUser(data) 
        })
        GetUnderCount(res => {
            setFenInfo(res.resultdata)
        })
    }, [])

    const GetUnderCount = (callback) => {
        Axios('get','/GetUnderCount',{}).then(res =>{
            callback(res)
        }).catch(err =>{
    
        })
    }

    const getUserInfo = (callback) => {
        Axios('get','/GetBrusherInfo',{}).then(data =>{
            localStorage.setItem('user_info',JSON.stringify(data.resultdata))
            callback(data.resultdata)
        }).catch(err =>{
    
        })
    }

    const handleCopy = () => {
        setTimeout(() => {
            Toast.info("链接已复制至剪贴板", 2, null, false)
            inputRef.current.select()
            document.execCommand("copy")
        }, 100)
    }

    const timeFiltter = () => {
        let time = new Date(new Date().getTime() + 1000*60*60*24*7);
        let m = time.getMonth()+1;
        let d = time.getDate();	
        return add0(m)+'月'+add0(d) + '日前'
    }

    const handlePlay = () => {
        let status = true
        debugger;
     if(user.EnabledMark == -1) {
        Toast.loading("正在加载邀请码信息...", 200, null, false) 
        return;
     }
        if(user.IsYWY!=2&&user.IsYWY!=1)
        {
            if(!user.IDCardNum)
            {
                status = false 
            }
        }

        if(!status) { 
            alert('邀请好友需要补充身份证信息', '', [
                { text: '取消', onPress: () => { }, style: 'default' },
                { text: '去补充', onPress: () => history.push('/basic') },
            ])
            return
        }
        setPopupShow(true)
    }

    const add0 = (m) => {return m<10?'0'+m:m }

    if(!localStorage.getItem('user_token')) {
        return <Redirect to='/login' />
    }

    return (<Fragment>
        <DvaHeader Left={true} Title={'推荐好友'} zIndex={99} background={'tran'}></DvaHeader>
        <input ref={inputRef} value={"http://" + window.location.host + "/#/register/" + user.EnCode} name="fileupload" onChange={() => { }} style={{ width: 1, height: 1, overflow: "hidden", zIndex: -1, opacity: 0, padding: 0, margin: 0, border: "none", position: "absolute", top: 0, left: 0 }}></input>
        <Modal
            popup
            visible={popupShow}
            onClose={()=>{ setPopupShow(false) }}
            animationType="slide-up"
            afterClose={()=>{ setPopupShow(false) }}
        >
            <div className='popup-warp' style={{ height: 170, padding: 10, display: 'flex', alignItems: 'center' }}>
                <div className='qr-img'>
                    <QRCode value={"http://" + window.location.host + "/#/register/" + user.EnCode} size={70}></QRCode>
                </div>
                <div className='qr-text'>
                    <div className='cell qr-invite'>
                        <span>我的邀请码： {user.EnCode}</span>
                        <span className='copy-btn' onClick={handleCopy}>复制</span>
                    </div>
                    <div className='cell qr-time'>
                        剩余可邀请人次<span className='active'> {user.CanInviteTimes}</span>
                    </div>
                    <div className='cell qr-time'>
                        邀请码<span className='active'>7天内（{timeFiltter()}）</span>有效
                    </div>
                    <div className='cell'>过期后回此页面重新获取分享</div>
                </div>
            </div>
        </Modal>
        <ContenWarp>
            {/* <Tip>
                {
                    user.IsYWY == 1 ? (`您是总代理: 每单抽成:${user.SelfMoney} , 下属推荐人每单默认提成: ${user.ExtraMoney}`) : (`您推荐的人每完成一单就会给您带来 ${user.ExtraMoney} 的提成`)
                }
            </Tip> */}
            <WhiteSpace size="md"></WhiteSpace>
            <div className='lg-title'>
                <img src="./imgs/text1.png" alt=""/>
            </div>
            <div className='box-warp'>
                <div className='cell' style={{width: '30%'}}>
                    <div className='cell-top'>
                        <span className='sum'>{fenInfo.全部粉丝}</span>人
                    </div>
                    <div className='cell-bottom'>已邀请买家</div>
                </div>
                <div className='cell border-left' style={{width: '40%'}}>
                    <div className='cell-top'>
                        <span className='sum'>{user.CanInviteTimes}</span>人
                    </div>
                    <div className='cell-bottom'>剩余可邀请人次</div>
                </div>
                <div className='cell border-left' style={{width: '30%'}}>
                    <div className='cell-top'>
                        <span className='btn' onClick={()=>{ history.push('/ext'); }}>查看详情</span>
                    </div>
                    <div className='cell-bottom invite'>总奖励明细</div>
                </div>
            </div>
            {/* <div className='lg-qr'>
                <QRCode value={"http://" + window.location.host + "/#/register/" + user.EnCode} size={100}></QRCode>
            </div> */}
            <div className='spec'>
                <p>1、用户邀请好友参加活动即可成为平台达人。</p>
                <p>2、邀请次数有限,请您务必邀请可靠的粉丝进来做单哦。</p>
                <p>3、邀请的人数做单越多,您的奖励越多,平台会根据您的粉丝量和底下人员的做单情况
定级并给予额外的奖励哦。</p>
            </div>
            <div className='recommend'>
                <p style={{ textAlign: "center" }}>  <img style={{ width: "50px", verticalAlign: "bottom" }} src='./imgs/hg.png' />  初级达人（成功邀请粉丝做单） </p>
                <p style={{ textAlign: "center", display: 'flex', justifyContent: 'space-around' }}>
                    <span><span style={{ color: "yellow", fontSize: "0.5rem" }}>{user.ExtraMoney}元</span><span style={{fontSize: "0.5rem"}}>/ </span>一级每单</span>
                    <span><span style={{ color: "yellow", fontSize: "0.5rem" }}>{user.SelfMoney}元</span><span style={{fontSize: "0.5rem"}}>/ </span>二级每单</span>
                </p>
            </div>
            {
                user.CanInviteTimes > 0 ? (<div className='bottom-warp' onClick={ handlePlay }>
                    <div className='bottom-btn' >邀请好友</div>
                </div>) : null 
            }
        </ContenWarp>
    </Fragment>)
}

export default Invite