import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { actionCreatore as MyactionCreatore } from '../my/store'
import { DatePicker, List, NoticeBar, Progress, Toast, Modal, SearchBar } from 'antd-mobile';
import URL from '../../../../common/Axios/URL_SET'
import { shopFilter } from '../../../../common/Js/textFilter'
import { Pagination } from 'antd';

import {
    Container,
    NotifyWarp,
    NotifyBox,
    IconBox,
    CarouselWarp,
    UserWarp,
    UserInfoBox,
    UserImg,
    InfoWarp,
    UserInfo,
    TaskSum,
    SearchWarp,
    InputWarp,
    InputBox,
    IconWarp,
    InputBtn,
    TermTime,
    TermWarp,
    TermCell,
    ListWarp,
    ListTitle,
    ListBannr,
    GrouupWarp,
    GrouupCell,
    CellLine,
    LineEnd,
    CellTitle,
    TitleLeft,
    TitleRight,
    CellStatus,
    StatusLeft,
    StatusDeg,
    StatusRight,
    StatusBtn,
    CellInfo,
    InfoCell,
    InfoSwitch,
    BottomBtn,
    ProgressWarp,
    ProTitle,
    ProText,
    ProLeft,
    ProRight,
    UserLever,
    PlaceOrder,
    Recevice,
    PaginationWarp,
    Tip,
    Affiche,
    Kefu,
    WWmodal,
    MobelPaginationWarp
} from './style'

import { jfFunction, differFn, differPriceFn, alllengthFn, UnokFn } from '../../../../common/Js/homeFlitter'

class Home extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {

        }
    }
    componentDidMount() {
        let nav = JSON.parse(sessionStorage.getItem('nav_data'))
        if (!nav) {
            let navData = {
                home: {
                    tab: 0,
                    index: 0
                },
                order: {
                    tab: 1,
                    index: 0
                },
                video: {
                    tab: 2,
                    index: 0
                },
                my: {
                    tab: 3,
                    index: 0
                },
                check: 0
            }
            sessionStorage.setItem('nav_data', JSON.stringify(navData))
            this.props.handleInital(navData, this.props.history)
        } else {
            this.props.handleInital(nav, this.props.history)
        }

    }

    render() {

        const { modelchangePno, total, page,
             handleWWsearch, selectWWCon,
              wwindex, handleWWitem, ChangeSearch, 
              wwsearchvalue, newwwList, wwModal, handleWWmodal, handleKF, handleAfficheShow, 
              AfficheText, AfficheShow, handleQuxiao, isGiftGain, newJfOrder, LinkTO, CarouselList,
               newUserInfo, history, handleNotify, TremIndex, handleTrem, changeTime, time, newList, 
               handleSwitch, handleRevise, searchValue, searchChange, 
               handleSearch, handleTogiftList, 
               changePno, pno, totalNum } = this.props
        const userInfo = newUserInfo.toJS()
        const alert = Modal.alert;
        const list = newList.toJS()
 
        const wwList = newwwList.toJS()
        const jfOrder = newJfOrder.toJS()
        const nowTimeStamp = Date.now();
        const now = new Date(nowTimeStamp);
        // console.log(list)
        const itemRender = (current, type, originalElement) => {
            if (type === 'prev') {
                return <a style={{ color: "#000", lineHeight: '16px' }}>上一页</a>;
            }
            if (type === 'next') {
                return <a style={{ color: "#000", lineHeight: '16px' }}>下一页</a>;
            }
            return originalElement;
        }

        const mobalitemRender = (current, type, originalElement) => {
            if (type === 'prev') {
                return <a style={{ color: "rgba(0, 0, 0, .65)", lineHeight: '16px' }}>上一页</a>;
            }
            if (type === 'next') {
                return <a style={{ color: "rgba(0, 0, 0, .65)", lineHeight: '16px' }}>下一页</a>;
            }
            return originalElement;
        }

        return (<Fragment>
            {/* <Tip>
                奖励规则:本金200以内奖励悠米3元,201-500奖励悠米4元
            </Tip>  */}
            {/* <Kefu onClick={() => { handleKF(userInfo.UserId, userInfo.Account, userInfo.RealName) }}>
                <div>
                    <div className='kefu-cell icon-img'>
                        <i className='iconfont'>&#xe620;</i>
                    </div>
                    <div className='kefu-cell'>客服</div>
                </div>
            </Kefu> */}
            {/* <a href='https://cdn.jsdelivr.net/gh/gaomingwww/jprb/jingtong2.apk' style={{ color: "blue", position: "fixed", left: "0px", bottom: "50px", zIndex: "9999", fontSize: "0.5rem" }}>
                点击下载精通APP,可实时提醒做单(限安卓)</a> */}
            <WWmodal style={{ display: (wwModal) ? 'block' : 'none' }}>
                <div className='box-warp-shad' onClick={() => { handleWWmodal(false) }}></div>
                <div className='box-warp'>
                    <div className='title dva-after-bottom'>选择旺旺号</div>
                    <SearchBar
                        value={wwsearchvalue}
                        placeholder="搜索旺旺号"
                        onSubmit={() => handleWWsearch(wwsearchvalue, userInfo, pno)}
                        onClear={() => { ChangeSearch('') }}
                        showCancelButton
                        onChange={(val) => { ChangeSearch(val) }}
                    />
                    <div className='content'>
                        <div className='list-warp'>
                            {
                                wwList.map((item, index) => {
                                    return (<div onClick={() => { handleWWitem(index + 1) }} key={index} className={index === (wwindex - 1) ? 'ww-item dva-after-bottom active' : 'ww-item dva-after-bottom'}>
                                        {
                                            index === (wwindex - 1) ? <i className='iconfont'>&#xe625;</i> : <i className='iconfont'>&#xe62e;</i>
                                        }
                                        <span>{item.AliAccount}</span>
                                    </div>)
                                })
                            }
                        </div>
                    </div>
                    <MobelPaginationWarp>
                        <div className='total-div'>共{total}条</div>
                        <Pagination defaultCurrent={1} total={total} defaultPageSize={5} pageSize={5} current={page} onChange={(pno) => { modelchangePno(pno, wwsearchvalue, userInfo) }} itemRender={mobalitemRender} simple />
                    </MobelPaginationWarp>
                    <div className='handle'>
                        <div className='btn cancel' onClick={() => { handleWWmodal(false); handleWWitem(null) }}>关闭</div>
                        <div className='btn confiram' onClick={() => { selectWWCon(userInfo, wwindex, wwList) }}>确认</div>
                    </div>
                </div>
            </WWmodal>
            <Container>
                <Affiche style={{ display: (AfficheShow) ? 'block' : 'none' }}>
                    <div className='box-warp-shad' onClick={() => { handleAfficheShow(false) }}></div>
                    <div className='box-warp'>
                        <div className='title'>公告</div>
                        <div className='content'>
                            <div dangerouslySetInnerHTML={{ __html: AfficheText }}></div>
                        </div>
                        <div className='confiram' onClick={() => { handleAfficheShow(false) }}>确认</div>
                    </div>
                </Affiche>

                <NotifyWarp onClick={handleNotify}>
                    <NotifyBox>
                        <IconBox>
                            <i className='iconfont'>&#xe749;</i>
                        </IconBox>

                        <CarouselWarp>
                            <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }} icon={null}>{CarouselList}</NoticeBar>
                        </CarouselWarp>
                    </NotifyBox>
                </NotifyWarp>
                <UserInfo className='dva-ellipsis' style={{fonSize:"15px"}}>{userInfo.RealName}（{userInfo.Mobile}）</UserInfo>
                             {/* <TaskSum className='dva-ellipsis'>账号：{userInfo.AliAccount}</TaskSum> */}
                            <TaskSum className='dva-ellipsis'>推荐人：{userInfo.RecomendUserName}  </TaskSum>

                 <SearchWarp>

                    {/* <InputWarp>
                        <InputBox>
                            <IconWarp>
                                <i className='iconfont davBold'>&#xe66d;</i>
                            </IconWarp>
                            <input type="text" placeholder='输入做单人姓名' value = {searchValue} onChange = {() => searchChange(this.searchInput.value)} 
                            ref={(input)=>{this.searchInput=input}} />
                           
                        </InputBox>
                        <InputBtn onClick = {() => handleSearch(searchValue, time)}>查询</InputBtn>
                    </InputWarp> */}
                    <TermTime>
                        <DatePicker
                            maxDate={now}
                            mode="date"
                            minDate={new Date(1970, 1, 1, 0, 0, 0)}
                            title="选择日期"
                            extra="请选择"
                            value={time}
                            onChange={(date) => { changeTime(date, searchValue, TremIndex) }}
                        >
                            <List.Item arrow="horizontal">任务日期</List.Item>
                        </DatePicker>
                    </TermTime>
                    {totalNum}     

                     <TermWarp>
                        <TermCell className = { TremIndex === 0 ? 'active' : '' } onClick = { ()=>{ handleTrem(time, searchValue, pno, 0) } }>待操作</TermCell>
                        <TermCell className = { TremIndex === 1 ? 'active' : '' } onClick = { ()=>{ handleTrem(time, searchValue, pno, 1) } }>已完成</TermCell>
                        <TermCell className = { TremIndex === 2 ? 'active' : '' } onClick = { ()=>{ handleTrem(time, searchValue, pno, 2) } }>其他</TermCell>
                    </TermWarp> 
                </SearchWarp>
                 

                <BottomBtn onClick={() => { LinkTO(userInfo, history) }} style={{ width: "100%", float: "left" }}>开始接单</BottomBtn>

                <ListWarp style={{ marginTop: 60 }}>
                     <ListBannr>
                        {
                            list.length ? null : (<div style={{ textAlign: 'center', fontSize: '25px', marginTop: 50, color: '#EC503F' }}>暂无数据</div>)
                        }
                    
                        {

                            list.map((item, index) => {
                                return (<>
                                    {
                                        <GrouupWarp key={index} onClick={() => { handleSwitch(index, list) }}>
                                            <GrouupCell className='dva-after-bottom'>
                                                <CellLine className={item.Switch ? '' : 'active'}>
                                                    <LineEnd></LineEnd>
                                                </CellLine>
                                                <CellTitle>
                                                    <TitleLeft>
                                                        <i className='iconfont'>&#xe736;</i>
                                                        <div className='title davBold'>
                                                            {
                                                                item.Status === 'OK' ? (item.title) : "--"
                                                            }
                                                            {
                                                                item.Status === 'OK' ? <span className='title davBold' style={{ marginLeft: 3, color: '#67C23A' }}>已完成</span>
                                                                    : (item.Status === 'WaitOper' ? (<span className='title davBold' style={{ marginLeft: 3, color: '#EC503F' }}>待操作 </span>)
                                                                        : (item.Status === 'Dispose' ? (<span className='title davBold' style={{ marginLeft: 3, color: '#EC503F' }}>已释放</span>)
                                                                            : (item.Status === 'Test' ? (<span className='title davBold' style={{ marginLeft: 3, color: '#EC503F' }}>等待做单</span>)
                                                                                : (item.Status === 'UserCancle' ? (<span className='title davBold' style={{ marginLeft: 3, color: '#E6A23C' }}>用户取消</span>) : null))))
                                                            }
                                                        </div>
                                                    </TitleLeft>
                                                    <TitleRight>
                                                        <b style={{ color: "blue" }}>{item.DoType}</b>
                                                        <div className='now'>应付:{item.Status === 'OK' ? (item.must_price) : "--"}</div>
                                                        <div className='new'>实付:{item.Status === 'OK' ? (item.fact_price) : "--"}</div>
                                                    </TitleRight>
                                                </CellTitle>
                                                {
                                                    item.Status === 'Dispose' ? (<CellStatus>
                                                        <div className='done_box'>
                                                            <div className="done_name" style={{ color: '#E6A23C' }}>被释放原因:
                                                                {
                                                                    item.Description ? item.Description : '--'
                                                                }
                                                            </div>
                                                        </div>
                                                    </CellStatus>) : null
                                                }
                                                <CellStatus>
                                                    <div className='done_box'>
                                                        <div className="done_name twinkle" style={{ color: '#EC503F' }}>可开始做单时间：
                                                            {
                                                                item.CreateDate ? item.CreateDate : '--'
                                                            }
                                                        </div>
                                                        {
                                                            item.Status === 'WaitOper' ? (<div className='quxiao_order'
                                                                onClick={() =>
                                                                    alert('每日取消任务超过三次后当日不可做单', '请选择取消做单原因', [
                                                                        { text: '取消操作', onPress: () => console.log(), style: 'color: "rgba(0,0,0,.65)"' },
                                                                        { text: '不想做了', onPress: () => { handleQuxiao(item.RelationId, '不想做了') } },
                                                                        { text: '关键词找不到', onPress: () => { handleQuxiao(item.RelationId, '关键词找不到') } },
                                                                    ])
                                                                }
                                                            >取消做单</div>) : null
                                                        }
                                                    </div>
                                                </CellStatus>
                                                <CellStatus style={{ margin: '5px 0' }}>
                                                    <div className='done_box'>
                                                        <div className="done_name">{item.BrusherName}</div>
                                                        <div className="done_name">推荐人:{item.RecommendUserName}</div>
                                                    </div>
                                                    <StatusLeft>
                                                        {
                                                            item.fact_price == 0 ? (<StatusDeg className='warning'>未识别付款价格</StatusDeg>) : (item.fact_price !== item.must_price ? (<StatusDeg className='danger'>差价 {differFn(item.fact_price, item.must_price)}</StatusDeg>) : null)
                                                        }
                                                        {
                                                            item.Status === 'BrusherCancle' ? (<StatusDeg className='danger'>任务取消</StatusDeg>) : (item.Status === 'TimeOut' ? (<StatusDeg className='danger'>任务超时</StatusDeg>) : null)
                                                        }
                                                        {
                                                            item.is_stop ? null : (<StatusDeg className='danger'>已停单</StatusDeg>)
                                                        }
                                                    </StatusLeft>
                                                </CellStatus>

                                                {
                                                    (item.Status != 'Dispose') ? (

                                                        item.is_stop ? null : <CellStatus style={{ margin: '5px 0' }}>
                                                            <StatusLeft>
                                                                <div className='done_box'>
                                                                    <div className="done_name" style={{ color: '#ec503f' }}>
                                                                        Tips:【若此单已完成，请勿退款,不影响余额】
                                                                    </div>
                                                                </div>
                                                            </StatusLeft>
                                                        </CellStatus>
                                                    ) : (<CellStatus style={{ margin: '5px 0' }}>
                                                        <StatusLeft>
                                                            <div className='done_box'>
                                                                <div className="done_name" style={{ color: '#ec503f' }}>
                                                                    Tips:【若此单已完成，请退款】
                                                                </div>
                                                            </div>
                                                        </StatusLeft>
                                                    </CellStatus>)
                                                }

                                                <CellInfo className={item.Switch ? '' : 'active'}>
                                                    <InfoCell><span className='dva-bold2'>店铺名：</span>
                                                        {
                                                            item.Status === 'OK' ? (item.title_info) : (shopFilter(item.title_info))
                                                        }
                                                    </InfoCell>
                                                    <InfoCell><span className='dva-bold2'>旺旺名：</span>{item.wangwnag}</InfoCell>
                                                    <InfoCell><span className='dva-bold2'>
                                                        实付：</span>{item.fact_price}
                                                        <StatusRight>
                                                            {/* <StatusBtn >  {item.ordertype}</StatusBtn> */}
                                                            {
                                                                item.Status == 'OK' ? (<StatusBtn onClick={(e) => { handleRevise(e, history, item) }}>
                                                                    修改任务
                                                                    <i className='iconfont'>&#xe603;</i>
                                                                </StatusBtn>) : (item.is_stop ? (item.Status === 'WaitOper' || item.Status === 'Test') ? (<StatusBtn onClick={(e) => { handleRevise(e, history, item) }}>
                                                                    {item.IsSure == 1 ? "继续做单>>" : "确认做单(确认成功后任务不会提前释放)"}

                                                                </StatusBtn>) : null : null)
                                                            }
                                                        </StatusRight>
                                                    </InfoCell>
                                                    <InfoCell><span className='dva-bold2'>应付金额：</span>{item.must_price}</InfoCell>
                                                    <InfoCell><span className='dva-bold2'>完成时间：</span>{item.time}</InfoCell>
                                                    <InfoSwitch>
                                                        收起
                                                        <i className='iconfont'>&#xe669;</i>
                                                    </InfoSwitch>
                                                </CellInfo>
                                            </GrouupCell>
                                        </GrouupWarp>
                                    }
                                </>)
                            })
                        }
                    </ListBannr>
                </ListWarp>

            </Container>
        </Fragment>)
    }
}

const mapStateToProps = (state) => {
    return {
        TremIndex: state.getIn(['home', 'TremIndex']),
        time: state.getIn(['home', 'time']),
        newList: state.getIn(['home', 'list']),
        newUserInfo: state.getIn(['home', 'userInfo']),
        CarouselList: state.getIn(['home', 'carouselList']),
        searchValue: state.getIn(['home', 'searchValue']),
        newJfOrder: state.getIn(['home', 'jfOrder']),
        isGiftGain: state.getIn(['home', 'isGiftGain']),
        pno: state.getIn(['home', 'pno']),
        page: state.getIn(['home', 'page']),
        total: state.getIn(['home', 'total']),
        totalNum: state.getIn(['home', 'totalNum']),
        AfficheShow: state.getIn(['home', 'AfficheShow']),
        AfficheText: state.getIn(['home', 'AfficheText']),
        wwModal: state.getIn(['home', 'wwModal']),
        newwwList: state.getIn(['home', 'wwList']),
        wwsearchvalue: state.getIn(['home', 'wwsearchvalue']),
        wwindex: state.getIn(['home', 'wwindex']),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleWWsearch(val, userInfo, pno) {
            dispatch(actionCreatore.changeWWlist(val, userInfo, pno))
        },
        selectWWCon(userInfo, wwindex, wwList) {
            console.log(wwindex)
            if (wwindex && wwList[wwindex - 1]) {
                Toast.info("选择的旺旺号为：" + wwList[wwindex - 1].AliAccount)
                dispatch(actionCreatore.LinkToPlace0(wwList[wwindex - 1].AliAccount, true, userInfo))
                dispatch(actionCreatore.setwwIndex(null))
                dispatch(actionCreatore.setWWmodal(false))
            } else {
                Toast.fail('请先选择旺旺号')
            }
        },
        handleWWitem(index) {
            dispatch(actionCreatore.setwwIndex(index))
        },
        ChangeSearch(val) {
            dispatch(actionCreatore.setwwsearchvalue(val))
        },
        handleWWmodal(val) {
            dispatch(actionCreatore.setWWmodal(val))
        },
        handleKF(id, account, name) {
            // console.log(Message)
            // return
            dispatch(MyactionCreatore.handleKF(id, account, name))
        },
        handleAfficheShow(val) {
            dispatch(actionCreatore.setAfficheShow(val))
        },
        handleQuxiao(id, text) {
            dispatch(actionCreatore.quxiaoTask(id, text))
        },
        modelchangePno(pno, val, userInfo) {
            dispatch(actionCreatore.changeWWlist(val, userInfo, pno))
        },
        changePno(pno, searchValue, time, TremIndex) {
            // console.log(pno)
            dispatch(actionCreatore.changePno(pno, searchValue, time, TremIndex))
        },
        handleTogiftList(history) {
            history.push('/jflist')
        },
        handleSearch(value, time) {
            dispatch(actionCreatore.searchAction(value, time))
        },
        LinkTO(userInfo, history) {
            // console.log(window.location.host + '/#/placeorder/0')
            dispatch(actionCreatore.changeLinkTo(userInfo, history))
        },
        handleRevise(e, history, item) {
             if (item.ordertype == "抖音") {
                window.location.href = '/douyin/dody.html?relationId=' + item.id;
            } else {
                if (item.DoType == "提问单") {
                    window.location.href = '/geridan/tiwen.html?relationId=' + item.id;
                }
                if (item.DoType == "评价投诉单") {
                    window.location.href = '/geridan/pjts.html?relationId=' + item.id;
                } else

                    if (item.DoType == "浏览单") {
                        // if(item.CreateDate>item.PayEndDay)
                        {
                            window.location.href = '/geridan/dodan.html?relationId=' + item.id;
                        }

                    } else {

                        e.nativeEvent.stopImmediatePropagation()
                        e.stopPropagation()
                        history.push('/placeorder/' + item.id)
                    }
            }
        },
        handleSwitch(index, list) {
            dispatch(actionCreatore.setListSwitch(index, list))
        },
        changeTime(date, searchValue, TremIndex) {
            dispatch(actionCreatore.timeAction(date, searchValue, TremIndex))
        },
        handleTrem(date, searchValue, pno, val) {
            dispatch(actionCreatore.TremAction(date, searchValue, pno, val))
        },
        handleNotify() {
            // console.log('跳转通知页面')
        },
        handleInital(nav, history) {
            dispatch(actionCreatore.handleInital(nav, history))
        },
        searchChange(value) {
            dispatch(actionCreatore.setSearchInput(value))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);