import React, { useState, useEffect, useRef } from 'react';
import { useHistory, Link, withRouter, Redirect } from 'react-router-dom'
import { Toast, Modal } from 'antd-mobile';
import { Axios } from '../../common/Axios'
import URL from '../../common/Axios/NEW_URL'
import { DvaHeader } from '../../common/Template'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse
} from './style'

const ChangePassNext = (props) => {
    let history = useHistory();
    const alert = Modal.alert;
    useEffect(() => {
        let phone_obj = JSON.parse(sessionStorage.getItem('jingping_revise_phone'))
        setphone(phone_obj.phone)
        setcode(phone_obj.code)
    }, [])

    const passInput = useRef(null)
    const opassInput = useRef(null)
    const [active, setactive] = useState(false)
    const [phone, setphone] = useState('')
    const [pass, setpass] = useState('')
    const [opass, setopass] = useState('')
    const [code, setcode] = useState('')
    const [ispassCon, setispassCon] = useState(false)

    const passCompare = (pass, opass) => {
        if(pass && opass) {
            setactive(true)
        }else {
            setactive(false)
        }
        if (pass === opass) {
            setispassCon(false)
        } else {
            setispassCon(true)
        }
    }

    const changePass = () => {
        let val = passInput.current.value
        setpass(val)
        passCompare(val, opass)
    }

    const changeOpass = () => {
        let val = opassInput.current.value
        setopass(val)
        passCompare(pass, val)
    }


    const handleStart = () => {
        if(active) {
            if(!pass) {
                Toast.fail('请输入密码', 2, null, false)
                return
            }
            passCompare(pass, opass)
            if (pass !== opass) {
                Toast.fail('两次输入密码不一致', 2, null, false)
                return
            }
            Toast.loading('正在修改...', 0, null, true); 
            // setTimeout(()=>{
            //     Toast.hide()
            //     Toast.success('密码修改成功，请重新登录', 2, null, false);
            //     sessionStorage.removeItem('jingping_revise_phone')
            //     localStorage.removeItem('user_token')
            //     localStorage.removeItem('user_info')
            //     setTimeout(()=>{
            //         history.push('/login')
            //     }, 2000)
            // }, 2000)
            Axios('get', '/UpdatePwd', {
                mobile: phone,
                password: pass,
                msgCode: code
            }).then(data => {
                Toast.hide()
                if (data.type == 1) {
                    Toast.hide()
                    Toast.success('密码修改成功，请重新登录', 2, null, false);
                    sessionStorage.removeItem('jingping_revise_phone')
                    localStorage.removeItem('user_token')
                    localStorage.removeItem('user_info')
                    setTimeout(()=>{
                        history.push('/login')
                    }, 2000)
                } else {
                    Toast.hide()
                    Toast.fail(data.message, 2, null, false);
                }
            }).catch(err => {
                Toast.hide()
            })
        }
    }

    return (<>
        <DvaHeader Left={true} Title={'设置新密码'}></DvaHeader>
        <Container>
            <LoginWarpper>
                <LoginBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe60f;</i>
                            <input placeholder='请输入密码' type='password' value={pass} ref={passInput} onChange={changePass} />
                        </NavInput>
                    </InputBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe60f;</i>
                            <input placeholder='请确认密码' type='password' value={opass} ref={opassInput} onChange={changeOpass} />
                            {
                                ispassCon ? (<span className='inputTpye active2 help-color'>
                                    <i className='iconfont'>&#xe6a7;</i>
                                            密码不一致
                                </span>) : null
                            }
                        </NavInput>
                    </InputBox>
                    <StartUse className={active ? 'active' : ''} onClick={handleStart} >提交</StartUse>

                </LoginBox>
            </LoginWarpper>
        </Container>
    </>)
}

export default withRouter(ChangePassNext)