import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Toast, Modal } from 'antd-mobile';
import { Redirect } from 'react-router-dom'
import { SwiperList, PathImg, PathImgBig } from '../../common/Js/orderFiltter'
import { Input } from 'antd';
import WxImageViewer from 'react-wx-images-viewer';

import {
    Container,
    Tip,
    InfoCell
} from './style'

import { DvaHeader } from '../../common/Template'

class ZFBCard extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            popupShow: false
        }
    }
    componentDidMount(){
        this.props.handleIntal()
    }

    popupCloose() {
        this.setState({'popupShow': false})
    }

    handlePopup() {
        this.setState({'popupShow': true})
    }

    render() { 
        const defaultImg = 'data:image/png;base64,'

        const { handleSubmit, viewerClose, handleUpload, handleViewer, changeFile, newInfo, select_img, viewerShow } = this.props
        const info = newInfo.toJS()
        console.log(info)

        const userInfo = JSON.parse(localStorage.getItem('user_info'))

        let token = localStorage.getItem('user_token')
        if(!token){
            Toast.fail('用户信息过期，请重新登录', 1, null, true)
            return <Redirect to="/login" />
        }else{
            return (<Fragment>
                <DvaHeader Left={true}  Title='支付宝信息'></DvaHeader>

                {
                    viewerShow ? (<WxImageViewer onClose={viewerClose} urls={SwiperList(select_img)} index={0} />) : null
                }

                <Input
                    type='file'
                    id='uploadInput'
                    accept='image/*'
                    style={{ display: 'none' }}
                    ref={(input) => { this.filrInput = input }}
                    onChange={(input) => { changeFile(input, info) }}
                ></Input>

                <Modal
                    popup
                    visible={this.state.popupShow}
                    onClose={() => { this.popupCloose() }}
                    animationType="slide-up"
                    afterClose={() => { this.popupCloose() }}
                >
                    <div className='popup-warp' style={{ height: '80vh' }}>
                        <img src="/imgs/ppp_ggg.png" alt="" style={{ width: '100%' }} />
                    </div>
                </Modal>

                <Container>
                    <Tip>提交<span> 支付宝信息 </span>和<span> 淘宝账号信息 </span>后，待后台审核通过即可做单</Tip>
                    <InfoCell>
                        <div className='flex-start'>
                            <span className='dva-bold2' style={{ fontSize: 15}}>上传支付宝信息照片</span>
                            <span style={{ color: '#409EFF', marginLeft: '15px', fontWeight: 700 }} onClick={()=>{ this.handlePopup() }}>如何获取支付宝信息？</span>
                        </div>
                        <div>
                            <div className='upload-warp'>
                                <div onClick={() => { handleUpload() }} className='upload-img-box'>
                                    <div className='img-box'>
                                        <img src={ info.img_url ? (PathImg(info.img_url)) : defaultImg } alt="" onClick={() => { return false }}/>
                                    </div>
                                </div>
                                {
                                    info.img_url ? (<div className='big-box' onClick={() => { handleViewer(PathImgBig(info.img_url, defaultImg)) }}>查看大图</div>) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                }
                            </div>
                        </div>
                    </InfoCell>  

                    <div className='bottom-warp'>
                        <div className='bottom-btn' onClick={() => { handleSubmit(info) }} >提交信息</div>
                    </div>

                </Container>
            </Fragment>)
        }
    }
}

const mapStateToProps = (state)=>{
    return {
        newInfo: state.getIn(['ZFB','info']),
        select_img: state.getIn(['ZFB','select_img']),
        viewerShow: state.getIn(['ZFB','viewerShow']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleSubmit(info) {
            dispatch(actionCreatore.handleSubmit(info))
        },
        viewerClose() {
            dispatch(actionCreatore.setViewerShow(false))
        },
        handleViewer(img) {
            dispatch(actionCreatore.viewerAction(img))
        },
        handleIntal() {
            dispatch(actionCreatore.intalAction())
        },
        handleUpload(index) {
            document.getElementById('uploadInput').click()
        },
        changeFile(input, info) {
            // console.log(info)
            dispatch(actionCreatore.UploadFileAction(input, info))
        }
    }
}
    
export default connect(mapStateToProps, mapDispatchToProps)(ZFBCard);