import React, { Fragment, useState, useEffect, useRef } from 'react';
import { ContenWarp } from './style'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Button, Modal, Toast } from 'antd-mobile';
import { Link, useHistory  } from 'react-router-dom'
import { stringToDate, getQueryVariable } from '../../common/Js/timeFiltter'


const Tixian = (props) => {
    const alert = Modal.alert;
    let history = useHistory();

    useEffect(()=> {
        let address = null
        address = getQueryVariable('dingweiinfo')
        if(address) {
            address = decodeURI(address)
        }else {
            address = ''
        }
        if(address) {
            sessionStorage.setItem('register_address', address )
            setTimeout(()=>{
                history.push('/re')
            }, 0)
        }else {

        }
    }, [])

    return (<Fragment>
        <DvaHeader Left={true} Title={'定位'} zIndex={99}></DvaHeader>
        <ContenWarp>

        </ContenWarp>
    </Fragment>)
}

export default Tixian