import { actionTypes } from './index'
import { Toast } from 'antd-mobile';
import { Axios } from '../../../common/Axios';
import { fromJS } from 'immutable';

const setLoginPass = (value)=>({
    type: actionTypes.LOGIN_PASS,
    value
})

const setLoginUser = (value)=>({
    type: actionTypes.LOGIN_USER,
    value
})

const setPassBlur = ()=>({
    type: actionTypes.PASS_BLUR
})

const setUserBlur = ()=>({
    type:actionTypes.USER_BLUR
})

const setStartOn = ()=>({
    type: actionTypes.START_ON
})

const setStartOff = ()=>({
    type: actionTypes.START_OFF
})

const setPact = (value)=>({
    type: actionTypes.PACT_CHECKBOX,
    value
})

const changeState = (user, pass, dispatch)=>{
    if( user && pass ){
        dispatch(setStartOn())
    }else{
        dispatch(setStartOff())
    }
}

const setInputType = (value)=>({
    type: actionTypes.INPUT_TYPE,
    value
})

const setMsg = (value) => ({
    type: actionTypes.MSG,
    value
})

export const setIsLogin = (value)=>({
    type: actionTypes.IS_LOGIN,
    value
})

export const changeStartState = (user, pass, pact, isActive, inpuType, isUser, isPass)=>({
    type: actionTypes.START_STATE,
    user,
    pass,
    pact,
    isActive,
    inpuType,
    isUser,
    isPass
})

export const PassFocusAction = ()=>({
    type: actionTypes.PASS_FOUCS
})

export const PassBlurAction = (value)=>{
    return (dispatch)=>{
        if(!value){
            dispatch(setPassBlur())
        }
    }
}

export const UserFocusAction = (value)=>({
    type: actionTypes.USER_FOUCS
})

export const PactAction = (user, pass, pact)=>{
    return (dispatch)=>{
        dispatch(setPact(pact))
        changeState(user, pass, pact , dispatch)
    }
}

export const UserBlurAction = (value)=>{
    return (dispatch)=>{
        if(!value){
            dispatch(setUserBlur())
        }
    }
}

export const changeLoginPassAction = (user, pass)=>{
    return (dispatch)=>{
        dispatch(setLoginPass(pass))
        changeState(user, pass, dispatch)
    }
}

export const changeLoginUserAction = (user, pass)=>{
    return (dispatch)=>{
        dispatch(setLoginUser(user))
        changeState(user, pass, dispatch)
    }
}

export const InputTypeAction = (inputType)=>{
    return (dispatch)=>{
        if(inputType==='password'){
            dispatch(setInputType('text'))
        }
        if(inputType==='text'){
            dispatch(setInputType('password'))
        }
    }
}

const setSelectBank = (value) => ({
    type: actionTypes.SET_SELECT_BANK,
    value
})

const setSelectBankIndex = (value) => ({
    type: actionTypes.SET_SELECT_BANK_INDEX,
    value
})

export const BankAction = (value, bankList) => {
    return (dispatch)=>{
        let val = bankList[value].label
        console.log(val)
        dispatch(setSelectBank(val))
        dispatch(setSelectBankIndex(value))
    }
}

export const loginSubmitAction = (user, pass, bank_type, history)=>{
    return (dispatch)=>{
        console.log(user, pass, bank_type)
        if(bank_type) {
            dispatch(setMsg(''))
            Axios('post', '/UpdateBrusherInfo', { 
                BankUserName: user,
                BankCardNum: pass,
                BankCategory: bank_type
            }).then(data => {
                Toast.hide()
                if(data.type == 1) {
                    Toast.success(data.message , 1);
                    Axios('get','/GetBrusherInfo',{
                        token: JSON.parse(localStorage.getItem('user_token')),
                    }).then(data =>{
                        localStorage.setItem('user_info',JSON.stringify(data.resultdata))
                        dispatch(setLoginUser(data.resultdata.BankUserName))
                        dispatch(setSelectBank(data.resultdata.BankCategory))
                        dispatch(setSelectBankIndex(1))
                        dispatch(setLoginPass(data.resultdata.BankCardNum))
                    }).catch(err =>{
            
                    })
                    setTimeout( ()=> {
                        history.push('/userInfor')
                    }, 1000)
                }else {
                    Toast.info( data.message, 1);
                }
            }).catch(err => {
                
            })
        }else{
            setTimeout(()=> {
                Toast.hide()
                Toast.info('请先选择银行类型', 1);
            }, 10)
        }
    }
}

export const intalAction = () => {
    return (dispatch) => {
        Axios('get', '/GetDataDic', { 
            code: 'JS_BankCategory'
        }).then(data => {
            let arr = data.resultdata
            for (let i = 0; i < arr.length; i++) {
                const el = arr[i];
                el.label = el.F_ItemName
                el.value = i
            }
            dispatch(setBankList(arr))
        }).catch(err => {

        })
        Axios('get','/GetBrusherInfo',{
            token: JSON.parse(localStorage.getItem('user_token')),
        }).then(data =>{
            localStorage.setItem('user_info',JSON.stringify(data.resultdata))
            // console.log(data.resultdata)
            dispatch(setLoginUser(data.resultdata.BankUserName))
            dispatch(setSelectBank(data.resultdata.BankCategory))
            dispatch(setSelectBankIndex(1))
            dispatch(setLoginPass(data.resultdata.BankCardNum))
        }).catch(err =>{

        })
    }
}

const setBankList = (list) => ({
    type: actionTypes.SET_BANK_LIST,
    list: fromJS(list)
})