export const MesListRequest = (int, pageNum, pageIndex) => {
    return (dispatch) => {
        Axios('get', '/GetUserMessage', {
            IsAll: int,
            token: JSON.parse(localStorage.getItem('user_token')),
            nowPage: pageIndex
        }).then(data =>{
            console.log(data.resultdata.page)
            if(int === 0) {
                let offList = orderFilter(data.resultdata.list)
                let off  = data.resultdata.page.records
                dispatch(setInitalOffList(offList))
                dispatch(setListData(offList))
                roderCountAuto(dispatch, setTabs, 0, off)
                dispatch(dispatch(setBaga(off)))
                sessionStorage.setItem('messageSum', off)
            }else {
                let onList = orderFilter(data.resultdata.list)
                dispatch(setInitalOnList(onList))
                dispatch(setListData(onList))
            }
            dispatch(setlistTotal(data.resultdata.page.records))
            dispatch(setlistPage(pageNum))
            dispatch(setlistPno(pageIndex))
            dispatch(setInital(int, true))
            dispatch(setTabIndex(int, false))
        }).catch(err =>{
        })
    }
}

const orderFilter = (data) =>{
    for (let i = 0; i < data.length; i++) {
        const el = data[i];
        el.id = el.ProblemID;
        el.applyReas = el.ProblemType;
        if(el.Status === '待处理') {el.isPlatPro = 0;} else {el.isPlatPro = 1;} 
        if(el.BrusherStatus === '已处理') {el.isYouPro = 1;} else {el.isYouPro = 0;} 
        el.applyData = el.CreateTime;
        el.proRes = el.Description;
    }
    return  data
}

const setTabs = (list) => ({
    type: actionTypes.SET_TABS,
    list: fromJS(list)
})

const setInital = (index, isloading)=>({
    type: actionTypes.SET_TABINDEX,
    index: parseInt(index),
    isloading,
})

const setTabIndex = (index, animite) => ({
    type: actionTypes.SET_ANIMITE,
    index: parseInt(index),
    animite,
})

const setListData = (list) =>({
    type: actionTypes.SET_LIST_DATA,
    list: fromJS(list)  
})

const setlistTotal = (value) =>({
    type: actionTypes.SET_LIST_TOTAL,
    value 
})

const setlistPage = (value) =>({
    type: actionTypes.SET_LIST_PAGE,
    value 
})

const setlistPno = (value) =>({
    type: actionTypes.SET_LIST_PNO,
    value 
})


export const changeTab = (index, nav, onList, offList, listTotal, listPage, listPno) => {
    return (dispatch) => {
        // nav.order.index = index
        console.log('切换' + index)
        if(index === 0 ){
            // dispatch(setListData(offList))
            dispatch(MesListRequest(0, 5, 1))
        }else if(index === 1){
            dispatch(MesListRequest(1, 5, 1))
            // dispatch(setListData(onList))
        }
        // sessionStorage.setItem('nav_data', JSON.stringify(nav))
        dispatch(setTabIndex(index, true))
        setTimeout(()=>{
            dispatch(setTabIndex(index, false))
        }, 500)
    }
}

