import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

const twinkles = keyframes`
    0%{
        color: ${MV.dva_color};
        transform: scale(1)
    }
    50%{
        color: #409eff;
        transform: scale(0.9)
    }
    100%{
        color: ${MV.dva_color};
        transform: scale(1)
    }
`
export const ContenWarp = styled.div`
    margin-top: ${ToRem(44)};
    font-size: ${ToRem(14)};
    .card-title {
        border-bottom: 2px solid ${MV.dva_color};
        .h1 {
            padding: 5px 0;
            line-height: 30px;
            text-align: center;
            font-size: 16px;
            color: rgba(0,0,0,0.85);
        }
        .card-cell {
            padding: 0 8px;

            .num {
                color: #006d75;
            }
        }
    }
    .tixian-body {
        width: 92%;
        margin: 0 auto;
        .msg-title {
            font-size: 16px;
            font-weight: 600;
            color: ${MV.dva_color};
        }
        .msg-p {
            &.twinkle {
                transition: all 2s;
                animation-duration: 2s;
                animation-iteration-count: infinite;
                animation-name: ${twinkles};
            }
        }
        .wechat-title {
            font-size: 16px;
            font-weight: 600;
            color: ${MV.fz_2};
        }
        .wechat-info {
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 60px;
                height: 60px;
            }
            .wechat-name {
                margin-left: 10px;
            }
        }
        .card-cell {
            display: flex;
            align-items: center;
            .big {
                font-size: 20px;
            }
            .card {
                margin-left: 8px;
                .num {
                    color: #c41d7f;
                }
            }
        }
        .input-box {
            display: flex;
            align-items: center;
            border-bottom: 1px solid rgba(0,0,0,.2);
            .icon {
                font-size: 25px;
                line-height: 40px;
                font-weight: 700;
                width: 30px;
                text-align: center;
            }
            input {
                border: none;
                line-height: 40px;
                padding-left: 16px;
                font-size: 16px;
                width: calc(100% - 34px);
            }
        }
        .money-box {
            display: flex;
            align-items: center;
            justify-content: space-between;
            line-height: 3;
            .left {

            }
            .right {
                color: #1890ff;
                display: flex;
                .tixian-info {
                    margin-right: 20px;
                }
            }
        }
        .btn-box {
            width: 95%;
            margin: 0 auto;
        }
        .tixian-info {
            color: #1890ff;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            line-height: 30px;
        }
    }
`