export const Affiche = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.65);
    z-index: 1000;
    font-size: 16px;
    line-height: 1.4;
    .box-warp-shad {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .box-warp {
        z-index: 1;
        width: 80%;
        margin: 0 auto;
        margin-top: 40px;
        border-radius: 8px;
        overflow: hidden;
        padding: 15px;
        background-color: #EB525D;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        .title {
            text-align: center;
            font-weight: 700;
            font-size: 18px;
            color: #FCF050;
            margin-bottom: 15px;
        }
        .content {
            color: #fff;
            font-size: 16px;
            p {
                margin-bottom: 5px;
                span {
                    color: #FCF050;
                }
            }
        }
        .confiram {
            margin-top: 20px;
            text-align: center;
            width: 100%;
            padding: 10px 0;
            color: #E8435A;
            font-size: 16px;
            background-color: #FCF050;
            border-radius: 8px;
            overflow: hidden;
        }
    }
`

