import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../../../../../common/Js/remToPx'
import {MV} from '../../../../../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh);
    background-color: #FAF8FA;
    margin-top: ${ToRem(44)};
`

export const MyTop = styled.div`
    
    
`

export const CellBox = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: ${ToRem(5)} 0;
    background: #fff;
    margin-top: ${ToRem(1)}
    &>div {
        width: 100%;
    }
    .iconfont {
        font-size: ${ToRem(25)};
        font-weight: 700;
        padding-left: ${ToRem(10)};
        color: ${MV.dva_color};
    }
    .am-list-item {
        padding-left: ${ToRem(10)} !important;
    }
    .am-list-content {
        font-weight: 700 !important;
        color: #303133 !important;
    }
`

export const MyMoney = styled.div`
    background-color: rgb(250,248,250);
    padding: ${ToRem(20)} ${ToRem(10)};
`

export const MyList = styled.div`
    background-color: rgb(250,248,250);
    padding: 0 ${ToRem(10)};
    padding-top: ${ToRem(10)};
    .my-radio {
        padding: 2.5px;
        border: 1px solid #ccc;
        border-radius: 50%;
        margin-right: 5px;
    }
`
  
export const MyWarp = styled.div`
   
`

export const OutLogin = styled.button`
    background-color: #EC503F;
    color: #fff;
    width: 90%;
    margin: ${ToRem(220)}  0  0  ${ToRem(19)};
    font-size: ${ToRem(14)};
    border-radius: ${ToRem(20)};
    border-color: #FAF8FA;
    border: ${ToRem(1)} solid transparent;
    height: ${ToRem(42)};
    line-height: ${ToRem(42)};
    font-size: ${ToRem(16)};
`