import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'
import { fz_2 } from '../../common/Css/motive1'

export const ContenWarp = styled.div`
    margin-top: ${ToRem(44)};
    font-size: ${ToRem(14)};
    .list {
        .li {
            border-bottom: 1px solid rgba(0,0,0,0.2);
            padding: 10px;
            .cell {
                display: flex;
                align-items: center;
                .label {
                    color: rgba(0,0,0,.85);
                }
                .content {
                    margin-left: 5px;
                    &.default {
                        color: #006d75;
                    }
                    &.time {
                        color: rgba(0,0,0,.55);
                    }
                    &.money {
                        color: ${MV.dva_color};
                    }
                }
            }
        }
    }
`


export const PaginationWarp = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
    .total-div {
        padding: 0 ${ToRem(8)};
        color: rgba(0, 0, 0, .65);
        font-size: ${ToRem(14)};
        line-height: ${ToRem(16)};
    }
    .ant-pagination-simple-pager {
        color: rgba(0, 0, 0, .65) !important;
        input {
            color: rgba(0, 0, 0, .65) !important;
        }
    }
`