import React, { Fragment, useEffect } from 'react';
import { GlobalStyle } from './style';
import { IconFontStyle } from './static/iconfont/iconfont'
import { Provider } from 'react-redux'
import store from './store'
import { Redirect } from 'react-router-dom'
import { HashRouter as Router, Route } from 'react-router-dom'
//  HashRouter  BrowserRouter


import Main from './pages/main'
import Login from './pages/login'
import Register from './pages/register'
import UserInfor from './pages/main/page/my/pages/userInfor'
import Ext from './pages/main/page/my/pages/ext'
import MobileRevise from './pages/mobile-revise'
import MobileReviseNext from './pages/mobile-revise/mobile-revise-next'
import TaskInfo from './pages/task-info' 
import PlaceOrder from './pages/place-order'
import GiftList from './pages/gift-list'
import BankCardRevise from './pages/bank-card'
import Tixian from './pages/tixian'
import TixianInfo from './pages/tixian-info'
import UserMoney from './pages/user-money'
import ZFBCard from './pages/zfb-card'
import TaobaoCard from './pages/taobao-card'
import UserBasic from './pages/user-basic'
import JFList from './pages/jf-list'
import Help from './pages/help'
import Invite from './pages/invite'
import Addww from './pages/add-ww'
import Dingwei from './pages/dingwei'
import ChnagePass from './pages/change-pass'
import ChnagePassNext from './pages/change-pass-next'

import 'antd/dist/antd.css';

import axios from 'axios'
import  URL  from './common/Axios/NEW_URL'

function App() {
  const routes = [
    { path:'/main', Component: Main, exact: false },
    { path:'/login', Component: Login, exact: false },
    { path:'/register/:code', Component: Register, exact: false },
    { path: '/userInfor' , Component: UserInfor ,exact:false},
    { path: '/ext' , Component: Ext ,exact:false},
    { path:'/mobilerevise' , Component: MobileRevise , exact:false},
    { path:'/mobilerevisenext/:user' , Component: MobileReviseNext , exact:false},
    { path:'/taskinfo/:id' , Component: TaskInfo , exact:false}, 
    { path:'/placeorder/:id' , Component: PlaceOrder , exact:false}, 
    { path:'/GiftList' , Component: GiftList , exact:false}, 
    { path:'/bankcard' , Component: BankCardRevise , exact:false},
    { path:'/tixian' , Component: Tixian , exact:false},
    { path:'/tixianinfo' , Component: TixianInfo , exact:false},
    { path:'/usermoney' , Component: UserMoney , exact:false},
    { path:'/zfb' , Component: ZFBCard , exact:false},
    { path:'/taobao' , Component: TaobaoCard , exact:false},
    { path:'/basic' , Component: UserBasic , exact:false},
    { path:'/jflist' , Component: JFList , exact:false},
    { path:'/help' , Component: Help , exact:false},
    { path:'/invite' , Component: Invite , exact:false},
    { path:'/addww' , Component: Addww , exact:false},
    { path:'/dingwei' , Component: Dingwei , exact:false},
    { path:'/changepass/:type' , Component: ChnagePass , exact:false},
    { path:'/changepassnext' , Component: ChnagePassNext , exact:false},
  ]

  const getRandom = (start, end, fixed=0) => {
    let differ = end - start
    let random = Math.random()
    return (start + differ * random).toFixed(fixed)
  }

  useEffect(()=>{
    // let request_url = sessionStorage.getItem('jingping_request')
    // if(!request_url) {
      axios.get(URL.NginxApi + '/GetServerIP').then(res => {
        let data = res.data.resultdata
        if(data) {
          let el = data
          if(data.indexOf(':') === -1) {
            el = data + ':8662'
          }
          el = 'http://' + el
          sessionStorage.setItem('jingping_request', el)
        }else {
          const index = getRandom(1, URL.BASE.length, 0)
          const el = URL.BASE[index -1];
          sessionStorage.setItem('jingping_request', el)
        }
      }).catch(err => {
        const index = getRandom(1, URL.BASE.length, 0)
        const el = URL.BASE[index -1];
        sessionStorage.setItem('jingping_request', el)
      })
    // }
  }, [])

  return (
    <Provider store= {store}>
      <Fragment>
        <GlobalStyle />
        <IconFontStyle />
        <Router>
          {/* <Redirect to='/main' /> */}
          {routes.map(({ path, Component, exact }) => (
            <Route key={path} path={path} component={Component}></Route>
          ))}
        </Router>
      </Fragment>
    </Provider>
  );
}

export default App;
