import React, { useState, useEffect, useRef } from 'react';
import { useHistory, Link, withRouter, Redirect } from 'react-router-dom'
import { Toast, Modal, NoticeBar } from 'antd-mobile';
import { Axios } from '../../common/Axios'
import  URL  from '../../common/Axios/NEW_URL'
import { DvaHeader } from '../../common/Template'
import { IPfilter } from '../../common/Js/remToPx'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse,
    ForgetBox,
    MsgWarp
} from './style'

const Login = (props) => {
    let history = useHistory();
    const alert = Modal.alert;
    useEffect(() => {
        console.log('新版本测试:' + URL.isNeedPass)
        setneedPass(URL.isNeedPass)
       // alert('欢迎旧平台用户，您可以使用原有的账密登录')
    }, [])

    const phoneInput = useRef(null)
    const codeInput = useRef(null)
    const passInput = useRef(null)
    const [needPass, setneedPass] = useState(false)
    const [token, settoken] = useState(localStorage.getItem('user_token'))
    const [phone, setphone] = useState('')
    const [code, setcode] = useState('')
    const [getCode, setgetCode] = useState(true)
    const [codeTime, setcodeTime] = useState(20)
    const [pass, setpass] = useState('')
    const [active, setactive] = useState(false)

    const changePhone = () => {
        let val = phoneInput.current.value
        setphone(val)
        changeActive(val, code, pass)
    }

    const changeCode = () => {
        let val = codeInput.current.value
        setcode(val)
        changeActive(phone, val, pass)
    }

    const changePass = () => {
        let val = passInput.current.value
        setpass(val)
        changeActive(phone, code, val)
    }

    const handleGetCode = () => {
        let phoneTest=/^((1[0-9][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
        if(!phone){
            Toast.info("手机号码不能为空", 2, null, false)
        }else if(!phoneTest.test(phone)){
            Toast.info("手机号码格式不正确", 2, null, false)
        }else{
            Axios('get', '/GetMsgCode', { 
                mobile: phone
            }).then(data => {
                Toast.info(data.message, 2, null, false)
                if(data.type == 1){
                    setgetCode(false)
                    setcodeTime(20)
                    let sum = 20
                    let timer = setInterval(()=>{
                        if(sum){
                            sum -- 
                            setcodeTime(sum)
                        }else {
                            clearInterval(timer)
                            setgetCode(true)
                        }
                    }, 1000)
                }
            }).catch(err => {
    
            })
        }
    }

    const changeActive = (phone, code, pass) => {
        if(needPass) {
            if(phone && pass) {
                setactive(true)
            }else {
                setactive(false)
            }
        }else {
            if(phone && code) {
                setactive(true)
            }else {
                setactive(false)
            }
        }
    }

    const handleStart = () => {
        if(active) {
            Toast.loading(`登录中... ${IPfilter()}`, 0, null, true);
            let data = {}
            let request_url = ''
            if(needPass) {
                data = {
                    account: phone,
                    password: pass
                }
                request_url = '/CheckLogin'
            }else {
                data = {
                    mobile: phone,
                    msgCode: code,
                    verifyCode: '1',
                }
                request_url = '/LoginByMobile'
            }
            Axios('get', request_url, data).then(data => {
                Toast.hide()
                if(data.resultdata){
                    let navData = {
                        "home": {tab: 0,index: 0},
                        "order": {tab: 1, index: 0},
                        "video": {tab: 2, index: 0 },
                        "my": { tab: 3, index: 0 },
                        "check": 0
                    }
                    sessionStorage.setItem('nav_data', JSON.stringify(navData))
                    localStorage.setItem('user_token', JSON.stringify(data.resultdata))
                    Axios('get', '/GetBrusherInfo',  {
                        token: data.resultdata,
                    }).then(data => {
                        localStorage.setItem('user_info',JSON.stringify(data.resultdata))
                        let oneUser = localStorage.getItem('one_user_id')
                        let obj = {}
                        if(oneUser) {
                            obj.FUserId = JSON.parse(oneUser)
                            obj.token = JSON.parse(localStorage.getItem('user_token'))
                            Axios('get', '/UserRelation',  obj).then(data => {})
                        }else {
                            localStorage.setItem('one_user_id', JSON.stringify(data.resultdata.UserId))
                        }
                        
                    }).catch(err => {
            
                    })
                    if (typeof window.android == "undefined") {console.log("非App");   }else
                    {
                        
                        if(localStorage.user_token!=undefined)
                        {	
                                var user_token=localStorage.user_token.replaceAll('"','');
                     
                            var res=window.android.setAlias(Math.ceil(Math.random()*1000),user_token);
                         }
                    }
                    Toast.success('登录成功,APP注入成功 !!!', 0.5);
    
                    setTimeout(()=>{
                        history.push('/main')
                    }, 500)
                }else{
                    Toast.info(data.message, 2, null, true)
                    localStorage.removeItem('user_token')
                }
            }).catch(err=> {
    
            })
        }
    }

    if(token) {
        return <Redirect to="/main" />
    }else {
        return (<>
            <DvaHeader Left={false}  Title='登录'></DvaHeader>
            <Container>
                <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>请使用密码登录，若未设置或忘记密码，请点击右下角 <Link to='/changepass/forget' style={{ color: '#409eff', fontWeight: 'bold' }}>设置新密码/忘记密码</Link> 前去设置密码</NoticeBar>
                <LoginWarpper>
                    <LoginBox>
                        <InputBox className='dva-after-bottom'>
                            <NavInput>
                                <i className='iconfont'>&#xe6d6;</i>
                                <input type="number" value={phone} ref={phoneInput} onChange={changePhone} placeholder='请输入手机号' />
                            </NavInput>
                        </InputBox>
                        {
                            needPass ? (<InputBox className='dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe60f;</i>
                                    <input type="password" value={pass} ref={passInput} onChange={changePass} placeholder='请输入密码' />
                                </NavInput>
                            </InputBox>) : (<InputBox className='dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe635;</i>
                                    <input type='text' value={code} ref={codeInput} onChange={changeCode} placeholder='请输入验证码' />
                                    {
                                        getCode ? (<span className='inputTpye' onClick={handleGetCode} >
                                            获取验证码
                                        </span>) : (<span className='inputTpye avtiive'>
                                            {codeTime} 秒
                                        </span>)
                                    }
                                </NavInput>
                            </InputBox>)
                        }
                        <StartUse className={active ? 'active' : ''} onClick={ handleStart } >登录</StartUse>
                        <ForgetBox>
                            <div>
                                无账号！
                                <Link to='/register/0' className='to-rigister davBold'>
                                    去注册
                                </Link>
                            </div>
                            {
                                needPass ? (<Link to='/changepass/forget' className='to-rigister davBold'>
                                设置新密码/忘记密码？
                            </Link>) : null
                            }
                        </ForgetBox>
                    </LoginBox>
                </LoginWarpper>
            </Container>
        </>)
    }
}

export default withRouter(Login)