import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'

export const MyImgViewer = styled.div`
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 10000;
    top: 0;
    left: 0;
    background: #000;
    display: flex;
    align-items: center;
    .img-warp {
        position: relative;
        img {
            width: 100%;
            max-height: 100vh;
        }
        .spec {
            position: absolute;
            bottom: 253px;
            right: 10px;
            font-size: 240px;
            font-weight: bolder;
            letter-spacing: 1.5px;
            -webkit-text-stroke:1px rgba(52,152,219,0.8);
            color: transparent;
            font-size: 20px;
        }
    }
`

export const Container = styled.div`
    min-height: calc(100vh);
    background-color: #fff;
    margin-top: ${ToRem(44)};
    padding-bottom: ${ToRem(50)};
    .bottom-warp {
        position: fixed;
        width: 100%;
        padding: 0 ${ToRem(10)};
        padding-bottom: ${ToRem(5)};
        height: ${ToRem(40)};
        bottom: 0;
        left: 0;
        background: #fff;
        z-index: 100;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .bottom-btn {
            background-color: ${MV.dva_color};
            color: #fff;
            font-size: ${ToRem(18)};
            text-align: center;
            height: ${ToRem(35)};
            line-height: ${ToRem(35)};
            width: 100%;
            margin: 0 auto;
            border-radius: ${ToRem(8)};
            &.userType {
                width: 45%; 
            }
            &.extend {
                background: rgba(0,0,0,.4);
            }
        }
    }
`

export const GroupCell = styled.div`

`

export const GroupTitle = styled.div`
    background-color: #eee;
    padding: 0 10px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .title {
        line-height: 32px;
        font-size: 15px;
    }
    .spec {
        color: rgb(64, 158, 255);
        margin-left: 15px;
        font-size: 12px;
    }
`

export const GroupBody = styled.div`
    &.pading {
        padding: 10px; 
        font-size: 14px;
    }
    .pading {
        padding: 10px; 
        font-size: 14px;
    }
    .body-warp {
        padding: 0 10px;
    }
    .upload-warp {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-top: ${ToRem(0)};
        &.flex-warp{
            justify-content: space-evenly;
        }
        .upload-box {
            .upload-desc {
                margin-top: 7px;
                text-align: center;
                color: rgba(0,0,0,.45);
                &.active {
                    color: rgb(64,158,255);
                }
            }
            .upload-title {
                margin-bottom: 5px;
                font-weight: 700;
                text-align: center;
            }
        }
        .upload-img-box {
            width: ${ToRem(100)};
            background: #ddd;
            border-radius: ${ToRem(5)};
            overflow: hidden;
            .img-box {
                width: 100%;
                height: 100%;
            }
            img {
                width: 100%;
                height: 100%;
            }
        }
        .big-box {
            margin-left: ${ToRem(8)};
            font-size: ${ToRem(14)};
            line-height: ${ToRem(16)};
            color: ${MV.bd_color7};
            &.flex-span  {
                margin-left: 0;
                text-align: center;
                margin-top: 7px;
                line-height: 21px;
            }
        }
    }
`

export const Tip = styled.div`
    color: #f76a24;
    line-height: ${ToRem(16)};
    font-size: ${ToRem(14)};
    text-align: left;
    margin-bottom: ${ToRem(25)} ;
    padding: ${ToRem(10)} ${ToRem(5)};
    background-color: #FEFCEC;
    margin-bottom: ${ToRem(5)};
    span
    {
        font-size: ${ToRem(14)};
        color: #4d94ff;
    }
`

export const LoginWarpper = styled.div`
    position: relative;
    flex: 1;
    padding: ${ToRem(10)} ${ToRem(10)} 0;
`

export const LoginBox = styled.div`
    background-color: #fff;
    border-radius: ${ToRem(5)};
    overflow: hidden;
    padding: ${ToRem(5)} 0;
`

export const InputBox = styled.div`
    position: relative;
    box-sizing: border-box;
    margin-bottom: 0.45333rem;
    padding: ${ToRem(10)} 0;
    width: 100%;
    border-bottom: 0.02667rem solid #f5f5f5;
    font-size: ${ToRem(17)};
    color: ${MV.fz_2};
    &.bank-input {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &.is-show {
        border: 1px solid #409eff;
        border-radius: 5px;
    }
`

export const NavInput = styled.div`
    width: 100%;
    font-size: 0.45333rem;
    border-radius: 0;
    display: flex;
    color: ${MV.fz_2};
    &.is-show {
        padding-bottom: 5px;
        border-bottom: 1px solid #ddd;
    }
    .input-label {
        margin-left: 20px;
    }
    .revise-btn {
        font-size: 14px;
        color: #fff;
        background: #EC503F;
        border-radius: 5px;
        padding: 5px 7px;
        margin-left: 15px;
    }
    input {
        border: none;
        outline: none;
        width: calc(100% - ${ToRem(100)} );
        margin-left: ${ToRem(5)};
        line-height: ${ToRem(23)};
        font-size: ${ToRem(17)};
        border-radius: 0;
        color: #2b2b2b;
        background-color: transparent;
        &::placeholder {
            color: #dcddde;
        }
    }
    .inputTpye {
        position: absolute;
        top: ${ToRem(6)};
        right: 0.26667rem;
        font-size: 0.53333rem;
        font-weight: 800;
        z-index: 1;
        .iconfont {
            color: #909399;
            font-size: ${ToRem(25)};
        }
    }
`

export const NavNote = styled.div`
    position: absolute;
    top: ${ToRem(11)};
    left: ${ToRem(20)};
    width: 100%;
    z-index: 0;
    line-height: ${ToRem(23)};
    font-size: 0.42667rem;
    pointer-events: none;
    color: #dcddde;
    &.none {
        display: none;
    }
`

export const StartUse = styled.div`
    margin-top: 0.74667rem;
    width: 100%;
    height: 1.12rem;
    line-height: 1.12rem;
    text-align: center;
    border-radius: 0.56rem;
    background-color: #d7d7d7;
    font-size: 0.4rem;
    color: #ffffff;
    &.active {
        background-color: #EC503F;
    }
`

export const ForgetBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: ${ToRem(18)};
    font-size: ${ToRem(14)};
    line-height: ${ToRem(14)};
    .forget {
        text-decoration: none;
        color: #a4a4a4;
    }
    .to-rigister {
        color: #EC503F; 
    }
`

export const MsgWarp = styled.div`
    color: #EC503F;
    height: ${ToRem(19)};
    line-height: ${ToRem(19)};
    font-size: ${ToRem(12)};
    font-weight: bold;
    text-align: center;
`

export const InputTitle = styled.div`
    color: ${MV.fz_1};
    width: ${ToRem(100)};
    margin-left: ${ToRem(20)};
    font-size: ${ToRem(17)};
    line-height: ${ToRem(25)};
    height: ${ToRem(25)};
    position: absolute;
`

export const InputRight = styled.div`
    width: 100%;
    background-color: transparent;
    text-align: right;
    position: relative;
    display: flex;
    justify-content: flex-end;
    .picker-warp {
        margin-right: ${ToRem(25)};
        color: ${MV.fz_3};
        width: calc(100% - 120px);
        &.active {
            color: ${MV.fz_1};
        }
    }
    i {
        position: absolute;
        line-height: ${ToRem(20)};
        width: ${ToRem(20)};
        text-align: center;
        height: ${ToRem(16)};
        top: ${ToRem(5)};
        right: 0;
    }
`

export const OpenList = styled.div`
    color: #EC503F;
    position: absolute;
    top: 0.4rem;
    right: ${ToRem(20)};
    text-align: right;
    width: 100%;
    z-index: 0;
    height: 0.42667rem;
    line-height: 0.42667rem;
    font-size: 0.42667rem;
    pointer-events: none;
    line-height: ${ToRem(25)};
    height: ${ToRem(25)};
    top: ${ToRem(10)};
    display: flex;
    justify-content: flex-end;
    align-items: center;
    right: ${ToRem(3)};
    i{ 
        color: ${MV.fz_1};
        font-size: ${ToRem(16)};
        line-height: ${ToRem(16)};
        height: ${ToRem(16)};
        width: ${ToRem(16)};
        text-align: center;
        position: absolute;
        top: ${ToRem(6.5)};
    }
   
`


export const modal = styled.div`
    color: #EC503F;
    position: absolute;
    top: 0.4rem;
    right: ${ToRem(20)};
    text-align: right;
    width: 100%;
    z-index: 0;
    height: 0.42667rem;
    line-height: 0.42667rem;
    font-size: 0.42667rem;
    pointer-events: none;
    i{ 
        color: #000;
    }
   
`

