import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    TremIndex: 0,
    time: null,
    list: [],
    userInfo: JSON.parse(localStorage.getItem('user_info')) ? JSON.parse(localStorage.getItem('user_info')) : {},
    searchValue: '',
    carouselList: [],
    jfOrder: {},
    isGiftGain: true,
    pno: 1, 
    page: 0,
    total: 0,
    totalNum: 0,
    wwModal: false,
    wwsearchvalue: '',
    wwindex: null,
    wwList: [],
    AfficheShow: false,
    AfficheText: '内容加载中...'
})
export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_WW_INDEX:
            return state.set('wwindex', action.value)
        case actionTypes.SET_WW_SEARCH:
            return state.set('wwsearchvalue', action.value)
        case actionTypes.SET_WW_LIST:
            return state.set('wwList', action.list)
        case actionTypes.SET_WW_MODAL:
            return state.set('wwModal', action.value)
        case actionTypes.SET_TAOTAL_NUM:
            return state.set('totalNum', action.value)
        case actionTypes.SET_AFFICHESHOW:
            return state.set('AfficheShow', action.value)
        case actionTypes.SET_AfficheText:
            return state.set('AfficheText', action.value)
        case actionTypes.SET_ALL_PNO:
            return state.set('pno', action.value)
        case actionTypes.SET_ALL_PAGE:
            return state.set('page', action.value)
        case actionTypes.SET_ALL_TOTAL:
            return state.set('total', action.value)
        case actionTypes.SET_GIFT_GAIN:
            return state.set('isGiftGain', action.value)
        case actionTypes.SET_USER_INFO:
            return state.set('userInfo', action.value)
        case actionTypes.SET_JF_ORDER:
            return state.set('jfOrder', action.value)    
        case actionTypes.SET_CAROUSEL_LIST:
            return state.set('carouselList', action.list)
        case actionTypes.SET_LIST:
            return state.set('list', action.list)
        case actionTypes.SET_TIME:
            return state.set('time', action.value)
        case actionTypes.SET_TREM_INDEX:
            return state.set('TremIndex', action.value)
        case actionTypes.SET_SEAR_INPUT:
            return state.set('searchValue', action.value)
        default:
            return state   
    }
}   