import styled from 'styled-components'
import { ToRem } from '../../Js/remToPx'
import {MV} from '../../../common/Css'

export const CardWarp = styled.div`
    background-color: #fff;
    border-radius: ${ToRem(5)};
    margin-top: ${ToRem(10)};
    padding: ${ToRem(10)};
    position: relative;
`

export const TitleWarp = styled.div`
    color: ${MV.fz_1};
    font-size: ${ToRem(14)};
    line-height: ${ToRem(20)};
`

export const CardContent = styled.div`
    display: flex;
    justify-content: space-between;
    .card-img {
        width: ${ToRem(96)};
        height: ${ToRem(96)};
        border-radius: ${ToRem(5)};
        overflow: hidden;
        background-color: rgb(238, 238, 238);
    }
`

export const CardInfo = styled.div`
    width: calc(100% - ${ToRem(106)} );
    display: flex;
    flex-direction: column;
    justify-content: space-between;
`

export const GoodName = styled.div`
    font-size: ${ToRem(14)};
    line-height: ${ToRem(16)};
    color: ${MV.fz_1};
    width: 100%;
`

export const GoodPrice = styled.div`
    width: 100%;
    height: ${ToRem(40)};
    display: flex;
    justify-content: flex-end;
    align-items: center;
`
export const PriceBox = styled.div`
    margin-right: ${ToRem(10)};
    text-align: right;
`

export const MisionWarp = styled.div`
    color: #EC503F;
    line-height: ${ToRem(22)};
    font-size: ${ToRem(15)};
`

export const PriceCell = styled.div`
    color: ${MV.fz_2};
    line-height: ${ToRem(18)};
    font-size: ${ToRem(13)};
    .price {
        margin-right: ${ToRem(10)};
        color: rgb(251, 114, 153);
    }
    .dog {
        font-size: ${ToRem(12)};
    }
`

export const CradBtn = styled.div`
    text-align: center;
    padding: ${ToRem(5)} ${ToRem(10)};
    border-radius: ${ToRem(10)};
    background-color: #EC503F;
    color: #fff;
    font-size: ${ToRem(12)};
`

export const MaskWarp = styled.div`
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: ${ToRem(10)};
    background-color: rgba(0, 0, 0, .6);
    color: #fff;
    text-align: center;
    line-height: 1;
    font-size: ${ToRem(16)};
    display: flex;
    align-items: center;
    justify-content: center;
`