import { actionTypes } from './index'

import { Toast} from 'antd-mobile'

import { fromJS } from 'immutable'
import { Axios } from '../../../../../../../common/Axios';
import { entireTime } from '../../../../../../../common/Js/timeFiltter';

export const setRealName = (value, RealName) =>{
    return (dispatch) =>{
        if(value == RealName){
            Toast.info("名字与之前重复", 2, null, false)
        }else if(value == ''){
            Toast.info("名字不能为空", 2, null, false)
        }else{
            Axios('post', '/UpdateBrusherInfo', { 
                RealName: value
            }).then(data => {
                // const user = JSON.parse(localStorage.getItem('user_info'))
                // user.RealName = value
                // localStorage.setItem('user_info', JSON.stringify(user))
                // dispatch(setUser(user))
                // Toast.success(data.message, 2, null ,false)
                getUserInfo(dispatch, (data) => {
                    Toast.success(data.message, 2, null ,false)
                })


            }).catch(err => {
    
            })
        }

        
    }
}



export const setUser = (obj) =>({
    type: actionTypes.SET_USER,
    obj: fromJS(obj)
})

export const setRadioIndex = (value) =>({
    type: actionTypes.SET_RADIO_INDEX,
    value
})


export const changeBirthday = (date)=>{
    return (dispatch)=>{
        let birthday = date.getTime()
        let time = entireTime(birthday)
        dispatch(setBirthday(date))
        Axios('post', '/UpdateBrusherInfo', { 
            Birthday: time
        }).then(data => {
            Toast.hide()
            Toast.success(data.message, 2, null ,false)
            getUserInfo(dispatch, (data) => {
               
            })
        }).catch(err => {

        })
    }
}

export const changeSex = (value)=>{
    return (dispatch)=>{
        console.log(value)
        Axios('post', '/UpdateBrusherInfo', { 
            Gender: value
        }).then(data => {
            Toast.hide()
            Toast.success(data.message, 2, null ,false)
            getUserInfo(dispatch, (data) => {
                
            })
        }).catch(err => {
            
        })
    }
}

const setBirthday = (date)=>({
    type: actionTypes.SET_BIRTHDAY,
    value: date
})

const getUserInfo = (dispatch, callback) => {
    Axios('get','/GetBrusherInfo',{}).then(data =>{
        localStorage.setItem('user_info',JSON.stringify(data.resultdata))
        dispatch(setUser(data.resultdata))
        callback(data)
    }).catch(err =>{

    })
}