import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../../../common/Js/remToPx'
import {MV} from '../../../../common/Css'

const topBg = "../imgs/topBg.jpg"

export const Kefu = styled.div`
    position: fixed;
    z-index: 100;
    background: #33a3f4;
    text-align: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    line-height: 50px;
    color: #fff;
    font-size: 16px;
    bottom: 100px;
    right: 6px;
`

export const Container = styled.div`
    min-height: calc(100vh - ${ToRem(44)} );
    background-color: #FAF8FA;
    .modal-btn {
        line-height: ${ToRem(22)};
        font-size: ${ToRem(14)};
        height: ${ToRem(22)};
        padding: 0 ${ToRem(10)};
        margin: 0 0;
        &.active {
            color: #fff;
            background: ${MV.dva_color};
            border-color: transparent;
            border-radius: 5px;
        }
    }
`

export const ProgressWarp = styled.div`
    position: absolute;
    bottom: ${ToRem(20)};
    left: 0;
    width: 100%;
    min-width: ${ToRem(293)};
    width: 80%;
    left: 10%;
    height: 60px;
    &.exdent {
        width: 100%;
        left: 0%;
        bottom: 0;
    }
    &.JFCount {

    }
    .commision {
        display: flex;
        align-items: center;
        justify-content: center;
        line-height:${ToRem(60)};
        font-size: ${ToRem(14)};
        .line {
            padding: 0 ${ToRem(16)};
        }
        .num {
            color: #ffff00;
            padding-right: ${ToRem(2)};
        }
    }
`

export const MyTop = styled.div`
    background-color: #fff;
    
`

export const MyMoney = styled.div`
    background-color: rgb(250,248,250);
    padding: ${ToRem(20)} ${ToRem(10)};
`

