import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh);
    background-color: #f6f6f6;
    margin-top: ${ToRem(44)};
    .bottom-warp {
        position: fixed;
        width: 100%;
        padding: 0 ${ToRem(10)};
        padding-bottom: ${ToRem(5)};
        height: ${ToRem(40)};
        bottom: 0;
        left: 0;
        background: #fff;
        z-index: 100;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .bottom-btn {
            background-color: ${MV.dva_color};
            color: #fff;
            font-size: ${ToRem(18)};
            text-align: center;
            height: ${ToRem(35)};
            line-height: ${ToRem(35)};
            width: 100%;
            margin: 0 auto;
            border-radius: ${ToRem(8)};
            &.userType {
                width: 45%; 
            }
            &.extend {
                background: rgba(0,0,0,.4);
            }
        }
    }
`


export const Tip = styled.div`
    color: ${MV.fz_2};
    line-height: ${ToRem(16)};
    font-size: ${ToRem(14)};
    text-align: left;
    margin-bottom: ${ToRem(25)} ;
    padding: ${ToRem(10)} ${ToRem(5)};
    background-color: #eee;
    margin-bottom: ${ToRem(5)};
    span
    {
        font-size: ${ToRem(14)};
        color: #EC503F;
    }
`

export const InfoCell = styled.div`
    padding: ${ToRem(15)} ${ToRem(10)};
    font-size: ${ToRem(12)};
    .flex-start {
        display: flex;
        justify-content: space-between;
        align-items: center;
        line-height: 2;
    }
    .upload-warp {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-top: ${ToRem(20)};
        .upload-img-box {
            width: ${ToRem(200)};
            background: #ddd;
            border-radius: ${ToRem(5)};
            overflow: hidden;
            .img-box {
                width: 100%;
                height: 100%;
            }
            img {
                width: 100%;
                height: 100%;
            }
        }
        .big-box {
            margin-left: ${ToRem(8)};
            font-size: ${ToRem(14)};
            line-height: ${ToRem(16)};
            color: ${MV.bd_color7};
        }
    }
`