import React, { PureComponent } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import URL from '../../common/Axios/URL_SET'
import Home from './page/home'
import Order from './page/order'
import Video from './page/video'
import My from './page/my'
import { Redirect } from 'react-router-dom'

import {
    TabBar,
    Toast
} from 'antd-mobile';

import {
    HomeWarp,
    TabsWarp
} from './style'

import { DvaHeader } from '../../common/Template'

class Main extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {}
    }
    componentDidMount() {
        let nav = JSON.parse(sessionStorage.getItem('nav_data'))
        if (!nav) {
            let navData = {
                home: {
                    tab: 0,
                    index: 0
                },
                order: {
                    tab: 1,
                    index: 0
                },
                my: {
                    tab: 2,
                    index: 0
                },
                video: {
                    tab: 3,
                    index: 0
                },
                check: 0
            }
            sessionStorage.setItem('nav_data', JSON.stringify(navData))
            this.props.handleInital(navData)
        } else {
            this.props.handleInital(nav)
        }

    }

    render() {
        const { match, selectedTab, changeSelectedTab, titleValue, handleTar, history, baga } = this.props
        const nav = JSON.parse(sessionStorage.getItem('nav_data'))

        let token = localStorage.getItem('user_token')

        if (!token) {
            Toast.fail('用户信息过期，请重新登录', 2, null, true)
            return <Redirect to="/login" />
        } else {
            return (<HomeWarp>
                <TabsWarp>
                    {/* <DvaHeader Left={false} Title={titleValue}></DvaHeader> */}
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="#33A3F4"
                        barTintColor="white"
                        tabBarPosition="bottom"
                        prerenderingSiblingsNumber={0}
                    >
                        <TabBar.Item
                            title="首页"
                            key="home"
                            icon={<div style={{
                                width: '22px',
                                height: '22px',
                                background: `url(/imgs/index.png) center center /  21px 21px no-repeat`
                            }}
                            />
                            }
                            selectedIcon={<div style={{
                                width: '22px',
                                height: '22px',
                                background: `url(/imgs/indexSelect.png) center center /  21px 21px no-repeat`
                            }}
                            />
                            }
                            selected={selectedTab === 'home'}
                            onPress={() => { changeSelectedTab('home', nav) }}
                        >
                            <Home match={match} history={history}></Home>
                        </TabBar.Item>

                        <TabBar.Item
                            title="售后问题"
                            key="order"
                            badge={baga}
                            icon={
                                <div style={{
                                    width: '22px',
                                    height: '22px',
                                    background: `url(/imgs/task.png) center center /  21px 21px no-repeat`
                                }}
                                />
                            }
                            selectedIcon={
                                <div style={{
                                    width: '22px',
                                    height: '22px',
                                    background: `url(/imgs/taskSelect.png) center center /  21px 21px no-repeat`
                                }}
                                />
                            }
                            selected={selectedTab === 'order'}
                            onPress={() => { changeSelectedTab('order', nav) }}
                        >
                            <Order history={history}></Order>
                        </TabBar.Item>
                        <TabBar.Item
                            title="我的"
                            key="my"
                            icon={{ uri: `/imgs/my.png` }}
                            selectedIcon={{ uri: `/imgs/mySelect.png` }}
                            selected={selectedTab === 'my'}
                            onPress={() => { changeSelectedTab('my', nav) }}
                        >
                            <My history={history}></My>
                        </TabBar.Item>
                        {/* <TabBar.Item
                            title="高佣任务"
                            key="integral"
                            badge="99"
                            icon={
                                <div style={{
                                    width: '22px',
                                    height: '22px',
                                    background: `url(/imgs/integral.png) center center /  21px 21px no-repeat`
                                }}
                                />
                            }
                            selectedIcon={
                                <div style={{
                                    width: '22px',
                                    height: '22px',
                                    background: `url(/imgs/integralSelect.png) center center /  21px 21px no-repeat`
                                }}
                                />
                            }
                            selected={selectedTab === 'video'}
                            onPress={() => { window.location.href="/geridan/askanser.html"; }}
                        >
                            <Video></Video>
                        </TabBar.Item>
                        */}
                    </TabBar>
                </TabsWarp>
            </HomeWarp>)
        }
    }
}

const mapStateToProps = (state) => {
    return {
        selectedTab: state.getIn(['main', 'selectedTab']),
        titleValue: state.getIn(['main', 'titleValue']),
        baga: state.getIn(['main', 'baga'])
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleInital(nav) {
            dispatch(actionCreatore.InitalTbrAction(nav))
            // dispatch(orderActionCreatore.setBaga())
        },
        changeSelectedTab(value, nav) {
            dispatch(actionCreatore.ChangeTabSelect(value, nav))
        },
        handleTar(value) {
            // console.log(value)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);