import { actionTypes } from './index'

import { Toast} from 'antd-mobile'
import { fromJS } from 'immutable'
import { Axios } from '../../../common/Axios'
import  URL  from '../../../common/Axios/URL_SET'


const setGiftList = (list) => ({
    type: actionTypes.SET_GIFT_LIST,
    list: fromJS(list)
})

const setGiftHistory = (list) => ({
    type: actionTypes.SET_GIFT_HISTORY,
    list: fromJS(list)
})

export const intailAction = (token) => {
    return (dispatch) => {
        Axios('get','/GetIsReceive',{
            
        }).then(data =>{
            if(data.resultdata.NoReceiveList){
                dispatch(setGiftList(data.resultdata.NoReceiveList))
                let arr = []
                for (let i = 0; i < data.resultdata.NoReceiveList.length; i++) {
                    const el = data.resultdata.NoReceiveList[i];
                    if(el.Receive == 1) {
                        arr.push(el)
                    }
                }
                console.log(arr)
                dispatch(setGiftHistory(arr))
            }else{
                dispatch(setGiftList([]))
            }
            
        }).catch(err =>{

        })
    }
}

export const setPopupShow = (value) => ({
    type: actionTypes.SET_POPUP_SHOW,
    value
})

const setSelectlevel = (value) => ({
    type: actionTypes.SET_SELECT_LEVEL,
    value
})

export const applyGiftAction = (list, level) => {
    return (dispatch) =>{
        // console.log(level)
        dispatch(setSelectlevel(level))
        dispatch(setPopupShow(true))
        let arr = []
        for (let i = 0; i < list.length; i++) {
            let obj = {}
            const el = list[i];
            obj.isCheck = false
            obj.value = el
            arr.push(obj)
        }

        dispatch(setPopupList(arr))
    }
}

export const setPopupList = (list) => ({
    type: actionTypes.SET_POPUP_LIST,
    list: fromJS(list)
})

export const setMobile = (value) => ({
    type: actionTypes.SET_MOBILE,
    value
})

export const setName = (value) => ({
    type: actionTypes.SET_NAME,
    value
})

export const setAddress = (value) => ({
    type: actionTypes.SET_ADDRESS,
    value
})

export const giftSubmitAction = (level, popupList, name, mobile, address) => {
    return (dispatch) => {
        // console.log(level, popupList, name, mobile, address)
        let int = false
        let gift = ''
        for (let i = 0; i < popupList.length; i++) {
            const el = popupList[i];
            if(el.isCheck) {
                int = true
                gift = el.value
            }
        }
        if(!level) {
            Toast.info('请先选择想要领取的等级礼包', 2, null, false)
        }else if(!int) {
            Toast.info('请选择礼品后再领取奖励', 2, null, false)
        }else if(!name) {
            Toast.info('请填写收货人姓名后再领取奖励', 2, null, false)
        }else if(!mobile) {
            Toast.info('请填写联系号码后再领取奖励', 2, null, false)
        }else if(!address) {
            Toast.info('请填写收货地址后再领取奖励', 2, null, false)
        }else{
            console.log('发起请求')
            Axios('get','/ApplyGift',{
                mobile,
                Level: level,
                Address: address,
                Gift: gift,
                ReceiveName: name
            }).then(data =>{
                console.log(data)
                Toast.info(data.message, 2, null, false)
                dispatch(setPopupShow(false))
                dispatch(intailAction())
            }).catch(err =>{
    
            })
        }
    }
}

export const setGiftMore = (value) => ({
    type: actionTypes.SET_GIFT_MORE,
    value
})

export const setGiftMoreShow = (item) => {
    return (dispatch) => {
        dispatch(setGiftMore(true))
        console.log(item)
        dispatch(setMoreData(item))
    }
}

const setMoreData = (obj) => ({
    type: actionTypes.SET_MORE_DATA,
    obj: fromJS(obj)
})