import { actionTypes } from './index'
import { Toast } from 'antd-mobile';
import { Axios } from '../../../common/Axios';
import { fromJS } from 'immutable';
import  URL  from '../../../common/Axios/URL_SET'
import HtmlImageCompress from 'html-image-compress'

const getBase64 = (img, callback) => {
    let htmlImageCompress = new HtmlImageCompress(img, { width: 1200, height: 800, quality:0.9})
    htmlImageCompress.then(function (result) {
        // 成功后执行 
        let file = result.file; // 压缩后的图片文件(file)对象
        let fileSize = result.fileSize // 压缩后的图片文件(file)大小
        let base64 = result.base64; // 压缩后的base64图片
        let origin = result.origin; // 压缩前的图片文件（file）对象
        callback(base64);
    }).catch((err) => {
            // 处理失败会执行 
            const reader = new FileReader(); 
            reader.addEventListener('load', () => {
                let newB64 = reader.result;
                callback(newB64);
            });
            reader.readAsDataURL(img);
        })
} 

const SaveImgToServer = (base64, orderIds, fieldName, callback) => {
    Toast.loading("正在上传...", 0, null, true);
    //上传到OSS
    Axios('post2', '/SaveImg', {
        token: JSON.parse(localStorage.getItem('user_token')),
        base64,
        orderIds,
        fieldName
    }).then(data => {
        Toast.hide();
        callback(data.resultdata);
    }).catch(err => {
        Toast.fail('上传失败,请重试!', 2, null, false);
    })
}

const beforeUpload = (file) => {
    if (file == undefined) {
        Toast.fail('请拍照或上传图片', 2, null, false);
        return false;
    }
    
    const isLt2M = file.size / 1024 / 1024 < 13;
    if (!isLt2M) {
        Toast.fail('图片大小不超过13M', 2, null, false);
    }
    return  isLt2M;
}

export const intalAction = () => {
    return (dispatch) => {
        Axios('get','/GetBrusherInfo',{
            token: JSON.parse(localStorage.getItem('user_token')),
        }).then(data =>{
            localStorage.setItem('user_info',JSON.stringify(data.resultdata))
            
        }).catch(err =>{

        })
    }
}

export const UploadFileAction = (input, info) => {
    return (dispatch) => {
        let file = input.target.files[0];
        if (beforeUpload(file)) {
            getBase64(input.target.files[0], imgInfo => { 
                SaveImgToServer(imgInfo, '111', 'ImgPathOrderPay', function (path) {
                    info.img_url = path
                    dispatch(setInfo(info))
                })
            })
        }
    }
}

const setInfo = (obj) => ({
    type: actionTypes.SET_INFO,
    obj: fromJS(obj)
})

export const viewerAction = (img) => {
    return (dispatch) => {
        dispatch(setSelectImg(img))
        setTimeout(()=>{
            dispatch(setViewerShow(true))
        }, 0)
    }
}

export const setViewerShow = (value)=>({
    type: actionTypes.SET_VIEWER_SHOW,
    value
})

const setSelectImg = (value) => ({
    type: actionTypes.SET_SELECT_IMG,
    value
})

