import styled from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import * as MV from '../../common/Css'

export const HomeWarp = styled.div`
    background-color: #F9F9F9;
    .base {
        height: ${ToRem(1000)};
    }
`

export const TabsWarp = styled.div`
    position: fixed;
    width: 100%;
    height: 100%;
    bottom: 0;
`
export const OutLogin = styled.button`
    background-color: #EC503F;
    color: #fff;
    width: 90%;
    margin: ${ToRem(220)}  0  0  ${ToRem(19)};
    font-size: ${ToRem(14)};
    border-radius: ${ToRem(20)};
    border-color: #FAF8FA;
    border: ${ToRem(1)} solid transparent;
    height: ${ToRem(42)};
    line-height: ${ToRem(42)};
    font-size: ${ToRem(16)};
`