// 响应拦截
axios.interceptors.response.use(
    response => {
        // console.log(response.status)
        if (response.status === 200) {
            return Promise.resolve(response)
        } else {
            return Promise.reject(response)
        }
    },
    err => {
        let status = err.response.status
        let requestUrl = err.response.config.url
        let apiUrl = sessionStorage.getItem('jingping_request')
        console.log(status, requestUrl)
        if (status) {
            switch (status) {
                case 403:
                    console.log('状态码：' + 403)
                    if (requestUrl.indexOf('GetServerIP') > -1) {
                        Toast.fail('获取负载均衡API服务器繁忙,请重试' + status, 4, null, false)
                    } else if (requestUrl.indexOf('GetOrder') > -1) {
                        let jingping_request = sessionStorage.getItem('jingping_request')
                        if (URL.BASE1) {
                            jingping_request = URL.BASE1
                        }

                        Toast.fail('请求ip地址：' + IPfilter(jingping_request) + '，状态：' + status + '，' + err.response.data, 4, null, false)
                    } else {
                        Toast.fail('请求ip地址：' + IPfilter(apiUrl) + '，状态：' + status + '，' + err.response.data, 4, null, false)
                    }

                    localStorage.removeItem('user_token')
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000)
                    break;
                default:
                    if (requestUrl.indexOf('GetServerIP') > -1) {
                        Toast.fail('获取负载均衡API服务器繁忙,请重试，状态：' + status, 4, null, false)
                    } else {
                        if (requestUrl.indexOf('DoneTask') > -1) {
                            Toast.fail('请求ip地址：' + IPfilter(URL.DonTaskServerIP) + '，服务器繁忙,请重试，状态：' + status, 4, null, false)
                        } else if (requestUrl.indexOf('GetOrder') > -1) {
                            let jingping_request = sessionStorage.getItem('jingping_request')
                            if (URL.BASE1) {
                                jingping_request = URL.BASE1
                            }

                            Toast.fail('请求ip地址：' + IPfilter(jingping_request) + '，状态：' + status, 4, null, false)
                        } else {
                            Toast.fail('请求ip地址：' + IPfilter(apiUrl) + '，服务器繁忙,请重试，状态：' + status, 4, null, false)
                        }
                    }
                    break;
            }
            return Promise.reject(err.response)
        }
    }
)

const getRandom = (start, end, fixed = 0) => {
    let differ = end - start
    let random = Math.random()
    return (start + differ * random).toFixed(fixed)
}

const get = (address, data) => {
    return new Promise((resolve, reject) => {
        data.token ? (data.token = data.token) : (data.token = JSON.parse(localStorage.getItem('user_token')))
        let jingping_request = sessionStorage.getItem('jingping_request')
        if (jingping_request) {
            axios.get(jingping_request + address, {
                params: data
            }).then(res => {
                resolve(res.data)
            }).catch(err => {
                reject(err.data)
            })
        } else {
            setTimeout(() => {
                window.location.reload()
            }, 5000)
        }
    })
}

