const post = (address, data, isQs) => {
    return new Promise((resolve, reject) => {
        data.token ? (data.token = data.token) : (data.token = JSON.parse(localStorage.getItem('user_token')))
        let fromData = {}
        isQs ? fromData = qs.stringify(data) : fromData = data;
        let jingping_request = sessionStorage.getItem('jingping_request')
        if (jingping_request) {
            axios({
                method: 'post',
                url: jingping_request + address,
                data: fromData,
            }).then(res => {
                resolve(res.data)
            }).catch(err => {
                reject(err.data)
            })
        } else {
            setTimeout(() => {
                window.location.reload()
            }, 5000)
        }
    })
}

