export const Axios = (method, address, data, isQs) => {
    if (method === 'get') {
        return get(address, data)
    } else if (method === 'getOrder') {
        return getOrder(address, data)
    } else if (method === 'get1') {
        return get1(address, data)
    } else if (method === 'get2') {
        return get2(address, data)
    } else if (method === 'post') {
        isQs === undefined ? isQs = true : isQs = isQs
        return post(address, data, isQs)
    } else if (method === 'post2') {
        isQs === undefined ? isQs = true : isQs = isQs
        return post2(address, data, isQs)
    } else if (method === 'getTask') {
        return getTask(address, data)
    } else if (method === 'PostDonTask') {
        isQs === undefined ? isQs = true : isQs = isQs
        return PostDonTask(address, data, isQs)
    }
    else if (method === 'YanHao') {
        isQs === undefined ? isQs = true : isQs = isQs
        return YanHao(address, data, isQs)
    }
    
}



