import React, { useState, useEffect, useRef } from 'react';
import { useHistory, Link, withRouter } from 'react-router-dom'
import { Toast, NoticeBar, Modal } from 'antd-mobile';
import { PathImg, defaultImg } from '../../common/Js/orderFiltter'
import HtmlImageCompress from 'html-image-compress'
import { Axios } from '../../common/Axios'
import  URL  from '../../common/Axios/NEW_URL'
import { phoneTest } from '../../utils/test/index'
import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse,
    ForgetBox,
    TaoCode,
    HandleWarp,
    InputWarp,
    InputBoxs,
    IconWarp,
    InputBtn
} from './style'
import './style.css'
import { DvaHeader } from '../../common/Template'

const Register = (props) => {
    let history = useHistory();
    const alert = Modal.alert;
    useEffect(() => {
        let _invite = location.pathname.split('/')[2]
        setneedPass(URL.isNeedPass)
        if(_invite) {
            if(_invite == 0) {
                setinvite('') 
            }else {
                setinvite(_invite)
            }
        }
    }, [])

    const { location } = props
    const phoneInput = useRef(null)
    const codeInput = useRef(null)
    const inviteInput = useRef(null)
    const TwordInput = useRef(null)
    const fileInput = useRef(null)
    const passInput = useRef(null)
    const opassInput = useRef(null)
    const [needPass, setneedPass] = useState(false)
    const [popupShow, setpopupShow] = useState(false)
    const [popupType, setpopupType] = useState('')
    const [phone, setphone] = useState('')
    const [code, setcode] = useState('')
    const [getCode, setgetCode] = useState(true)
    const [codeTime, setcodeTime] = useState(20)
    const [invite, setinvite] = useState('')
    const [pass, setpass] = useState('')
    const [opass, setopass] = useState('')
    const [ispassCon, setispassCon] = useState(false)
    const [TwordValue, setTwordValue] = useState('')
    const [ww, setww] = useState('')
    const [wwImg, setwwImg] = useState('')
    const [wwTest, setwwTest] = useState(false)

    const popupCloose = () => {
        setpopupShow(false)
    }

    const handlePopup = (int) => {
        setpopupShow(true)
        setpopupType(int)
    }

    const changePhone = () => {
        let val = phoneInput.current.value
        setphone(val)
    }

    const handleGetCode = () => {
        if(!phone){
            Toast.info("手机号码不能为空", 2, null, false)
        }else if(!phoneTest.test(phone)){
            Toast.info("手机号码格式不正确", 2, null, false)
        }else{
            Axios('get', '/GetMsgCode', { 
                mobile: phone
            }).then(data => {
                Toast.info(data.message, 2, null, false)
                if(data.type == 1){
                    setgetCode(false)
                    setcodeTime(20)
                    let sum = 20
                    let timer = setInterval(()=>{
                        if(sum){
                            sum -- 
                            setcodeTime(sum)
                        }else {
                            clearInterval(timer)
                            setgetCode(true)
                        }
                    }, 1000)
                }
            }).catch(err => {
    
            })
        }
    }

    const changeCode = () => {
        let val = codeInput.current.value
        setcode(val)
    }

    const changeInvite = () => {
        let val = inviteInput.current.value
        setinvite(val)
    }

    const passCompare = (pass, opass) => {
        if(pass === opass) {
            setispassCon(false)
        }else {
            setispassCon(true)
        }
    }

    const changePass = () => {
        let val = passInput.current.value
        setpass(val)
        passCompare(val, opass)
    }

    const changeOpass = () => {
        let val = opassInput.current.value
        setopass(val)
        passCompare(pass, val)
    }

    const changeTword = () => {
        let val = TwordInput.current.value
        setTwordValue(val)
    }

    const clearTword = () => {
        TwordInput.current.value = ''
        setTwordValue('')
        Toast.info('已清空', 1, null, false )
    }

    //paraName 等找参数的名称
    const GetUrlParam = (paraName, url) => {
        if(!url)    url = document.location.toString()
        let arrObj = url.split("?");
        if (arrObj.length > 1) {
            let arrPara = arrObj[1].split("&");
            var arr;
            for (var i = 0; i < arrPara.length; i++) {
                arr = arrPara[i].split("=");
                if (arr != null && arr[0] == paraName) {
                    return arr[1];
                }
            }    
            return ""; 　　　　　　　　
    　　}else{
            return "";
    　　}
    }

    const TwordTest = () => {
        if(!TwordValue) {
            Toast.fail('淘口令不能为空', 2, null, false);
            return
        }
        let str = TwordValue
        Toast.loading("解析中...", 0, null, true)
        var indexName=str.indexOf("&name=");
if(indexName>=0)
{
var indexPG=str.indexOf("&isPublicGroup=");
var FinalAliww=str.substring(indexName+6,indexPG);

FinalAliww=decodeURI(FinalAliww);
setww(FinalAliww)
Toast.hide() 
return
 }

        Axios('get', '/ChangeTaoKoulingToLink', {
            token: 0,
            TaoKouLing: str
        }).then(data => {
            // console.log(data.resultdata.data)
            let res = JSON.parse(JSON.stringify(data.resultdata.data))
            if(data.type == 1){
                let url = res.url
                let psname = decodeURI(GetUrlParam("name", url))
                if(psname) {
                    Toast.hide() 
                    Toast.success(`解析成功，结果为 ${psname}`, 2, null, false)
                    setww(psname)
                    return
                }
            }else {
                Toast.fail(res.msg, 2, null, false);
            }
            Toast.hide() 
            Toast.fail("解析失败，请重试", 2, null, false)
        }).catch(err => {
            console.log(err)
            Toast.hide() 
            Toast.fail("解析失败，请重试", 2, null, false)
        })
    }   

    const handleUpload = () => {
        document.getElementById('uploadInput').click()
    }

    const beforeUpload = (file) => {
        if (file == undefined) {
            Toast.fail('请拍照或上传图片', 2, null, false);
            return false;
        }
        const isLt2M = file.size / 1024 / 1024 < 13;
        if (!isLt2M) {
            Toast.fail('图片大小不超过13M', 2, null, false);
        }
        return  isLt2M;
    }

    const getBase64 = (img, callback) => {
        let htmlImageCompress = new HtmlImageCompress(img, { width: 1200, height: 800, quality:0.9})
        htmlImageCompress.then(function (result) {
            let file = result.file; // 压缩后的图片文件(file)对象
            let fileSize = result.fileSize // 压缩后的图片文件(file)大小
            let base64 = result.base64; // 压缩后的base64图片
            let origin = result.origin; // 压缩前的图片文件（file）对象
            callback(base64);
        }).catch((err) => {
                const reader = new FileReader(); 
                reader.addEventListener('load', () => {
                    let newB64 = reader.result;
                    callback(newB64);
                });
                reader.readAsDataURL(img);
            })
    } 

    const SaveImgToServer = (base64,orderIds,fieldName, callback) => {
        Toast.loading("正在上传...", 0, null, true);
        Axios('post2', '/SaveImg', {
            token: JSON.parse(localStorage.getItem('user_token')),
            base64,
            orderIds,
            fieldName
        }).then(data => {
            Toast.hide();
            callback(data);
        }).catch(err => {
            Toast.fail('上传失败,请重试!', 2, null, false);
        })
    }

    // 识别旺旺号信息
    const getIdInfo = (path, callback) => {
        Toast.loading("正在识别身份信息...", 0, null, true);
        Axios('get2', '/RecognizeOrderPic', {
            token: JSON.parse(localStorage.getItem('user_token')),
            picpath: URL.IMG_UPLOAD.replace("hapi","")+ path
        }).then(res => {
            Toast.hide();
            callback(res.message);
        }).catch(err => {
            Toast.hide();
            Toast.fail('识别失败，截图不清晰或图片信息不正确!', 2, null, false);
        })
    }

    const changeFile = (info) => {
        let file = info.target.files[0];
        if (beforeUpload(file)) {
            getBase64(file, imgInfo => { 
                SaveImgToServer(imgInfo, '', '', function (res) {
                    let path = res.resultdata
                    setwwImg(path)
                    getIdInfo(path, (data) => {
                        let str1 = data.split('淘宝账号')[1]
                        if(str1) {
                            let psname = ''
                            if(str1.indexOf('修改手机号') > -1) {
                                psname = str1.split('修改手机号')[0].substring(0, 25)
                            }else {
                                psname = str1.split('手机号')[0].substring(0, 25)
                            }
                            // let psname = str1.split('修改手机号')[0]
                            if(psname) {
                                Toast.hide() 
                                Toast.success(`识别成功，结果为 ${psname}`, 2, null, false)
                                setww(psname)
                                return
                            }else {
                                Toast.fail('识别失败，您的截图不包含旺旺号,请重新截图,付款信息包含关键字  淘宝账号 和 修改手机号', 2, null, false);
                                return
                            }
                        }else {
                            Toast.fail('识别失败，您的截图不包含旺旺号,请重新截图,付款信息包含关键字  淘宝账号 和 修改手机号', 2, null, false);
                            return
                        }
                    })
                })
            })
        }
    }

    const handleAtTest = () => {
        Toast.loading('正在验号...', 0, null, true);
        setwwTest(false)
        if(ww) {
            Axios('YanHao', '/CheckAliAccount', {
                RelationId: 0,
                account: ww   
            }).then(res => { 
                Toast.hide()
                setTimeout(()=>{
                    if(res.resultdata == 1) {
                        Toast.success(res.message, 2, () => {}, true); 
                        setwwTest(true)
                        return
                    }else if(res.resultdata == 0){
                        Toast.fail(res.message, 2, () => {}, true);
                        setwwTest(false)
                    }  
                }, 0)
            }).catch(err => {
                setTimeout(()=>{
                    Toast.hide()
                }, 0)
                Toast.fail('网络错误，请稍后再试', 2, () => {}, true);
                console.log(err)
            })
        }else {
            Toast.hide()
            setTimeout(()=>{
                Toast.fail('请先提取旺旺号或提取到正确格式旺旺号后再验号', 2, () => {}, true);
            }, 0)
        }
    }

    const handleStart = () => {
        if(!phone) {
            Toast.fail('手机号不能为空', 2, null, false)
            return
        }
        if(!phoneTest.test(phone)){
            Toast.fail('手机号格式不正确，请检查', 2, null, false)
            return
        }
        if(!code) {
            Toast.fail('短信验证码不能为空', 2, null, false)
            return
        }
        if(!invite) {
            Toast.fail('邀请码不能为空', 2, null, false)
            return
        }
        if(needPass) {
            if(!pass) {
                Toast.fail('请输入密码', 2, null, false)
                return
            }
            passCompare(pass, opass)
            if (pass !== opass) {
                Toast.fail('两次输入密码不一致', 2, null, false)
                return
            }
        }
        if(!ww) {
            Toast.fail('请先解析得到旺旺号', 2, null, false)
            return
        }
        if(!wwTest) {
            Toast.fail('请先验证并通过旺旺号', 2, null, false)
            return
        }
        Toast.loading('正在注册...', 0, null, true); 
        Axios('get', '/Register', {
            mobile: phone,
            inviteCode: invite,
            password: pass,
            msgCode: code,
            aliAccount: ww,
            way: 0,
            LevelImg: '',
            TaoAgeImg: '',
            token: 0,
            uaddress: ''
        }).then(data => {
            Toast.hide()
            if(data.type == 1) {
                Toast.success(data.message, 2, null, false);
                history.push('/login') 
            }else{
                Toast.fail(data.message, 2, null, false);
            }
        }).catch(err => {
            Toast.hide()  
            Toast.fail('网络请求失败，请重试', 2, null, false);
        })
    }

    return (<>
        <DvaHeader Left={true} Title='账号注册'></DvaHeader>
        <Container>
            <NoticeBar className='notice-warp' marqueeProps={{ loop: true }}>
                手机号必须要跟本人淘宝号旺旺号以及支付宝绑定的手机号一致，否则无法提现
            </NoticeBar>
            <LoginWarpper>
                <LoginBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe6e8;</i>
                            <input placeholder='请输入手机号' type='number' value={phone} ref={phoneInput} onChange = { changePhone } />
                        </NavInput>
                    </InputBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe635;</i>
                            <input placeholder='请输入短信验证' type='text' value={code} ref={codeInput} onChange = { changeCode } />
                            {
                                getCode ? (<span className='inputTpye' onClick={ handleGetCode } >
                                    获取验证码
                                </span>) : (<span className='inputTpye avtiive' >
                                    {codeTime} 秒
                                </span>)
                            }
                        </NavInput>
                    </InputBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe6f6;</i>
                            <input placeholder='请输入邀请码' type='text' value={invite} ref={inviteInput} onChange = { changeInvite } />
                            <span className='inputTpye active2' >(邀请码)</span>
                        </NavInput>
                    </InputBox>
                    {
                        needPass ? (<>
                            <InputBox className='dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe60f;</i>
                                    <input placeholder='请输入密码' type='password' value={pass} ref={passInput} onChange={changePass} />
                                </NavInput>
                            </InputBox>
                            <InputBox className='dva-after-bottom'>
                                <NavInput>
                                    <i className='iconfont'>&#xe60f;</i>
                                    <input placeholder='请确认密码' type='password' value={opass} ref={opassInput} onChange={changeOpass} />
                                    {
                                        ispassCon ? (<span className='inputTpye active2 help-color'>
                                            <i className='iconfont'>&#xe6a7;</i>
                                            密码不一致
                                        </span>) : null
                                    }
                                </NavInput>
                            </InputBox>
                        </>) : null
                    }
                    <TaoCode>
                        <div className='title box-warp'>
                            解析旺旺号 <span className='desp'>, 再验证旺旺号</span>
                        </div>
                        <div className='cell'>
                            <div className='cell-title'>①、淘口令解析 <span className='help' onClick={() => { handlePopup('tao') }}>如何获取淘口令？</span></div>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs>
                                        <IconWarp>
                                            <i className='iconfont'>&#xe6f5;</i>
                                        </IconWarp>
                                        <input placeholder='请复制淘口令' type='text' value={TwordValue} ref={TwordInput} onChange = { changeTword } />
                                        <div className='clear' onClick={clearTword}>
                                            <i className='iconfont'>&#xe68c;</i>
                                        </div>
                                    </InputBoxs>
                                    <InputBtn onClick={ TwordTest }>解析</InputBtn>
                                </InputWarp>
                            </HandleWarp>
                        </div>
                        {/* <div className='cell'>
                            <div className='cell-title'>②、旺旺号图片识别 <span className='help' onClick={() => { handlePopup('shibie') }}>如何获取旺旺号图片？</span></div>
                            <div className='pading'>
                                <div className='upload-warp'>
                                    <div onClick={handleUpload} className='upload-img-box'>
                                        <div className='img-box'>
                                            <img className={wwImg ? '' : 'default-img'} src={wwImg ? (PathImg(wwImg)) : null} alt="" onClick={() => { return false }} />
                                        </div>
                                    </div>
                                    {
                                        wwImg ? (null) : (<div className='big-box' style={{ color: 'rgba(0,0,0,.65)' }}>暂无上传图片</div>)
                                    }
                                </div>
                            </div>
                        </div> */}
                        <div className='title box-warp'>验证旺旺号</div>
                        <div className='cell'>
                            <HandleWarp>
                                <InputWarp>
                                    <InputBoxs className='dva-ellipsis' style={{ border: 'none' }}>
                                        解析的旺旺号：<span style={{ color: '#409eff' }}>{ww}</span>
                                    </InputBoxs>
                                    {
                                        wwTest ? (<span style={{ color: '#67C23A' }}>验号已通过</span>) : (<InputBtn style={{ borderRadius: 5 }} onClick={ handleAtTest }>验号</InputBtn>)
                                    }
                                </InputWarp>
                            </HandleWarp>
                        </div>
                    </TaoCode>
                    <StartUse className='active' onClick={ handleStart } >提交注册</StartUse>
                    <ForgetBox>
                        <div>
                            有账号！
                            <Link to='/login' className='to-rigister davBold' style={{marginLeft: 5}}>
                                去登录
                            </Link>
                        </div>
                    </ForgetBox>
                </LoginBox>
            </LoginWarpper>
        </Container>

        <input
            type='file'
            id='uploadInput'
            accept='image/*'
            style={{ display: 'none' }}
            ref={fileInput}
            onChange={(info) => { changeFile(info) }}
        ></input>
        <Modal
            popup
            visible={popupShow}
            onClose={() => { popupCloose() }}
            animationType="slide-up"
            afterClose={() => { popupCloose() }}
        >
            <div className='popup-warp' style={{ height: '80vh' }}>
                {
                    popupType === 'shibie' ? (<>
                        <div className='popup-title'>获取旺旺号图片流程</div>
                        <div className='popup-body'>
                            <div className='popup-box'>
                                <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击右上角设置图标进入设置页面</p>
                                <img src="/imgs/tb1.png" alt="" style={{ width: '100%' }} />
                            </div>
                            <div className='popup-box'>
                                <p className='desc'>二、进入设置页面后，点击 <span>账户与安全</span> 进入账户与安全页面</p>
                                <img src="/imgs/tb2.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                            <div className='popup-box'>
                                <p className='desc'>三、进入账户与安全页面后，截图保存图片并上传即可</p>
                                <img src="/imgs/tb3.png" alt="" style={{ width: '100%' }} />
                            </div>
                        </div>
                    </>) : (popupType === 'tao' ? (<>
                        <div className='popup-title'>获取淘口令流程</div>
                        <div className='popup-body'>
                            <div className='popup-box'>
                                <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击右上角通讯录图标进入通讯录页面</p>
                                <img src="/imgs/tao1.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                            <div className='popup-box'>
                                <p className='desc'>二、进入通讯录页面后，点击右上角的 <span>添加好友</span> 图标进入添加好友页面</p>
                                <img src="/imgs/tao2.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                            <div className='popup-box'>
                                <p className='desc'>三、进入添加好友页，点击 <span>我的淘口令</span> 会弹出一个分享的弹出层</p>
                                <img src="/imgs/tao3.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                            <div className='popup-box'>
                                <p className='desc'>四、点击弹出层的 <span>复制链接</span>，即可复制淘口令</p>
                                <img src="/imgs/tao4.jpg" alt="" style={{ width: '100%' }} />
                            </div>
                        </div>
                    </>) : (popupType === 'mi' ? (<>
                        <div className='popup-title'>获取小蜜问候语流程</div>
                        <div className='popup-box'>
                            <img src="/imgs/mi.jpg" alt="" style={{ width: '100%' }} />
                        </div>
                    </>) : (popupType === 'level' ? (<>
                        <div className='popup-title'>获取等级图流程</div>
                        <div className='popup-body'>
                            <div className='popup-box'>
                                <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击 <span>评价</span> 进入评价中心页面</p>
                                <img src="/imgs/ping1.png" alt="" style={{ width: '100%' }} />
                                <p className='desc'>二、进入评价中心页面后，点击 <span>全部评价</span> 进入我的评价页面</p>
                                <img src="/imgs/ping2.jpg" alt="" style={{ width: '100%' }} />
                                <p className='desc'>三、进入我的评价页面，保证图中包含等级信息，截图保存即可</p>
                                <img src="/imgs/ping3.jpg" alt="" style={{ width: '100%' }} />
                            </div>

                        </div>
                    </>) : (popupType === 'age' ? (<>
                        <div className='popup-title'>获取淘龄图流程</div>
                        <div className='popup-body'>
                            <div className='popup-box'>
                                <p className='desc'>一、打开淘宝主页，点击底部导航栏 <span>我的淘宝</span> 进入我的淘宝页面，点击 <span>成就奖励</span> 进入成就页面</p>
                                <img src="/imgs/age1.png" alt="" style={{ width: '100%' }} />
                                <p className='desc'>二、进入成就页面，保证图中包含注册天数信息，截图保存即可</p>
                                <img src="/imgs/age2.jpg" alt="" style={{ width: '100%' }} />
                            </div>

                        </div>
                    </>) : (null)))))
                }
            </div>
        </Modal>
    </>)
}

export default withRouter(Register)