import React, { Fragment, useState, useEffect, useRef } from 'react';
import { ContenWarp, ProTitle } from './style'
import { DvaHeader } from '../../common/Template'
import { WhiteSpace, Toast } from 'antd-mobile';
import { Link, useHistory  } from 'react-router-dom'
import { getUserInfo } from '../../common/Js/glabalAxios'
import { Axios } from '../../common/Axios'

const JFList = (props) => {
    let history = useHistory();

    useEffect(()=> {
        getUserInfo((data)=>{
            console.log(data.resultdata)
        })

        Axios('get', '/GetJFLevel',  {
            token: JSON.parse(localStorage.getItem('user_token')),
        }).then(data => {
            // console.log(data)
            if(data.type == 1) {
                setJfOrder(data.resultdata)
            }
        }).catch(err => {

        })

    }, [])
    
    const [list, setList] = useState([])
    const [jfOrder, setJfOrder] = useState({})
    const myInput = useRef(null)

    return (<Fragment>
        <DvaHeader Left={true} Title={'积分详情'} zIndex={99}></DvaHeader>
        <ContenWarp>
            <ProTitle>当前积分 <span>{jfOrder.JFCount}</span> ,   还需要 <span>{jfOrder.JFMax - jfOrder.JFCount}</span> 积分值到达LV <span>{jfOrder.JFLevel + 1}</span></ProTitle>
            <div className='tab-title'>
                <div className = "type tab-box">兑换类型</div> 
                <div className = "mask tab-box">发货状态</div>
                <div className = "gift tab-box">兑换礼品</div>
                <div className = "time tab-box">兑换时间</div>
                <div className = "more tab-box">查看详细</div>
            </div>
            {
                list.map((item, index) => {
                    return (
                        <div className='tab-body' key={index}>
                            <div className={index % 2 ? ("type item tab-box") : ("type item active tab-box")}>等级{item.level}礼包</div>
                            <div className={index % 2 ? ("mask tab-box") : ("mask active tab-box")}>{item.record.IsDeliver == 0 ? '未发货' : '已发货'}</div>
                            <div className={index % 2 ? ("gift tab-box") : ("gift active tab-box")}>{item.record.ReceiveGiftName}</div>
                            <div className={index % 2 ? ("time tab-box") : ("time active tab-box")}>{item.record.CreateTime}</div>
                            <div className={index % 2 ? ("more tab-box") : ("more active tab-box")}>查看</div>
                        </div>
                    )
                })
            }
        </ContenWarp>
    </Fragment>)
}

export default JFList