import React, { Fragment, useState, useEffect, useRef } from 'react';
import { ContenWarp, Tip, PaginationWarp } from './style'
import { DvaHeader } from '../../../../../../common/Template'
import { WhiteSpace, Button, Modal, Toast } from 'antd-mobile';
import { Link, useHistory, Redirect } from 'react-router-dom'
import { Axios } from '../../../../../../common/Axios'
import { List } from 'immutable';
import { Empty, Pagination, Input } from 'antd';

const Ext = (props) => {
    const alert = Modal.alert;
    let history = useHistory();

    useEffect(()=> {
        getUserInfo(data => { 
            setUser(data) 
        })
        request('', '', 1, '', 1, (res)=>{
            setList(res.resultdata.list)
            setTotal(res.resultdata.page.records)
            // setTotal(50)
        })
        GetUnderCount(res => {
            setFenInfo(res.resultdata)
        })
    }, [])

    const GetUnderCount = (callback) => {
        Axios('get','/GetUnderCount',{}).then(res =>{
            callback(res)
        }).catch(err =>{
    
        })
    }

    const getUserInfo = (callback) => {
        Axios('get','/GetBrusherInfo',{}).then(data =>{
            localStorage.setItem('user_info',JSON.stringify(data.resultdata))
            callback(data.resultdata)
        }).catch(err =>{
    
        })
    }

    const request = (orderBy, sord, pno, search, status, callback) => {
        Axios('get','/GetUnderBrushers',{
            token: JSON.parse(localStorage.getItem('user_token')),
            isEnable: 1,
            nowPage: pno,
            userName: search,
            orderBy,
            sord,
            status
        }).then(res =>{
            callback(res)
        }).catch(err =>{
    
        })
    }

    const handleSord = (int) => {
        let _sord = ''
        if(int === orderBy){
            if(sord === 'desc') {
                _sord = 'asc'
            }else if(sord === 'asc') {
                _sord = 'desc'
            }else{
                _sord = 'desc'
            }
        }else{
            _sord = 'desc'
        }
        setOrderBy(int)
        setSord(_sord)
        request(int, _sord, page, search, type, (res)=>{
            setList(res.resultdata.list)
            setTotal(res.resultdata.page.records)
        })
    }

    const itemRender = (current, type, originalElement) => {
        if (type === 'prev') {
          return <a style={{color: "rgba(0, 0, 0, .65)", lineHeight: '16px'}}>上一页</a>;
        }
        if (type === 'next') {
          return <a style={{color: "rgba(0, 0, 0, .65)", lineHeight: '16px'}}>下一页</a>;
        }
        return originalElement;
    }

    const changePno = (page) => {
        request(orderBy, sord, page, search, type, (res)=>{
            setList(res.resultdata.list)
            setTotal(res.resultdata.page.records)
            setPage(page)
        })
    }

    const changeSreach = () => {
        let inputVal = searchInput.current.value
        setSearch(inputVal)
    }

    const handleSreach = () => {
        request('', '', 1, search, 1, (res)=>{
            setList(res.resultdata.list)
            setTotal(res.resultdata.page.records)
            setSord('')
            setOrderBy('')
            setPage(1)
            setType(1)
        })
    }

    const reviseExt = (index) => {
        setSelcetIndex(index)
        setSelcetName(list[index].RealName)
        setSelcetExt(list[index].ExtraMoney2)
        setTimeout(()=>{
            setExtShow(true)
        }, 0)
    }

    const ExtClose = () =>{
        setExtShow(false)
    }

    const ExtConfiram = () => {
        let id = list[selcetIndex].UserId
        Axios('get', '/UpdateExtraMoney', {
            userId: id,
            ExtraMoney: selcetExt
        }).then(res => {
            if(res.type === 1) {
                Toast.success(res.message, 2, null, false)
                list[selcetIndex].ExtraMoney2 = selcetExt
                setList(list)
                setExtShow(false)
            }else{
                Toast.fail(res.message, 2, null, false)
                setExtShow(false)
            }
        }).catch(err => {
            Toast.info(err.message, 2, null, false)
            setExtShow(false)
        })
    }

    const changeRevise = (e) => {
        let inputVal = e.target.value
        setSelcetExt(inputVal)
    }

    const changeType = (int) => {
        setType(int)
        request('', '', 1, search, int, (res)=>{
            setList(res.resultdata.list)
            setTotal(res.resultdata.page.records)
            setSord('')
            setOrderBy('')
            setPage(1)
        })
    }

    const requestRote = (UserId) => {
        Axios('get', '/UpdateUnderXSF', {
            UserId
        }).then(res => {
            if(res.type === 1) {
                Toast.success(res.message, 2, null, false)
                request(orderBy, sord, page, search, type, (res)=>{
                    setList(res.resultdata.list)
                })
            }else{
                Toast.fail(res.message, 2, null, false)
            }
        }).catch(err => {
            Toast.info(err.message, 2, null, false)
        })
    }

    const EnabledMarkRequest = (UserId) => {
        Axios('get', '/AuditUser', {
            UserId
        }).then(res => {
            console.log(res)
            if(res.type === 1) {
                Toast.success(res.message, 2, null, false)
                request(orderBy, sord, page, search, type, (res)=>{
                    setList(res.resultdata.list)
                })
            }else{
                Toast.fail(res.message, 2, null, false)
            }
        }).catch(err => {
            Toast.info(err.message, 2, null, false)
        })
    }

    const EnabledMarkRender = (status, id, name) => {
        if(status === 0) {
            return (<span style={{padding: '5px 3px', background: '#67c23a', borderRadius: 5}} onClick={()=>{
                alert('提示', `是否对${name}审核通过???`, [
                    { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                    { text: '确定', onPress: () => { EnabledMarkRequest(id) } },
                ])
            }}>审核通过</span>)
        }else if(status == 3) {
            return (<span style={{padding: '5px 3px', background: '#f76a24', borderRadius: 5}} onClick={()=>{
                alert('提示', `是否对${name}取消审核???`, [
                    { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                    { text: '确定', onPress: () => { EnabledMarkRequest(id) } },
                ])
            }}>取消审核</span>)
        }else {
            return <span>正常启用</span>
        }
    }

    const removeUser = (id) => {
        Axios('get', '/RemoveUnderUser', {
            brusherId: id
        }).then(res => {
            Toast.success(res.message, 2, null, false)
            request(orderBy, sord, page, search, type, (res)=>{
                setList(res.resultdata.list)
            })
        }).catch(err => {
            Toast.info(err.message, 2, null, false)
        })
    }

    const searchInput = useRef(null)
    const popupInput = useRef(null)
    const [user, setUser] = useState({})
    const [list, setList] = useState([])
    const [total, setTotal] = useState(0)
    const [page, setPage] = useState(1)
    const [orderBy, setOrderBy] = useState(null)
    const [sord, setSord] = useState(null)
    const [search, setSearch] = useState(null)
    const [selcetIndex, setSelcetIndex] = useState(-1)
    const [selcetName, setSelcetName] = useState('')
    const [selcetExt, setSelcetExt] = useState(null)
    const [extShow, setExtShow] = useState(false)
    const [fenInfo, setFenInfo] = useState({})
    const [type, setType] = useState(1)

    if(!localStorage.getItem('user_token')) {
        return <Redirect to='/login' />
    }

    return (<Fragment>
        <Modal
            visible={extShow}
            transparent
            maskClosable={false}
            title={`对 ${selcetName} 提成进行修改`}
            footer={[
                { text: '取消', onPress: () => { ExtClose() } },
                { text: '确认', onPress: () => { ExtConfiram() } },
            ]}
        >
            <div>
                <Input
                    type='number'
                    value={selcetExt}
                    ref={popupInput}
                    onChange={(e)=>{changeRevise(e)}}
                />
            </div>
        </Modal>

        <DvaHeader Left={true} Title={'分享朋友'} zIndex={99}></DvaHeader>
        <ContenWarp>
            <Tip>
                {
                   (user.IsYWY === 1 ? '您是大师傅，' : (user.IsYWY === 2 ? '您是小师傅，' : null )) + `您的一级提成${user.ExtraMoney } ，二级抽成${user.SelfMoney }。`
                }
                {/* {
                    user.IsYWY == 1 ? (`您是大师傅: 每单抽成:${user.SelfMoney} , 下属推荐人每单默认提成: ${user.ExtraMoney}`) : (`您推荐的人每完成一单就会给您带来 ${user.ExtraMoney} 的提成`)
                } */}
            </Tip>
            <div className='nav-warp'>
                <div className='nav-box'>
                    <div className='sum'>{fenInfo.新增用户下单}</div>
                    <div className='text'>新增用户下单</div>
                </div>
                <div className='nav-box'>
                    <div className='sum'>{fenInfo.今日累计下单}</div>
                    <div className='text'>今日累计下单</div>
                </div>
                <div className='nav-box'>
                    <div className='sum'>{fenInfo.三十日累计结算}</div>
                    <div className='text'>30日内累计结算</div>
                </div>
            </div>
            <div className='term'>
                <div className='term-cell' onClick={()=>{ changeType(1) }}>
                    <div className={type === 1 ? 'type-text selected' : 'type-text'}>全部粉丝</div>
                    <div className='type-sum'>{fenInfo.全部粉丝}</div>
                </div>
                {/* <div className='term-cell border-left' onClick={()=>{ changeType(2) }}>
                    <div className={type === 2 ? 'type-text selected' : 'type-text'}>直属粉丝</div>
                    <div className='type-sum'>{fenInfo.直属粉丝}</div>
                </div> */}
                <div className='term-cell border-left' onClick={()=>{ changeType(3) }}>
                    <div className={type === 3 ? 'type-text selected' : 'type-text'}>今日新增</div>
                    <div className='type-sum'>{fenInfo.今日新增}</div>
                </div>
            </div>
            <div className='search-warp'>
                <div className='input-warp'>
                    <input type="text" placeholder='请输入姓名或手机号进行搜索' ref={searchInput} onChange={changeSreach} />
                </div>
                <div className='search-btn' onClick={handleSreach}>查询</div>
            </div>
            <PaginationWarp>
                <div className='total-div'>共{total}条</div>
                <Pagination defaultCurrent={1} total={total} defaultPageSize={5} pageSize={5} current={page} onChange={(pno) => { changePno(pno) }} itemRender={itemRender} simple />
            </PaginationWarp>
            <div className='table-warp'>
                <div className='title'>
                    <div className='tatle-box RealName'>
                        <span>姓名/<span style={{color: '#67C23A'}}>旺旺号</span>/<span style={{color: '#E6A23C'}}>手机号</span></span>
                    </div>
                    <div className='tatle-box CreateDate' onClick={()=>{ handleSord('CreateDate') }}>
                        <div>注册时间</div>
                        <div className='icon-warp'>
                            {
                                orderBy === 'CreateDate' ? (<>
                                    {
                                        sord === 'desc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72e;</i>) : (sord === 'asc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72f;</i>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>))
                                    }
                                </>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>)
                            }
                        </div>
                    </div>
                    <div className='tatle-box RecommendUserCount' onClick={()=>{ handleSord('RecommendUserCount') }}>
                        <div>下级数量</div>
                        <div className='icon-warp'>
                            {
                                orderBy === 'RecommendUserCount' ? (<>
                                    {
                                        sord === 'desc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72e;</i>) : (sord === 'asc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72f;</i>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>))
                                    }
                                </>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>)
                            }
                        </div>    
                    </div>
                    <div className='tatle-box TotalOrder' onClick={()=>{ handleSord('TotalOrder') }}>
                        <div>做单总量</div> 
                        <div className='icon-warp'>
                            {
                                orderBy === 'TotalOrder' ? (<>
                                    {
                                        sord === 'desc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72e;</i>) : (sord === 'asc' ? (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe72f;</i>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>))
                                    }
                                </>) : (<i className='icon iconfont' style={{ fontSize: 12, marginLeft: 5, fontWeight: 700 }}>&#xe663;</i>)
                            }
                        </div>
                    </div>
                    {
                        user.IsYWY === 1 ? (<div className='tatle-box RecomendUserName'>
                            <span>小师傅</span>
                        </div>) : null
                    }
                    <div className='tatle-box EnabledMark'  onClick={()=>{ handleSord('EnabledMark') }}>
                        <span>设置状态</span>
                    </div>
                </div>
                {
                    list.length ? null : (<Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={'暂无数据'} />)
                }
                {
                    list.map((item, index) => {
                        return (<span key={index}>
                            <div className='list' className={index % 2 ? ("list") : ("list active")}>
                                <div className={index % 2 ? ("list-box RealName") : ("list-box RealName active")}>
                                    <div>
                                        <span style={{ color: '#EC503F' }}>{item.RealName}</span> / <span style={{ color: '#67C23A' }}>{item.AliAccount}</span><br /><span style={{ color: '#E6A23C' }}>{item.Mobile}</span> 
                                        <div style={{marginTop: 5}}>
                                            <span style={{background: '#ec503f', padding: '2px 5px', borderRadius: 5}}
                                                onClick={()=>{
                                                    alert('警告，用户删除后相关资料不可恢复', `是否对${item.RealName}进行删除???`, [
                                                        { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                                                        { text: '确定', onPress: () => { removeUser(item.UserId) } },
                                                    ])
                                                }}
                                            >删除账号</span>
                                        </div>
                                    </div>
                                </div>
                                <div className={index % 2 ? ("list-box CreateDate RealName") : ("list-box CreateDate RealName active")}>
                                    <div>{item.CreateDate}</div>
                                </div>
                                <div className={index % 2 ? ("list-box RecommendUserCount RealName") : ("list-box RecommendUserCount RealName active")}>
                                    <div>{item.RecommendUserCount}</div>
                                </div>
                                <div className={index % 2 ? ("list-box TotalOrder RealName") : ("list-box TotalOrder RealName active")}>
                                    <div>{item.TotalOrder}</div>
                                </div>
                                {
                                    user.IsYWY == 1 ? (<div className={index % 2 ? ("list-box RecomendUserName RealName") : ("list-box RecomendUserName RealName active")}>
                                        {
                                            item.IsYWY === 2 ? (<span className='revise-btn default' onClick={()=>{
                                                alert('提示', `是否取消${item.RealName}的小师傅???`, [
                                                    { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                                                    { text: '确定', onPress: () => { requestRote(item.UserId) } },
                                                ])
                                            }}>取消</span>) : (<span className='revise-btn' onClick={()=>{
                                                alert('提示', `是否授权${item.RealName}为小师傅???`, [
                                                    { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                                                    { text: '确定', onPress: () => { requestRote(item.UserId) } },
                                                ])
                                            }}>授权</span>)
                                        }
                                        
                                    </div>) : null
                                }
                                
                                <div className={index % 2 ? ("list-box ExtraMoney2 RealName") : ("list-box ExtraMoney2 RealName active")} >
                                    {EnabledMarkRender(item.EnabledMark, item.UserId, item.RealName)}
                                </div>
                            </div>
                        </span>)
                    })
                }
            </div>

            {
                user.CanInviteTimes > 0 ? (<div className='bottom-warp' onClick={() => {  
           history.push('/invite'); }}>
                    <div className='bottom-btn' >邀请好友</div>
                </div>) : null
            }
        </ContenWarp>
    </Fragment>)
}

export default Ext