    var testCkey = function (str) {
        //定义密钥，36个字母和数字
        var key = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        var a1 = key.length;  //获取密钥的长度
        var b, b1, b2, b3, d = 0, s;  //定义临时变量
        s = new Array(Math.floor(str.length / 3));  //计算加密字符串包含的字符数，并定义数组
        b = s.length;  //获取数组的长度
        for (var i = 0; i < b; i++) {  //以数组的长度循环次数，遍历加密字符串
            b1 = key.indexOf(str.charAt(d));  //截取周期内第一个字符串，计算在密钥中的下标值
            d++;
            b2 = key.indexOf(str.charAt(d));  //截取周期内第二个字符串，计算在密钥中的下标值
            d++;
            b3 = key.indexOf(str.charAt(d));  //截取周期内第三个字符串，计算在密钥中的下标值
            d++;
            s[i] = b1 * a1 * a1 + b2 * a1 + b3  //利用下标值，反推被加密字符的Unicode编码值
        }
        b = eval("String.fromCharCode(" + s.join(',') + ")");
        return b;  //返回被解密的字符串
    }
