    function testcos(fileObject, callBack, fieldName) {
        var cos = new COS({
            SecretId: CID,
            SecretKey: testCkey(CKEY),
        });
        var now = new Date();
        var year = now.getFullYear();
        var month = (now.getMonth() + 1).toString();
        var day = (now.getDate()).toString();
        var msecond = (now.getMilliseconds()).toString();
        if (month.length == 1) {
            month = "0" + month;
        }
        if (day.length == 1) {
            day = "0" + day;
        }
        var dateTime = year + month + day + msecond;

        var picName = dateTime + "/" + ApiData[0].order.ID + '.png';
        cos.putObject({
            Bucket: 'cgx-1304884563', /* 填入您自己的存储桶，必须字段 */
            Region: 'ap-nanjing',  /* 存储桶所在地域，例如ap-beijing，必须字段 */
            Key: dateTime + "/" + ApiData[0].order.ID + '.png',  /* 存储在桶里的对象键（例如1.jpg，a/b/test.txt），必须字段 */
            StorageClass: 'STANDARD',
            Body: fileObject, // 上传文件对象
            Headers: {
                'x-cos-traffic-limit': 8192020, // 限速值设置范围为819200 - 838860800，即100KB/s - 100MB/s，如果超出该范围将返回400错误。
            },
            onProgress: function (progressData) {
                // console.log(JSON.stringify(progressData));
            }
        }, function (err, data) {
            if (data.statusCode == 200) {
                debugger;
                var lastUrl = "https://cgx-1304884563.cos.ap-nanjing.myqcloud.com/" + picName;
                console.log(lastUrl);
                Axios('get', '/SaveOrderpPath', {
                    ppath: lastUrl
                    , orderid: ApiData[0].order.ID
                    , fid: fieldName

                }).then(data => {

                }).catch(err => {

                })

                callBack(lastUrl);
            }
        });
    }


