    window.TestIsFSKW = function (kw) {
        if (kw.length > 50 || kw.indexOf(".") >= 0 || kw.indexOf("\\") >= 0
            || kw.indexOf("//") >= 0 || kw.indexOf("$") >= 0 || kw.toLowerCase().indexOf("resource") >= 0 || (kw.length > 10 && kw.toLowerCase().indexOf("resource") >= 0)
            || kw.toLowerCase().indexOf("http") >= 0 || kw.indexOf("?") >= 0) {
            return true;
        } else {
            //包含中文
            var reg = /[\u4E00-\u9FFF]+/,
                regEnUp = /[A-Z]+/,//大写字母
                regEnLow = /[a-z]+/,//小写字母
                regNum = /[0-9]+/;//数字
            if (reg.test(kw) && regEnUp.test(kw) && regEnLow.test(kw) && regNum.test(kw)) {
                return true;
            }

        }

        return false;
    }
