export const defaultImg = 'data:image/png;base64,'

export const myImgFn = (img) => {
    let str = ''
    if(img.indexOf("http") != -1) {
        str = img
    }else{
        let url = URL.IMG_URL
        str = url + img
    }
    return str
}

export const myImgPath = (img) =>{
    let str=''
    if(!img){
        return str
    }
    if(img.indexOf('http') >= 0){
        str = img
    }else{
        str = URL.IMG_URL + img 
    }
    return str
}

