export const keyRender = (str) => {
    if(!str) return '暂无关键词'
    let img = ''
    if(str.indexOf('http') >= 0){
        img = str
        return <img src={`${img}`} style={{width: 100}}></img>
    }else{
        if(str.indexOf('Resource') >= 0) {
            img = URL.IMG_URL + str
            return <img src={`${img}`} style={{width: 100}}></img>
        }else{
            return str
        }
        return str
    }
}

