export const PathImg = (img) => {
    let str = ''
    if(img) {
        img = img.split('.png')[0]
        if(img.indexOf(URL.IMG_URL) <0){
            str = URL.IMG_URL + img + '_120w.img'
        }else{
            str = img + '_120w.img'
        }
    }
    
    return str
}

export const PathImgSm = (img) => {
    let str = ''
    if(img) {
        img = img.split('.png')[0]
        if(img.indexOf(URL.IMG_URL) <0){
            str = URL.IMG_URL + img + '_360w.img'
        }else{
            str = img + '_360w.img'
        }
    }
    
    return str
}

export const PathImgBig = (img, defaultImg) => {
    let str = ''
    if(img) {
        
        if(img.indexOf('http') > -1) {
            str = img
        }else {
            if(img.indexOf(URL.IMG_URL) < 0){
                str = URL.IMG_URL + img
            }
        }
    }else{
        str = defaultImg
    }
    return str
}

export const yongFn = (price) => {
    if(price <= 200) {
        return 3
    }else if(price <= 500) {
        return 4
    }else if(price >= 800) {
        return 5
    }else
    {
        return 5;
    }
    console.log(price)
}