import JsonP from 'jsonp'

export default class Ajax {
    static jsonp(options){
        return new Promise((resolve,reject)=>{
            JsonP(options.url,{
                param:'callback'
            },function(err,res){
                resolve(res)
                // if(res.status === 'success') {
                //     resolve(res)
                // } else {
                //     reject(err)
                // }
            })
        })
    }
}