import styled, { keyframes } from 'styled-components'
import { ToRem } from '../../../../../../common/Js/remToPx'
import {MV} from '../../../../../../common/Css'

export const ContenWarp = styled.div`
    margin-top: ${ToRem(44)};
    font-size: ${ToRem(14)};
    min-height: calc(100vh);
    .nav-warp {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #EB525D;
        .nav-box {
            width: 33%;
            text-align: center;
            color: #fff;
            .sum {
                font-size: 18px;
                font-weight: 700;
                color: #fff;
            }
            .text {
                font-size: 14px;
                margin-top: 10px;
                font-weight: 500;
            }
        }
    }
    .term {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        .term-cell {
            width: 50%;
            text-align: center;
            font-size: 16px;
            padding: 0 10px;
            &.border-left {
                border-left: 1px solid #ddd;
            }
            .type-text {
                color: rgba(0,0,0,.65);
                padding: 3px 0;
                border-radius: 16px;
                &.selected {
                    color: #fff;
                    background-color: #dda535;
                }
            }
            .type-sum {
                margin-top: 10px;
                font-weight: 700;
                font-size: 18px;
            }
        }
    }
    .search-warp {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 40px;
        line-height: 40px;
        width: 95%;
        margin: 0 auto;
        .input-warp {
            position: relative;
            border: 1px solid rgb(220, 223, 230);
            border-right: none;
            border-image: initial;
            border-right: none;
            border-radius: 8px 0px 0px 8px;
            height: 100%;
            overflow: hidden;
            width: calc(100% - 75px);
            input {
                font-size: 16px;
                border: none;
                outline: none;
                padding: 0px;
                height: 40px;
                line-height: 40px;
                width: calc(100% - 10px);
                color: rgb(96, 98, 102);
                position: absolute;
                top: 0px;
                left: 10px;
            }
        }
        .search-btn {
            width: 75px;
            background-color: rgb(236, 80, 63);
            text-align: center;
            color: rgb(255, 255, 255);
            font-size: 16px;
            line-height: 40px;
            border-radius: 0px 8px 8px 0px;
        }
    }
    .table-warp {
        width: 100%;
        margin: 0 auto;
        padding-bottom: 50px;
        .title {
            width: 100%;
            display: flex;
            margin: 0px auto 0px;
            text-align: center;
            font-size: 12px;
            justify-content: space-between;
            background-color: #F8E7EC;
            padding: 10px 0 4px 0;
            .tatle-box {
                display: flex;
                align-items: center;
                flex-direction: column;
                line-height: 1.1;
                color: ${MV.dva_color};
                div {
                    width: 100%;
                }
                .icon-warp {
                    margin-top: 5px;
                    .icon {
                        font-size: 16px !important;
                    }
                }
                &.RealName {
                    width: 30%;
                }
                &.CreateDate {
                    width: 20%;
                }
                &.RecomendUserName {
                    width: 10%;
                }
                &.RecommendUserCount {
                    width: 15%;
                }
                &.TotalOrder {
                    width: 15%;
                }
                &.ExtraMoney2 {
                    width: 15%;
                }
            }
        }
        .list {
            width: 100%;
            display: flex;
            margin: 0px auto 0px;
            text-align: center;
            font-size: 12px;
            justify-content: space-between;
            background-color: #1E81C4;
            &.active {
                background-color: #0075C4 !important;
            }
            .list-box {
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-direction: column;
                line-height: 1.1;
                color: #fff;
                padding: 10px 0;
                background-color: #1E81C4;
                &.active {
                    background-color: #0075C4 !important;
                }
                div {
                    width: 100%;
                }
                &.RealName {
                    width: 30%;
                }
                &.CreateDate {
                    width: 20%;
                }
                &.RecomendUserName {
                    width: 10%;
                    .revise-btn {
                        display: block;
                        line-height: 18px;
                        padding: 2px 5px;
                        border-radius: 8px;
                        background: #dda535;
                        font-size: 12px;
                        &.default {
                            background: #f76a24;
                        }
                    }
                }
                &.RecommendUserCount {
                    width: 12.5%;
                }
                &.TotalOrder {
                    width: 12.5%;
                }
                &.ExtraMoney2 {
                    width: 15%;
                }
                .btn-warp {
                    height: 18px;
                    margin-top: 5px;
                    display: flex;
                    justify-content: center;
                    .revise-btn {
                        display: block;
                        height: 100%;
                        line-height: 18px;
                        padding: 0 10px;
                        border-radius: 8px;
                        background: #dda535;
                        font-size: 12px;
                    }
                }
            }
        }
    }
    .bottom-warp {
        position: fixed;
        width: 100%;
        padding: 0 ${ToRem(10)};
        padding-bottom: ${ToRem(5)};
        height: ${ToRem(40)};
        bottom: 0;
        left: 0;
        background: #fff;
        z-index: 100;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .bottom-btn {
            color: #fff;
            font-weight: 700;
            font-size: ${ToRem(18)};
            text-align: center;
            height: ${ToRem(35)};
            line-height: ${ToRem(35)};
            width: 100%;
            margin: 0 auto;
            border-radius: ${ToRem(8)};
            background-color: ${MV.dva_color};
            &.userType {
                width: 45%; 
            }
            &.extend {
                background: rgba(0,0,0,.4);
            }
        }
    }
`

export const Tip = styled.div`
    color: #f76a24;
    line-height: ${ToRem(16)};
    font-size: ${ToRem(14)};
    text-align: left;
    margin-bottom: ${ToRem(25)} ;
    padding: ${ToRem(10)} ${ToRem(5)};
    background-color: #FEFCEC;
    margin-bottom: ${ToRem(0)};
    span
    {
        font-size: ${ToRem(14)};
        color: #4d94ff;
    }
`

export const PaginationWarp = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
    .total-div {
        padding: 0 ${ToRem(8)};
        color: rgba(0, 0, 0, .65);
        font-size: ${ToRem(14)};
        line-height: ${ToRem(16)};
    }
    .ant-pagination-simple-pager {
        color: rgba(0, 0, 0, .65) !important;
        input {
            color: rgba(0, 0, 0, .65) !important;
        }
    }
`