import React, { useState, useEffect, useRef } from 'react';
import { useHistory, Link, withRouter, Redirect } from 'react-router-dom'
import { Toast, Modal } from 'antd-mobile';
import { Axios } from '../../common/Axios'
import URL from '../../common/Axios/NEW_URL'
import { DvaHeader } from '../../common/Template'
import { phoneTest } from '../../utils/test/index'

import {
    Container,
    LoginWarpper,
    LoginBox,
    InputBox,
    NavInput,
    StartUse,
    MsgWarp
} from './style'

const ChangePassNext = (props) => {
    let history = useHistory();
    const alert = Modal.alert;
    useEffect(() => {
        let types = props.match.params.type
        if (types === 'forget') {
            settitle('忘记密码')
        } else if (types === 'change') {
            settitle('修改密码')
        }
    }, [])

    const { location } = props
    const phoneInput = useRef(null)
    const codeInput = useRef(null)
    const [title, settitle] = useState('')
    const [active, setactive] = useState(false)
    const [phone, setphone] = useState('')
    const [code, setcode] = useState('')
    const [getCode, setgetCode] = useState(true)
    const [codeTime, setcodeTime] = useState(20)

    const changePhone = () => {
        let val = phoneInput.current.value
        setphone(val)
        changeActive(val, code)
    }

    const changeActive = (phone, code) => {
        if (phone && code) {
            setactive(true)
        } else {
            setactive(false)
        }
    }

    const changeCode = () => {
        let val = codeInput.current.value
        setcode(val)
        changeActive(phone, val)
    }

    const handleGetCode = () => {
        if (!phone) {
            Toast.info("手机号码不能为空", 2, null, false)
        } else if (!phoneTest.test(phone)) {
            Toast.info("手机号码格式不正确", 2, null, false)
        } else {
            Axios('get', '/GetMsgCode', {
                mobile: phone
            }).then(data => {
                Toast.info(data.message, 2, null, false)
                if (data.type == 1) {
                    setgetCode(false)
                    setcodeTime(20)
                    let sum = 20
                    let timer = setInterval(() => {
                        if (sum) {
                            sum--
                            setcodeTime(sum)
                        } else {
                            clearInterval(timer)
                            setgetCode(true)
                        }
                    }, 1000)
                }
            }).catch(err => {

            })
        }
    }

    const handleNext = () => {
        if(active) {
            Axios('get', '/CheckMsgCode', {
                mobile: phone,
                msgCode: code,
            }).then(data => {
                Toast.hide()
                if(data.type == 1) {
                    if(data.message === '短信验证码正确') {
                        Toast.hide()
                        Toast.success('验证成功', .5, null, false);
                        sessionStorage.setItem('jingping_revise_phone', JSON.stringify({
                            phone,
                            code
                        }))
                        setTimeout(()=>{
                            history.push('/changepassnext')
                        }, 500)
                    }else{
                        Toast.hide()  
                        Toast.fail(data.message, 2, null, false);
                    }
                }else{
                    Toast.hide()  
                    Toast.fail(data.message, 2, null, false);
                }
            }).catch(err => {
                Toast.hide()  
            })
        }
    }

    return (<>
        <DvaHeader Left={true} Title={title}></DvaHeader>
        <Container>
            <LoginWarpper>
                <LoginBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe6d6;</i>
                            <input type="number" value={phone} ref={phoneInput} onChange={changePhone} placeholder='请输入手机号' />
                        </NavInput>
                    </InputBox>
                    <InputBox className='dva-after-bottom'>
                        <NavInput>
                            <i className='iconfont'>&#xe635;</i>
                            <input type='text' value={code} ref={codeInput} onChange={changeCode} placeholder='请输入验证码' />
                            {
                                getCode ? (<span className='inputTpye' onClick={handleGetCode} >
                                    获取验证码
                                </span>) : (<span className='inputTpye avtiive'>
                                    {codeTime} 秒
                                </span>)
                            }
                        </NavInput>
                    </InputBox>
                    <StartUse className={active ? 'active' : ''} onClick={handleNext} >下一步</StartUse>

                </LoginBox>
            </LoginWarpper>
        </Container>
    </>)
}

export default withRouter(ChangePassNext)