import { fromJS } from 'immutable'
import { actionTypes } from './index'

const defaultState = fromJS({
    loginPass: '',
    loginUser: '',
    loginUserText: 'ccc    (银行卡姓名)',
    isUser: true, 
    isPass: true,
    isActive: false,
    pact: false,
    inpuType: 'password',
    isLogin: false,
    msg: '',
    SelectBank: null,
    SelectBankIndex: null,
    bankList: []
})

const changeStartState = (state, action)=>{
    return state.merge({
        loginUser: action.user,
        loginPass: action.pass,
        pact: action.pact,
        isActive: action.isActive,
        inpuType: action.inpuType,
        isPass: action.isPass,
        isUser: action.isUser
    })
}

export default (state=defaultState,action)=>{
    switch (action.type){
        case actionTypes.SET_BANK_LIST:
            return state.set('bankList', action.list)
        case actionTypes.LOGIN_PASS:
            return state.set('loginPass', action.value)
        case actionTypes.LOGIN_USER:
            return state.set('loginUser', action.value)
        case actionTypes.PASS_FOUCS:
            return state.set('isPass', false)
        case actionTypes.PASS_BLUR:
            return state.set('isPass', true)
        case actionTypes.USER_FOUCS:
            return state.set('isUser', false)
        case actionTypes.USER_BLUR:
            return state.set('isUser', true)
        case actionTypes.START_ON:
            return state.set('isActive', true)
        case actionTypes.START_OFF:
            return state.set('isActive', false)
        case actionTypes.PACT_CHECKBOX:
            return state.set('pact', action.value)
        case actionTypes.INPUT_TYPE:
            return state.set('inpuType', action.value)
        case actionTypes.START_STATE:
            return changeStartState(state, action)
        case actionTypes.IS_LOGIN:
            return  state.set('isLogin', action.value)
        case actionTypes.MSG:
            return  state.set('msg', action.value)
        case actionTypes.SET_SELECT_BANK:
            return  state.set('SelectBank', action.value)
        case actionTypes.SET_SELECT_BANK_INDEX:
            return  state.set('SelectBankIndex', action.value)
        default:
            return state   
    }
}   