export const giftIndexAction = (index, list) => {
    return (dispatch) => {
        // console.log(index, list)
        for (let i = 0; i < list.length; i++) {
            const el = list[i];
            el.check = false
        }
        list[index].check = true
        dispatch(setGiftIndex(list))
    }
}

