const GetTaskListFlitter = (list) => {
    for (let i = 0; i < list.length; i++) {
        const el = list[i];
        el.Switch = true
        el.id = el.RelationId
        el.title = el.ShopName
        el.title_info = el.内部店铺名
        el.wangwnag = el.BrusherAliAccount
        el.is_stop = el.tEnabledMark
        el.fact_price = el.TaskSelfMoney
        el.must_price = el.NeedPayMoney
        el.time = el.DoneTime
        el.IsSure=el.IsSure
    }
    return list
}

export const searchAction = (value, date, pno, Status) =>{
    return (dispatch) => {
        // console.log(date)
        let _url = sessionStorage.getItem('jingping_request')
        Toast.loading(`加载中... ${IPfilter(1)}`, 0, null, true);
        // return
        if(!pno) pno = 1
        if(!date) {
            const nowTimeStamp = Date.now();
        
            const date = new Date(nowTimeStamp);
        }
        if(!Status) Status = 0
        if(!value) value = ''
        dispatch(setPno(pno))
        Axios('getTask', '/GetTaskListNew', {
            token: JSON.parse(localStorage.getItem('user_token')),
            CurrentTabIndex: 0,
            IsNoPic: 0,
            ChangeDate: date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate(),
            SearchAccount: value,
            nowPage: pno,
            Status,
        }).then(data => {
            Toast.hide()
            if(data.type === 1) {
                if(data.resultdata){
                    console.log(data.resultdata)
                    let newlist = GetTaskListFlitter(data.resultdata.list)
                    dispatch(setList(newlist))
                    dispatch(setTotalNum("总记录数："+data.resultdata.page.records
                    +" ;应付款："+data.resultdata.countMoney.NeedPayMoneyAll
                    +"  ;实付款："+data.resultdata.countMoney.TaskSelfMoneyAll
                    ))

              //  Alert(data.resultdata.countMoney.TaskSelfMoneyAll);
                }
                // else{
                //     Toast.info(data.message, 2, null, false);
                // }
                
            }
        }).catch(err => {
            
        })
    }
}

