const TaskInterval = (UserId) => {
    return;
        window.Tasktimer = setTimeout(()=>{
        console.log(UserId)
        Axios('get', '/IsHaveTask', {
            UserID: UserId
        }).then(data =>{
            if(data.message) {  
                alert('您有待操作任务!')
                clearTimeout(window.Tasktimer)
            }else {
                TaskInterval(UserId) 
            }
        }).catch(err =>{
            
        })
    }, 10000)
}


