const setList = (list) =>({
    type: actionTypes.SET_LIST,
    list: fromJS(list)
})

export const setListSwitch = (index, list) => {
    return (dispatch) => {
        for (let i = 0; i < list.length; i++) {
            const el = list[i];
            if(i === index){
                list[index].Switch = !list[index].Switch
            }else{
                el.Switch = false
            }
        }
        dispatch(setList(list))
    }
}

