export const giftBuyAction = (initalIndex, list) => {
    return (dispatch) => {
        let id = null
        for (let i = 0; i < list.length; i++) {
            const el = list[i];
            if(el.check) {
                id = el.id
                break;
            }
        }
        // console.log('选择的商品ID为：' + id , 'initalIndex为：' + initalIndex)
        // 关闭弹窗,请求数据
        dispatch(setPopupShow(false))
        if(initalIndex === 0){  //当前为淘宝标签页
            setTimeout(()=>{
                dispatch(setTabLoading(false))

            }, 500)
            dispatch(setTabLoading(true))
        }else if(initalIndex === 1){  //当前为任务标签页
            setTimeout(()=>{
                dispatch(setTabTask(false))

            }, 500)
            dispatch(setTabTask(true))
        }
    }
}

export const changeTab = (index, nav)=>{
    return (dispatch)=>{
        nav.home.index = index
        sessionStorage.setItem('nav_data', JSON.stringify(nav))

        dispatch(setInitalIndex(index))

        if(index === 0) {  //手机淘宝 --请求淘宝接口 并修改数据
            dispatch(setTabLoading(true))
            setTimeout(()=>{
                dispatch(setTabLoading(false))
                let list = [{
                    gid: 5,
                    ID: '8746415645614',
                    good_img: 'http://119.23.218.210:4000/imgs/shop_logo/2bd2fc97055998ad1e37f18b365787c4.png@800w_800h.webp',
                    title: 'BANPRESTO（眼镜厂）偶像大师 灰姑娘女孩 安部菜菜 景品手办 EXQ系列',
                    mision: 1,
                    price: 190,
                    type: '活动垫款',
                    isShow: false
                }]
                dispatch(setTBlist(list))
            },1500)
        }else {   //预售任务 --请求预售接口 并修改数据
            dispatch(setTabTask(true))
            setTimeout(()=>{
                dispatch(setTabTask(false))
                let list = [{
                    gid: 1,
                    ID: '4841415645615648',
                    good_img: 'http://119.23.218.210:4000/imgs/shop_logo/6203f850744b67169c9c2d77e29a77d3.png@800w_800h.webp',
                    title: 'ALTER 偶像大师 灰姑娘女孩 鹰富士茄子 强运才女Ver. 手办',
                    mision: 1,
                    price: 180,
                    type: '活动垫款',
                    isShow: false
                }]
                dispatch(setTasklist(list))
            },1500)
        }
        
    }
}

