export const changeLinkTo2 = (userInfo, history, dispatch) => {
    let status = true
    let str = ''
    // if(userInfo.EnabledMark == -1) {
    //     status = false 
    //     Alert('该账号已被禁用')
    //     return
    // }
    let CreateUserName = userInfo.CreateUserName
    if (CreateUserName === '正品王紫发' || CreateUserName === '小明') {
        status = true
    }else if (CreateUserName !== '极品王紫发') {
        if (userInfo.EnabledMark == 2) {
            status = false
            Alert('您的账户信息审核不通过')
            return
        }

        if (userInfo.IsNeedUpSFZ === 1) {
            if (!userInfo.IDCardFront) {
                status = false
                str += '身份证反面照片、'
            }
            if (!userInfo.IDCardBack) {
                status = false
                str += '身份证正面照片、'
            }
            if (!status) {
                if (userInfo.EnabledMark == 1) {
                    Alert('您的身份证信息未填写完整，请先填写', '', [
                        { text: '取消', onPress: () => { }, style: 'default' },
                        { text: '去填写', onPress: () => history.push('/basic') },
                    ])
                    return
                }
            }

        }

        if (!userInfo.RealName) {
            status = false
            str += '用户姓名、'
        }
        if (!userInfo.BankCardNum) {
            status = false
            str += '银行卡号、'
        }
        if (!userInfo.BankCategory) {
            status = false
            str += '银行名称、'
        }
        if (!userInfo.ZFBPP) {
            status = false
            str += '支付宝截图照片、'
        }
        if (!userInfo.TBPP) {
            status = false
            str += '淘宝截图照片、'
        }
        if (userInfo.EnabledMark == 0) {
            if (status) {
                Alert('平台正在审核您的账号信息，请等待...')
            } else {
                Alert('账号信息审核中，请完善以下信息提交审核', str, [
                    { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                    { text: '去完善', onPress: () => history.push('/basic') },
                ])
            }
            return
        }
    }
    if (status) {
        
        if (userInfo.CreditMoney >= -20) {
            // let isNewUser = stringToDate3(userInfo.CreateDate)
            // console.log(CreateUserName === '正品王紫发')
            // if (isNewUser) {
            //     if(CreateUserName === '江小黑') {
            //         console.log('DDD')
            //         startOrder(userInfo, false, dispatch)
            //     }else {
            //         console.log('ddd')
            //         startOrder2(userInfo, false)
            //     }
            // }
            if(CreateUserName === '正品王紫发' || CreateUserName === '小明') {
                startOrder(userInfo, false, dispatch)
            }else {
                startOrder2(userInfo, false)
            }
        } else {
            Alert("提示", <div>
                <p>您的账户已欠费，请转款并截图转款记录给客服为您充值，(点击二维码可查看对应大图链接)</p>
                <div style={{ width: "100%", display: 'flex', justifyContent: "space-between", alignItems: 'center' }}>
                    <a href="/imgs/wx_pay.jpg" style={{ width: "48%", display: 'block' }}><img src='/imgs/wx_pay.jpg' style={{ width: "100%" }}></img></a>
                    <a href="/imgs/zfb_pay.jpg" style={{ width: "48%", display: 'block' }}><img src='/imgs/zfb_pay.jpg' style={{ width: "100%" }}></img></a>
                </div>
            </div>)
        }

    } else {
        Toast.fail('您的账户信息未完善，请先完善后再做单', 2, null, false)
        setTimeout(() => {
            history.push('/basic');
        }, 2000)
    }
}

