export const LinkToPlace = (account, isnew, userInfo, int = false) => {
    // if (!int) { TaskInterval(userInfo.UserId) }
    TaskInterval(userInfo.UserId)
    const now = new Date()
    const hour = now.getHours()
    if (hour >= 21) {
        Alert('请注意', '晚上【九点】之后接到的任务请务必完成，多次未完成平台将作封号处理', [
            { text: '取消', onPress: () => { }, style: 'default' },
            {
                text: '继续做单', onPress: () => {
                    LinkToPlace2(account, isnew)
                }, style: 'default'
            }
        ])
    } else {
        LinkToPlace2(account, isnew)
    }
}

