const fullQurest = (account, isnew) => {
    if(window.isQuest) {
        Toast.info('已停止自动排队')
        return
    };
    Toast.loading(`加载中... ${IPfilter(2)}`, 0, null, true);
    Axios('getOrder', '/GetOrder', {
        token: JSON.parse(localStorage.getItem('user_token')),
        Aliww: account
    }).then(data => {
        Toast.hide()
        if(window.count >= 100) {
            clearInterval(window.timerRequest)
        }
        if(data.message.indexOf("获取成功")>-1) {
            clearInterval(window.timerRequest)
            window.location.href = '/#/placeorder/' + data.resultdata
        }else {
            if(data.message == '正在排队'){
                setTimeout(()=>{
                    fullQurest(account, isnew)
                }, 3000)
            }else {
                Alert(data.message)
            }
        }
    }).catch(err => {
        
    })
}