import { combineReducers } from 'redux-immutable'
import { reducer as mainReducer } from '../pages/main/store'
import { reducer as homeReducer } from '../pages/main/page/home/store'
import { reducer as orderReducer } from '../pages/main/page/order/store'
import { reducer as myReducer } from '../pages/main/page/my/store'
import { reducer as userInfor} from '../pages/main/page/my/pages/userInfor/store'
import { reducer as MobileReducer} from '../pages/mobile-revise/store'
import { reducer as TaskInfoReducer} from '../pages/task-info/store'
import { reducer as GiftListReducer} from '../pages/gift-list/store'
import { reducer as BankCardReducer} from '../pages/bank-card//store'
import { reducer as ZFBCardReducer} from '../pages/zfb-card/store'
import { reducer as TBCardReducer} from '../pages/taobao-card/store'

export default combineReducers({
    main: mainReducer,
    home: homeReducer,
    order: orderReducer,
    my: myReducer,
    userInfor: userInfor,
    mobile: MobileReducer,
    taskInfo: TaskInfoReducer,
    giftList: GiftListReducer,
    bankCard: BankCardReducer,
    ZFB: ZFBCardReducer,
    TB: TBCardReducer
})