import styled from 'styled-components'
import { ToRem } from '../../common/Js/remToPx'
import {MV} from '../../common/Css'

export const Container = styled.div`
    min-height: calc(100vh - ${ToRem(44)} );
    background: #fff;
    margin-top: ${ToRem(44)};
`

export const LoginWarpper = styled.div`
    position: relative;
    flex: 1;
    padding: ${ToRem(45)} ${ToRem(25)} 0;
`

export const LoginBox = styled.div`
    background-color: #fff;
    border-radius: ${ToRem(5)};
    overflow: hidden;
    padding: ${ToRem(5)} 0;
`

export const InputBox = styled.div`
    position: relative;
    margin-bottom: ${ToRem(16)};
    width: 100%;
`

export const NavInput = styled.div`
    width: 100%;
    font-size: 0.45333rem;
    border-radius: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    i {
        font-size: ${ToRem(20)};
        font-weight: 700;
        width: ${ToRem(25)};
        text-align: center;
        color: ${MV.dva_color};
    }
    input {
        line-height: ${ToRem(45)};
        font-size: ${ToRem(17)};
        border: none;
        outline: none;
        width: calc(100% - ${ToRem(30)} );
        margin-left: ${ToRem(5)};
        border-radius: 0;
        color: #2b2b2b;
        background-color: transparent;
        &.code {
            width: calc(100% - ${ToRem(125)} );
        }
        &::placeholder {
            color: #dcddde;
        }
        &.active {
            color: #EC503F;
        }
    }
    .inputTpye {
        position: absolute;
        top: ${ToRem(15)};
        right: 0.26667rem;
        font-size: ${ToRem(16)};
        font-weight: 700;
        z-index: 1;
        color: ${MV.fz_2};
        .iconfont {
            color: #909399;
            font-size: ${ToRem(25)};
        }
        &.avtiive {
            color: #EC503F;
        }
        &.active2 {
            color: ${MV.fz_3};
        }
    }
`

export const NavNote = styled.div`
    position: absolute;
    top: 0.4rem;
    left: ${ToRem(20)};
    width: 100%;
    z-index: 0;
    height: 0.42667rem;
    line-height: 0.42667rem;
    font-size: 0.42667rem;
    pointer-events: none;
    color: #dcddde;
    &.none {
        display: none;
    }
`

export const StartUse = styled.div`
    margin-top: 0.74667rem;
    width: 100%;
    height: 1.12rem;
    line-height: 1.12rem;
    text-align: center;
    border-radius: 0.56rem;
    background-color: #d7d7d7;
    font-size: 0.4rem;
    color: #ffffff !important;
    &.active {
        background-color: #dda535;
    }
`

export const ForgetBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: ${ToRem(18)};
    font-size: ${ToRem(14)};
    line-height: ${ToRem(14)};
    .forget {
        text-decoration: none;
        color: #a4a4a4;
    }
    .to-rigister {
        color: #409eff; 
    }
`

export const MsgWarp = styled.div`
    color: ##dda535;
    height: ${ToRem(19)};
    line-height: ${ToRem(19)};
    font-size: ${ToRem(12)};
    font-weight: bold;
    text-align: center;
`